import React from 'react'
import type { ElementRef } from 'react'

import { StyledThumb, StyledThumbWrapper } from './thumb.styled'
import type { StyledThumbProps } from './thumb.styled'

export const Thumb = React.forwardRef<
  ElementRef<typeof StyledThumb>,
  StyledThumbProps
>((props, forwardRef) => (
  <StyledThumbWrapper>
    <StyledThumb ref={forwardRef} {...props} />
  </StyledThumbWrapper>
))
