import type { ElementRef } from 'react'
import React from 'react'
import { Button } from '@mirohq/design-system-button'
import type { BaseButtonProps } from '@mirohq/design-system-base-button'

import { useCalendarContext } from '../use-calendar-context'

export type ClearButtonProps = BaseButtonProps & {
  /**
   * Event handler called when the calendar value is cleared
   */
  onClear?: () => void
}

export const ClearButton = React.forwardRef<
  ElementRef<typeof Button>,
  ClearButtonProps
>(({ onClear, children, ...restProps }, forwardRef) => {
  const { value, setValue } = useCalendarContext()

  const onClearHandler = (): void => {
    setValue(undefined)
    if (onClear !== undefined) {
      onClear()
    }
  }

  return (
    <Button
      {...restProps}
      ref={forwardRef}
      variant='ghost'
      size='medium'
      onClick={onClearHandler}
      disabled={value === null}
    >
      {children}
    </Button>
  )
}) as React.ForwardRefExoticComponent<ClearButtonProps>
