import React, { useEffect } from 'react'
import * as RadixDropdownMenu from '@radix-ui/react-dropdown-menu'
import {
  BaseTooltipProvider,
  useBaseTooltipContext,
} from '@mirohq/design-system-base-tooltip'
import { usePrevious } from '@mirohq/design-system-use-previous'

import { CheckboxItem } from './partials/checkbox-item'
import { Content } from './partials/content'
import { Item } from './partials/item'
import { LinkItem } from './partials/link-item'
import { RadioGroup } from './partials/radio-group'
import { RadioItem } from './partials/radio-item'
import { Separator } from './partials/separator'
import { SwitchItem } from './partials/switch-item'
import { Trigger } from './partials/trigger'
import { SubTrigger } from './partials/sub-trigger'
import { SubContent } from './partials/sub-content'
import { Sub } from './partials/sub'
import { Portal } from './partials/portal'
import { ItemDescription } from './partials/item-description'
import { IconSlot } from './partials/icon-slot'
import { Hotkey } from './partials/hotkey'
import { IllustrationSlot } from './partials/illustration-slot'
import { DropdownProvider } from './hooks/use-dropdown-context'
import type { Direction } from './types'

export interface DropdownMenuProps {
  /**
   * The open state of the dropdown menu when it is initially rendered. Use when
   * you do not need to control its open state.
   * @default false
   */
  defaultOpen?: boolean

  /**
   * The reading direction of submenus when applicable. If omitted, assumes LTR
   * (left-to-right) reading mode.
   * @default 'ltr'
   */
  direction?: Direction

  /**
   * The current dropdown open state.
   */
  open?: boolean

  /**
   * Event handler called when the dropdown opens.
   */
  onOpen?: () => void

  /**
   * Event handler called when the dropdown closes.
   */
  onClose?: () => void

  /**
   * Defines whether the interaction with outside elements will be disabled and
   * only menu content will be visible to screen readers. This prop is ignored
   * within submenus.
   * @default false
   */
  interactOutside?: boolean

  /**
   * The content
   */
  children: React.ReactNode
}

const Root = ({
  defaultOpen = false,
  direction,
  interactOutside = false,
  open,
  onOpen,
  onClose,
  ...restProps
}: DropdownMenuProps): React.ReactNode => {
  const { ignoreNextTooltip } = useBaseTooltipContext()
  const prevOpen = usePrevious(open)

  useEffect(() => {
    if (prevOpen === true && open === false) {
      ignoreNextTooltip()
    }
  }, [ignoreNextTooltip, open, prevOpen])

  return (
    <RadixDropdownMenu.Root
      {...restProps}
      dir={direction}
      modal={interactOutside}
      open={open}
      defaultOpen={defaultOpen}
      onOpenChange={newOpen => {
        if (!newOpen && open === undefined) {
          ignoreNextTooltip()
        }
        newOpen ? onOpen?.() : onClose?.()
      }}
    />
  )
}

export const DropdownMenu: ((props: DropdownMenuProps) => React.ReactNode) &
  Partials = ({ direction = 'ltr', ...restProps }) => (
  <DropdownProvider direction={direction}>
    <BaseTooltipProvider>
      <Root direction={direction} {...restProps} />
    </BaseTooltipProvider>
  </DropdownProvider>
)

// Partials
// -----------------------------------------------------------------------------

export interface Partials {
  CheckboxItem: typeof CheckboxItem
  Content: typeof Content
  Hotkey: typeof Hotkey
  IconSlot: typeof IconSlot
  IllustrationSlot: typeof IllustrationSlot
  Item: typeof Item
  ItemDescription: typeof ItemDescription
  LinkItem: typeof LinkItem
  Portal: typeof Portal
  RadioGroup: typeof RadioGroup
  RadioItem: typeof RadioItem
  Separator: typeof Separator
  Sub: typeof Sub
  SubContent: typeof SubContent
  SubTrigger: typeof SubTrigger
  SwitchItem: typeof SwitchItem
  Trigger: typeof Trigger
}

DropdownMenu.CheckboxItem = CheckboxItem
DropdownMenu.Content = Content
DropdownMenu.Hotkey = Hotkey
DropdownMenu.IconSlot = IconSlot
DropdownMenu.IllustrationSlot = IllustrationSlot
DropdownMenu.Item = Item
DropdownMenu.ItemDescription = ItemDescription
DropdownMenu.LinkItem = LinkItem
DropdownMenu.Portal = Portal
DropdownMenu.RadioGroup = RadioGroup
DropdownMenu.RadioItem = RadioItem
DropdownMenu.Separator = Separator
DropdownMenu.Sub = Sub
DropdownMenu.SubContent = SubContent
DropdownMenu.SubTrigger = SubTrigger
DropdownMenu.SwitchItem = SwitchItem
DropdownMenu.Trigger = Trigger
