import { useCallback, useSyncExternalStore } from 'react'
import { useListeners } from '@mirohq/design-system-use-listeners'

export const useMediaQuery = (
  query: string,
  callback?: (matches: boolean) => void
): boolean => {
  const { addListener, removeListener } = useListeners()
  const subscribe = useCallback(
    (fn: () => void) => {
      const matchMedia = window.matchMedia(query)

      addListener(matchMedia, 'change', event => {
        const matches = (event as MediaQueryListEvent).matches
        fn()
        callback?.(matches)
      })
      return () => {
        removeListener(matchMedia, 'change', fn)
      }
    },
    [addListener, query, removeListener, callback]
  )

  const getSnapshot = (): boolean => window.matchMedia(query).matches

  const getServerSnapshot = (): boolean => false

  return useSyncExternalStore(subscribe, getSnapshot, getServerSnapshot)
}
