import { createContext, useContext } from 'react'

import type { StyledCalloutProps } from '../callout.styled'

interface CalloutProps {
  variant?: StyledCalloutProps['variant']
}

interface CalloutContextProps extends CalloutProps {}

export interface CalloutProviderProps extends CalloutContextProps {
  children: React.ReactNode
}

const CalloutContext = createContext<CalloutContextProps>({} as any)

export const CalloutProvider = ({
  children,
  ...restProps
}: CalloutProviderProps): JSX.Element => (
  <CalloutContext.Provider
    value={{
      ...restProps,
    }}
  >
    {children}
  </CalloutContext.Provider>
)

export const useCalloutContext = (): CalloutContextProps =>
  useContext(CalloutContext)
