import { Primitive } from '@mirohq/design-system-primitive'
import { styled } from '@mirohq/design-system-stitches'
import type { StrictComponentProps } from '@mirohq/design-system-stitches'
import * as RadixDropdownMenu from '@radix-ui/react-dropdown-menu'

import { itemDefaults } from '../styles/item'

export const StyledIndicator = styled(Primitive.span, {
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
})

const checkedSelector = `&[aria-checked=true] ${StyledIndicator}`
const uncheckedSelector = `&[aria-checked=false] ${StyledIndicator}`
const indeterminateSelector = `&[data-state="indeterminate"] ${StyledIndicator}`
const hoverCheckedSelector = `&[aria-checked=true]:is(:hover,[data-hovered]):not([aria-disabled="true"],[data-disabled]) ${StyledIndicator}`
const hoverUncheckedSelector = `&[aria-checked=false]:is(:hover,[data-hovered]):not([aria-disabled="true"],[data-disabled]) ${StyledIndicator}`
const disabledSelector = `&[aria-disabled="true"] ${StyledIndicator}, &[data-disabled] ${StyledIndicator}`

export const StyledCheckboxItem = styled(RadixDropdownMenu.CheckboxItem, {
  ...itemDefaults,
  [`${StyledIndicator}`]: {
    borderRadius: '$50',
    boxSizing: 'border-box',
  },
  variants: {
    variant: {
      'solid-prominent': {
        [`${StyledIndicator}`]: {
          square: '$4',
        },
        [`${checkedSelector}, ${indeterminateSelector}`]: {
          backgroundColor: '$background-primary-prominent',
          color: '$icon-primary-inverted',
        },
        [hoverCheckedSelector]: {
          backgroundColor: '$background-primary-prominent-hover',
        },
        [uncheckedSelector]: {
          border: '1px solid $border-neutrals-controls',
        },
        [hoverUncheckedSelector]: {
          border: '1px solid $border-primary-hover',
        },
        [disabledSelector]: {
          color: '$icon-neutrals-disabled',
          backgroundColor: '$background-neutrals-disabled',
        },
      },
      ghost: {
        [`${StyledIndicator}`]: {
          square: '$3',
        },
        [checkedSelector]: {
          color: '$icon-primary',
        },
        [indeterminateSelector]: {
          color: '$icon-primary',
        },
        [hoverCheckedSelector]: {
          color: '$icon-primary-hover',
        },
        [disabledSelector]: {
          color: '$icon-neutrals-disabled',
        },
      },
    },
  },
})

export type StyledCheckboxItemProps = StrictComponentProps<
  typeof StyledCheckboxItem
>
