import React, { useState, useRef } from 'react'
import type { ElementRef } from 'react'
import {
  IconEyeOpen,
  IconEyeClosed,
  IconLockClosed,
} from '@mirohq/design-system-icons'
import { mergeRefs } from '@mirohq/design-system-utils'
import type { BaseInputSharedProps } from '@mirohq/design-system-base-input'
import { BaseInput } from '@mirohq/design-system-base-input'

import type { Value } from './types'
import { useBaseInputProps } from './hooks/use-base-input-props'

export interface InputPasswordProps extends Omit<BaseInputSharedProps, 'type'> {
  /**
   * A boolean for the default password state (visible or masked).
   */
  defaultReveal?: boolean

  /**
   * Indicates whether the password should be revealed (visible) or hidden (masked).
   */
  reveal?: boolean

  /**
   * Callback function to be called when the password is revealed.
   */
  onReveal?: (value: Value) => void

  /**
   * Callback function to be called when the password is hidden (masked).
   */
  onHide?: (value: Value) => void

  /**
   * Tooltip label text for the action to reveal the password.
   */
  revealLabel: string

  /**
   * Tooltip label text for the action to hide (mask) the password.
   */
  hideLabel: string
}

export const InputPassword = React.forwardRef<
  ElementRef<'input'>,
  InputPasswordProps
>(
  (
    {
      reveal,
      defaultReveal,
      onReveal,
      onHide,
      hideLabel,
      revealLabel,
      children,
      ...restProps
    },
    forwardRef
  ) => {
    const inputRef = useRef<HTMLInputElement>(null)
    const [revealState, setRevealState] = useState(defaultReveal ?? false)
    const revealInternal = reveal ?? revealState

    const { baseInputProps, inputProps } = useBaseInputProps(
      restProps,
      forwardRef
    )

    return (
      <BaseInput {...baseInputProps}>
        <BaseInput.Input
          {...inputProps}
          type={revealInternal ? 'text' : 'password'}
          ref={mergeRefs([inputRef, forwardRef])}
        />
        {children}
        <BaseInput.IconSlot>
          <IconLockClosed />
        </BaseInput.IconSlot>
        <BaseInput.ActionButton
          label={revealInternal ? hideLabel : revealLabel}
          disabled={false}
          onPress={() => {
            const valueInternal = inputProps.value ?? inputRef.current?.value

            if (revealInternal) {
              onHide?.(valueInternal)
            } else {
              onReveal?.(valueInternal)
            }

            if (reveal === undefined) {
              setRevealState(reveal => !reveal)
            }
          }}
        >
          {revealInternal ? <IconEyeOpen /> : <IconEyeClosed />}
        </BaseInput.ActionButton>
      </BaseInput>
    )
  }
)
