import React, { useState } from 'react'
import { useLayoutEffect } from '@mirohq/design-system-use-layout-effect'

let count = 0

// `toString( )` is needed to prevent bundlers from importing `useId`
const getReactId: () => string | undefined =
  (React as any)['useId'.toString()] ?? (() => undefined)

/**
 * Generates a unique id for an element.
 * @param id - The id to use, if provided.
 * @returns The id to use
 * @example
 * const id = useId() // 'mds-1'
 * const id = useId('custom-id') // 'custom-id '
 */
export const useId = (id?: string): string => {
  const [internalId, setId] = useState<string | undefined>(id ?? getReactId())
  // layout effect is needed to prevent SSR mismatch
  useLayoutEffect(() => {
    if (id === '' || id === undefined) {
      setId(reactId => reactId ?? String(count++))
    }
  }, [id])

  return id ?? (internalId !== undefined ? `mds-${internalId}` : '')
}
