import React, { useEffect, useState } from 'react'
import type { ElementRef } from 'react'
import { useAriaDisabled } from '@mirohq/design-system-use-aria-disabled'

import { useSliderContext } from '../hooks/use-slider-context'
import type { StyledInputProps } from './input.styled'
import { StyledInput } from './input.styled'

export interface ValueProps extends StyledInputProps {}

export const Input = React.forwardRef<
  ElementRef<typeof StyledInput>,
  ValueProps
>((props, forwardRef) => {
  const {
    value,
    onValueChange,
    onValueCommit,
    min,
    max,
    step,
    disabled,
    ariaDisabled,
  } = useSliderContext()
  const [inputValue, setInputValue] = useState(value.toString())

  useEffect(() => {
    setInputValue(value.toString())
  }, [value])

  const elementProps = useAriaDisabled({
    ...props,
    ariaDisabled,
    onKeyUp: e => {
      if (e.key === 'Enter') {
        let valueToSet = Number(inputValue)
        if (max !== undefined && valueToSet > max) {
          valueToSet = max
        } else if (min !== undefined && valueToSet < min) {
          valueToSet = min
        } else if (step !== undefined && valueToSet % step !== 0) {
          valueToSet = Math.round(valueToSet / step) * step
        }

        setInputValue(valueToSet.toString())
        onValueChange(valueToSet)
        onValueCommit?.(valueToSet)
      }
    },
    onChange: (e: React.ChangeEvent<HTMLInputElement>) => {
      setInputValue(e.target.value)
    },
  })

  return (
    <StyledInput
      {...elementProps}
      type='number'
      ref={forwardRef}
      value={inputValue}
      disabled={disabled}
    />
  )
})
