import React from 'react'
import type { ElementRef } from 'react'

import { useFormFieldContext } from './hooks/use-form-field-context'
import { StyledFloatingLabel } from './floating-label.styled'
import type { StyledFloatingLabelProps } from './floating-label.styled'

export interface FloatingLabelProps
  extends Omit<StyledFloatingLabelProps, 'visuallyHidden'> {}

export const FloatingLabel = React.forwardRef<
  ElementRef<typeof StyledFloatingLabel>,
  FloatingLabelProps
>((props, forwardRef) => {
  const { isFloatingLabel, isHiddenLabel, formElementId } =
    useFormFieldContext()

  return (
    <StyledFloatingLabel
      htmlFor={formElementId}
      floating={isFloatingLabel}
      visuallyHidden={isHiddenLabel}
      {...props}
      ref={forwardRef}
    />
  )
})
