import { IconCross } from '@mirohq/design-system-icons'
import { booleanify } from '@mirohq/design-system-utils'
import type { PressEvent } from '@mirohq/design-system-use-press'
import { BaseInput, useInputContext } from '@mirohq/design-system-base-input'

import type { ClearProps } from './types'

export type ClearActionProps = ClearProps

const createSyntheticChangeEvent = (
  inputRef: React.RefObject<HTMLInputElement>
): React.ChangeEvent<HTMLInputElement> => {
  const event: React.ChangeEvent<HTMLInputElement> = {
    target: inputRef.current,
    currentTarget: inputRef.current,
    nativeEvent: new Event('input', {
      bubbles: true,
    }),
    preventDefault: () => {},
    stopPropagation: () => {},
    isDefaultPrevented: () => false,
    isPropagationStopped: () => false,
    persist: () => {},
  } as unknown as React.ChangeEvent<HTMLInputElement>

  return event
}

export const ClearAction = ({
  clearable,
  clearLabel,
  onClearPress,
  value,
}: ClearActionProps): JSX.Element => {
  const {
    disabled,
    'aria-disabled': ariaDisabled,
    readOnly,
    inputRef,
    onChange,
  } = useInputContext()

  if (!clearable) return <></>

  return (
    <BaseInput.ActionButton
      label={clearLabel}
      disabled={booleanify(disabled ?? ariaDisabled ?? readOnly)}
      onPress={(e: PressEvent) => {
        onClearPress?.(e)

        if (value === undefined && inputRef.current !== null) {
          if (inputRef.current.value === '') return
          inputRef.current.value = ''

          const event = createSyntheticChangeEvent(inputRef)
          onChange?.(event)
        }
      }}
    >
      <IconCross />
    </BaseInput.ActionButton>
  )
}
