import React from 'react'
import type { ReactNode, ElementRef } from 'react'
import { styled } from '@mirohq/design-system-stitches'
import { Box } from '@mirohq/design-system-box'
import { Button } from '@mirohq/design-system-button'
import type { ButtonProps } from '@mirohq/design-system-button'
import type { BoxProps } from '@mirohq/design-system-box'
import { Heading } from '@mirohq/design-system-typography'

// Types
// -----------------------------------------------------------------------------
export interface PatternEmptyStateProps extends Omit<BoxProps, 'children'> {
  heading?: ReactNode
  description?: ReactNode
  illustration?: string
  actions?: {
    primary: ActionButton & {
      variant?: 'primary' | 'secondary'
    }
    secondary?: ActionButton
  }
}

type BaseActionButtonProps = Omit<ButtonProps, 'children'> & {
  label: ReactNode
}

type ActionButton = Omit<BaseActionButtonProps, 'variant'>

// Styled
// -----------------------------------------------------------------------------
const Container = styled(Box, {
  background: 'transparent',
  padding: '$500',
  display: 'flex',
  flexDirection: 'column',
  minWidth: '304px',
  maxWidth: '480px',
  textAlign: 'center',
  alignItems: 'center',
})

const Illustration = styled('img', {
  width: '72px',
  minHeight: '72px',
  display: 'block',
  marginBottom: '$150',
})

const Description = styled(Box, {
  color: '$text-neutrals-subtle',
  marginTop: '$100',
  maxWidth: '304px',
})

const Actions = styled(Box, {
  marginTop: '$150',
  paddingY: '$200',
  display: 'flex',
  flexDirection: 'column',
  gap: '$100',
  alignItems: 'center',
})

const BaseActionButton = React.forwardRef<
  ElementRef<typeof Button>,
  BaseActionButtonProps
>(({ label, ...actionProps }, ref) => (
  <Button ref={ref} {...actionProps}>
    {label}
  </Button>
))

// Component
// -----------------------------------------------------------------------------
export const PatternEmptyState = React.forwardRef<
  ElementRef<typeof Box>,
  PatternEmptyStateProps
>(({ heading, description, illustration, actions, ...restProps }, ref) => (
  <Container ref={ref} {...restProps}>
    {illustration != null && (
      <Illustration src={illustration} aria-hidden alt='' />
    )}
    {heading != null && (
      <Heading ref={ref} level={2} styledAs='h3'>
        {heading}
      </Heading>
    )}
    {description != null && (
      <Description css={{ color: '$text-neutrals-subtle' }}>
        {description}
      </Description>
    )}
    {actions != null && (
      <Actions>
        <BaseActionButton variant='primary' {...actions.primary} />

        {actions.secondary != null && (
          <BaseActionButton {...actions.secondary} variant='ghost' />
        )}
      </Actions>
    )}
  </Container>
))
