import { createContext, useContext } from 'react'

import type { Direction } from '../types'

interface DropdownProps {
  direction?: Direction
}

interface DropdownContextProps extends DropdownProps {}

export interface DropdownProviderProps extends DropdownProps {
  children: React.ReactNode
}

const DropdownContext = createContext<DropdownContextProps>({} as any)

export const DropdownProvider = ({
  children,
  ...restProps
}: DropdownProviderProps): JSX.Element => (
  <DropdownContext.Provider
    value={{
      ...restProps,
    }}
  >
    {children}
  </DropdownContext.Provider>
)

export const useDropdownContext = (): DropdownContextProps =>
  useContext(DropdownContext)
