import type { ElementRef } from 'react'
import React, { useMemo } from 'react'
import type { DateRange, DateValue } from '@react-types/datepicker'
import type { DateFormatter } from 'react-aria'
import { useDateFormatter } from 'react-aria'
import { getLocalTimeZone } from '@internationalized/date'

import type { StyledValueProps } from './value.styled'
import { StyledValue } from './value.styled'
import { useCalendarContext } from '../use-calendar-context'

type FormatOptions = Pick<
  Intl.DateTimeFormatOptions,
  'weekday' | 'era' | 'year' | 'month' | 'day' | 'formatMatcher'
>

export type ValueProps = StyledValueProps & {
  format?: FormatOptions
}

const DEFAULT_FORMAT: FormatOptions = {
  day: '2-digit',
  month: 'short',
  year: 'numeric',
}

function formatDate(
  date: DateValue,
  formatter: DateFormatter,
  timeZone: string
): string | undefined {
  return formatter.format(date.toDate(timeZone))
}

function formatValue(
  value: DateValue | DateRange | undefined | null,
  formatter: DateFormatter,
  timeZone: string
): string | undefined {
  if (value == null || value === undefined) {
    return undefined
  }

  if ('start' in value) {
    if (value.start == null || value.end == null) {
      return undefined
    }

    return `${formatDate(value.start, formatter, timeZone)} - ${formatDate(
      value.end,
      formatter,
      timeZone
    )}`
  }

  return formatDate(value, formatter, timeZone)
}

export const Value = React.forwardRef<
  ElementRef<typeof StyledValue>,
  ValueProps
>(({ format = DEFAULT_FORMAT, ...restProps }, forwardRef) => {
  const { value } = useCalendarContext()

  const timeZone = useMemo(getLocalTimeZone, [format])

  const formatWithTimeZone = useMemo(
    () => ({ ...format, timeZone }),
    [format, timeZone]
  )
  const dateFormatter = useDateFormatter(formatWithTimeZone)

  const valueToDisplay = formatValue(value, dateFormatter, timeZone)

  return (
    <StyledValue ref={forwardRef} {...restProps}>
      {valueToDisplay}
    </StyledValue>
  )
})
