import React from 'react'
import { Button } from '@mirohq/design-system-button'
import type { DateRange, DateValue } from 'react-aria'

import { StyledPredefinedRanges } from './predefined-ranges.styled'
import { useCalendarContext } from '../use-calendar-context'

export const PredefinedRanges = (): React.ReactElement | null => {
  const { predefinedValues, onCalendarDateClick } = useCalendarContext<
    DateRange | DateValue
  >()

  if (predefinedValues === undefined) {
    return null
  }

  return (
    <StyledPredefinedRanges>
      {predefinedValues.map(item => (
        <Button
          variant='ghost'
          size='medium'
          key={item.id}
          type='button'
          onClick={() => onCalendarDateClick(item.value)}
        >
          {item.label}
        </Button>
      ))}
    </StyledPredefinedRanges>
  )
}
