import React from 'react'
import { ScrollArea } from '@mirohq/design-system-scroll-area'

import type { Direction, Overflow } from '../types'

export const RADIX_CONTENT_AVAILABLE_HEIGHT =
  'var(--radix-dropdown-menu-content-available-height)'

interface ScrollableContentProps {
  children?: React.ReactNode
  maxHeight?: string
  overflow: Overflow
  direction?: Direction
}

export const ScrollableContent = ({
  children,
  maxHeight,
  overflow,
  direction,
}: ScrollableContentProps): JSX.Element => {
  if (overflow === 'auto') {
    return (
      <ScrollArea dir={direction} type='always'>
        <ScrollArea.Viewport
          css={{
            maxHeight: maxHeight,
          }}
        >
          {children}
        </ScrollArea.Viewport>
        <ScrollArea.Scrollbar orientation='vertical'>
          <ScrollArea.Thumb />
        </ScrollArea.Scrollbar>
      </ScrollArea>
    )
  }

  return <>{children}</>
}
