import React from 'react'
import type { ElementRef } from 'react'

import { StyledRadioGroup } from './radio-group.styled'
import type { StyledRadioGroupProps } from './radio-group.styled'

export interface RadioGroupProps
  extends Omit<StyledRadioGroupProps, 'onChange'> {
  /**
   * The value of the selected item in the group
   */
  value: string

  /**
   * Event handler called when the selected radio changes
   */
  onChange: (value: string) => void
}

export const RadioGroup = React.forwardRef<
  ElementRef<typeof StyledRadioGroup>,
  RadioGroupProps
>((props, forwardRef) => {
  const { onChange, ...restProps } = props

  return (
    <StyledRadioGroup
      {...restProps}
      ref={forwardRef}
      onValueChange={onChange}
    />
  )
})
