import React from 'react'
import type { ElementRef } from 'react'
import { useLocalStorage } from '@mirohq/design-system-use-local-storage'

import type { StyledParagraphProps } from './paragraph.styled'
import { StyledParagraph } from './paragraph.styled'

export interface ParagraphProps extends Omit<StyledParagraphProps, 'debug'> {
  /**
   * The content.
   */
  children: React.ReactNode

  /**
   * Change the paragraph font-size.
   */
  size?: StyledParagraphProps['size']

  /**
   * Change the paragraph font-weight.
   */
  weight?: StyledParagraphProps['weight']
}

export const Paragraph = React.forwardRef<ElementRef<'p'>, ParagraphProps>(
  (props, forwardRef) => {
    const [debug] = useLocalStorage('DEBUG_TEXT', false)

    return <StyledParagraph ref={forwardRef} debug={debug} {...props} />
  }
)
