import type { ElementRef, ForwardRefExoticComponent } from 'react'
import React from 'react'
import { I18nProvider } from 'react-aria'
import * as RadixPopover from '@radix-ui/react-popover'
import type { DateRange, DateValue } from 'react-aria'

import type { CalendarDatePickerProps, CalendarRangePickerProps } from './types'
import { Trigger } from './partials/trigger'
import { Content } from './partials/content'
import type { CalendarProviderProps } from './use-calendar-context'
import { CalendarProvider, useCalendarContext } from './use-calendar-context'
import { Value } from './partials/value'
import { ClearButton } from './partials/clear-button'
import { SaveButton } from './partials/save-button'
import { StyledCalendar } from './calendar.styled'

// If an unrecognised locale string is passed in, the component will error, this protects from that by defaulting to English
// https://github.com/adobe/react-spectrum/issues/7457
function getValidLocale(locale: string | undefined): string {
  if (locale === undefined) {
    return 'en'
  }

  try {
    Intl.DateTimeFormat.supportedLocalesOf(locale)
    return locale
  } catch {
    try {
      // the client locale is in the format of ja_JP, but Intl.DateTimeFormat expects ja-JP
      // https://github.com/formatjs/formatjs/discussions/2440
      const localeWithDashes = locale.replace(/_/g, '-')
      Intl.DateTimeFormat.supportedLocalesOf(localeWithDashes)
      return localeWithDashes
    } catch {
      return 'en'
    }
  }
}

export type CalendarProps = (
  | CalendarDatePickerProps
  | CalendarRangePickerProps
) & {
  /**
   * The locale for Calendar internationalization
   */
  locale?: string
}

interface CalendarRootProps {
  children: React.ReactNode
}

const Root = React.forwardRef<
  ElementRef<typeof StyledCalendar>,
  CalendarRootProps
>(({ children, ...restProps }, forwardRef) => {
  const { open, setOpen } = useCalendarContext()

  return (
    <RadixPopover.Root open={open} onOpenChange={setOpen}>
      <StyledCalendar ref={forwardRef} {...restProps}>
        {children}
      </StyledCalendar>
    </RadixPopover.Root>
  )
})

export const Calendar = React.forwardRef<
  ElementRef<typeof StyledCalendar>,
  CalendarProps
>(
  (
    {
      picker,
      defaultValue,
      value,
      onValueChange,
      defaultOpen,
      open,
      onClose,
      onOpen,
      locale,
      visibleMonths,
      minDate,
      maxDate,
      disabled,
      'aria-disabled': ariaDisabled,
      children,
      predefinedValues,
      ...props
    },
    forwardRef
  ) => {
    // eslint-disable-next-line @typescript-eslint/consistent-type-assertions
    const providerProps = {
      picker,
      defaultValue,
      value,
      onValueChange,
      visibleMonths,
      minDate,
      maxDate,
      defaultOpen,
      open,
      onClose,
      onOpen,
      disabled,
      ariaDisabled,
      predefinedValues,
    } as CalendarProviderProps<DateValue> | CalendarProviderProps<DateRange>

    const validatedLocale = getValidLocale(locale)

    return (
      <I18nProvider locale={validatedLocale}>
        <CalendarProvider {...(providerProps as any)}>
          <Root {...props} ref={forwardRef}>
            {children}
          </Root>
        </CalendarProvider>
      </I18nProvider>
    )
  }
) as ForwardRefExoticComponent<CalendarProps> & Partials

export interface Partials {
  Trigger: typeof Trigger
  Content: typeof Content
  Value: typeof Value
  ClearButton: typeof ClearButton
  SaveButton: typeof SaveButton
}

Calendar.Trigger = Trigger
Calendar.Content = Content
Calendar.Value = Value
Calendar.ClearButton = ClearButton
Calendar.SaveButton = SaveButton
