import type { InputHTMLAttributes, ForwardedRef } from 'react'
import { useEffect } from 'react'
import type {
  BaseInputProps,
  BaseInputInputProps,
  BaseInputSharedProps,
} from '@mirohq/design-system-base-input'
import { mergeRefs, stringAttrValue } from '@mirohq/design-system-utils'
import { useFormFieldContext } from '@mirohq/design-system-base-form'

import type { InputFloatingLabelProps } from '../floating-label'

const baseInputAllowedEvents = [
  'onMouseEnter',
  'onMouseLeave',
  'onMouseMove',
  'onMouseMoveCapture',
  'onMouseOut',
  'onMouseOutCapture',
  'onMouseOver',
  'onMouseOverCapture',
  'onPointerEnter',
  'onPointerLeave',
  'onPointerMove',
  'onPointerMoveCapture',
  'onPointerOut',
  'onPointerOutCapture',
  'onPointerOver',
  'onPointerOverCapture',
  'onTouchMove',
  'onTouchMoveCapture',
] as const

type BaseInputAllowedEventsKeys = typeof baseInputAllowedEvents[number]

type BaseInputEventProps = Pick<
  InputHTMLAttributes<HTMLInputElement>,
  BaseInputAllowedEventsKeys
>

interface AdditionalRootProps {
  // to pass down className generated by Stitches.styled when providing Input as an element
  className?: string
}

export type BaseInputRootProps = BaseInputProps &
  BaseInputEventProps &
  AdditionalRootProps

export const useBaseInputProps = (
  allProps: BaseInputSharedProps & AdditionalRootProps,
  forwardRef: ForwardedRef<HTMLInputElement>
): {
  baseInputProps: BaseInputRootProps
  inputProps: BaseInputInputProps
  floatingLabelProps: InputFloatingLabelProps
} => {
  const {
    // base-input props
    valid,
    'aria-disabled': ariaDisabled,
    readOnly,
    required,
    disabled,
    size,
    className,
    css,
    onChange,

    // input props
    id,
    'aria-describedby': ariaDescribedBy,
    'aria-invalid': ariaInvalid,

    // input & floatingLabel shared props
    placeholder,
    value,
    defaultValue,

    ...restProps
  } = allProps

  const {
    formElementId,
    formElementRef,
    ariaDescribedBy: formFieldContextDescribedBy,
    ariaInvalid: formFieldAriaInvalid,
    valid: formFieldValid,
    setRequired,
    setReadOnly,
    setDisabled,
    setAriaDisabled,
  } = useFormFieldContext<HTMLInputElement>()

  useEffect(() => {
    setRequired?.(required)
    setReadOnly?.(readOnly)
    setDisabled?.(disabled)
    setAriaDisabled?.(ariaDisabled)
  }, [
    disabled,
    ariaDisabled,
    readOnly,
    required,
    setRequired,
    setReadOnly,
    setDisabled,
    setAriaDisabled,
  ])

  const baseInputProps: BaseInputRootProps = {
    valid: formFieldValid ?? valid,
    'aria-disabled': ariaDisabled,
    readOnly,
    required,
    disabled,
    size,
    className,
    css,
    onChange,
  }

  const inputProps: BaseInputInputProps = {
    id: id ?? formElementId,
    'aria-describedby': stringAttrValue(
      ariaDescribedBy,
      formFieldContextDescribedBy
    ),
    'aria-invalid': ariaInvalid ?? formFieldAriaInvalid,
    placeholder,
    value,
    defaultValue,
    ref: mergeRefs<HTMLInputElement>([formElementRef, forwardRef]),
  }

  const floatingLabelProps = {
    placeholder,
    value,
    defaultValue,
    size,
  }

  Object.entries(restProps).forEach(([key, value]) => {
    if (baseInputAllowedEvents.includes(key as any)) {
      baseInputProps[key as BaseInputAllowedEventsKeys] = value
    } else {
      inputProps[key as keyof BaseInputInputProps] = value
    }
  })

  return { baseInputProps, inputProps, floatingLabelProps }
}
