import React from 'react'
import type { ElementRef } from 'react'
import { useLayoutEffect } from '@mirohq/design-system-use-layout-effect'
import type { IconSizes } from '@mirohq/design-system-base-icon'
import { isIconComponent } from '@mirohq/design-system-base-icon'
import { addPropsToChildren } from '@mirohq/design-system-utils'
import { Primitive } from '@mirohq/design-system-primitive'

import type { StyledIconSlotProps } from './icon-slot.styled'
import { StyledIconSlot } from './icon-slot.styled'
import { useItem } from '../hooks/use-item'

interface IconComponentProps {
  'data-icon-component': ''
  size: IconSizes
}

export type IconSlotProps = { children: React.ReactNode } & StyledIconSlotProps

export const IconSlot = React.forwardRef<
  ElementRef<typeof StyledIconSlot>,
  StyledIconSlotProps
>(({ children, ...restProps }, forwardRef) => {
  const { leftSlotMount, leftSlotDestroy } = useItem()

  const formattedChildren = addPropsToChildren<IconComponentProps>(
    children,
    isIconComponent,
    {
      'data-icon-component': '',
      size: 'small',
    }
  )

  useLayoutEffect(() => {
    leftSlotMount()
    return () => leftSlotDestroy()
  }, [leftSlotMount, leftSlotDestroy])

  return (
    <StyledIconSlot ref={forwardRef} {...restProps}>
      <Primitive.svg asChild aria-hidden>
        {formattedChildren}
      </Primitive.svg>
    </StyledIconSlot>
  )
})
