import React, { useState } from 'react'
import { booleanify } from '@mirohq/design-system-utils'
import { Indicator as RadixIndicator } from '@radix-ui/react-checkbox'
import type { ElementRef } from 'react'
import type { Booleanish } from '@mirohq/design-system-types'

import {
  StyledCheckboxContent,
  StyledCheckbox,
  StyledCheckMark,
  StyledDisabledMark,
  StyledIndeterminateMark,
} from './checkbox.styled'
import type { StyledCheckboxProps } from './checkbox.styled'

export interface CheckboxProps extends StyledCheckboxProps {
  /**
   * The checked state of the checkbox when it is initially rendered. Use when
   * you do not need to control its checked state.
   */
  defaultChecked?: boolean

  /**
   * The style of the checkbox.
   * @default 'solid-prominent'
   */
  variant?: StyledCheckboxProps['variant']

  /**
   * The size of the checkbox.
   * @default 'large'
   */
  size?: StyledCheckboxProps['size']

  /**
   * The controlled checked state of the checkbox. Must be used in conjunction
   * with onChecked and onUnchecked.
   */
  checked?: boolean | 'indeterminate'

  /**
   * When true, indicates that the user must check the checkbox before the
   * owning form can be submitted.
   */
  required?: boolean

  /**
   * The name of the checkbox. Submitted with its owning form as part of a
   * name/value pair.
   */
  name?: string

  /**
   * The value given as data when submitted with a name.
   */
  value?: string

  /**
   * When true, prevents the user from interacting with the checkbox.
   */
  disabled?: boolean

  /**
   * Event handler called when the checked state equals true.
   */
  onChecked?: () => void

  /**
   * Event handler called when the checked state equals false.
   */
  onUnchecked?: () => void

  /**
   * When true, prevents the user from interacting with the checkbox but focus
   * is still possible.
   */
  'aria-disabled'?: Booleanish
}

export const Checkbox = React.forwardRef<
  ElementRef<typeof StyledCheckbox>,
  CheckboxProps
>(
  (
    {
      'aria-disabled': ariaDisabled,
      checked,
      defaultChecked,
      disabled,
      onChecked,
      onUnchecked,
      onClick,
      onKeyDown,
      size = 'large',
      variant = 'solid-prominent',
      ...restProps
    },
    forwardRef
  ) => {
    const [checkedState, setCheckedState] = useState<CheckboxProps['checked']>(
      defaultChecked ?? false
    )
    const internalChecked = checked ?? checkedState
    const isAriaDisabled = booleanify(ariaDisabled ?? false)
    const disabledUnchecked =
      (disabled === true || isAriaDisabled) && internalChecked === false
    const checkedStateEvents = {
      true: onChecked,
      false: onUnchecked,
    }

    return (
      <StyledCheckbox
        {...restProps}
        aria-disabled={ariaDisabled}
        data-form-element='checkbox'
        data-indeterminate={checked === 'indeterminate' ? '' : undefined}
        checked={internalChecked}
        defaultChecked={defaultChecked}
        disabled={disabled}
        size={size}
        variant={variant}
        onClick={e => {
          if (isAriaDisabled) {
            e.preventDefault()
            return
          }
          onClick?.(e)
        }}
        onCheckedChange={(checked: boolean) => {
          setCheckedState(checked)

          checkedStateEvents[`${checked}`]?.()
        }}
        ref={forwardRef}
      >
        <StyledCheckboxContent>
          <RadixIndicator>
            {internalChecked === 'indeterminate' && (
              <StyledIndeterminateMark size={size} />
            )}
            {internalChecked === true && <StyledCheckMark size={size} />}
          </RadixIndicator>
          {disabledUnchecked && <StyledDisabledMark size={size} />}
        </StyledCheckboxContent>
      </StyledCheckbox>
    )
  }
)
