import React, {
  createContext,
  useCallback,
  useContext,
  useMemo,
  useState,
} from 'react'
import type { DateRange, DateValue } from 'react-aria'
import type { DateDuration } from '@internationalized/date'
import { useControllableState } from '@radix-ui/react-use-controllable-state'
import type { Booleanish } from '@mirohq/design-system-types'

import type { PredefinedValue } from './types'

interface SharedProps<T extends DateRange | DateValue> {
  picker: 'single' | 'range'
  minDate?: DateValue
  maxDate?: DateValue
  disabled?: boolean
  ariaDisabled?: Booleanish
  predefinedValues?: Array<PredefinedValue<T>>
}

interface CalendarContextProps<T extends DateRange | DateValue>
  extends SharedProps<T> {
  visibleDuration: DateDuration

  value?: T
  setValue: React.Dispatch<React.SetStateAction<T | undefined>>

  open: boolean
  setOpen: React.Dispatch<React.SetStateAction<boolean | undefined>>

  setImmediateSave: React.Dispatch<React.SetStateAction<boolean>>

  calendarValue?: T | null
  calendarDefaultValue?: T | null
  onCalendarDateClick: (date: T) => void
  onSaveClick: () => void
}

export interface CalendarProviderProps<T extends DateRange | DateValue>
  extends SharedProps<T> {
  children?: React.ReactNode
  visibleMonths?: number

  defaultValue?: T
  value?: T
  onValueChange?: (value: T | undefined) => void

  defaultOpen?: boolean
  open?: boolean
  onOpen?: () => void
  onClose?: () => void
}

const CalendarContext = createContext<CalendarContextProps<any>>({} as any)

export const CalendarProvider = <T extends DateRange | DateValue>({
  children,
  visibleMonths = 1,
  value: valueProp,
  defaultValue: defaultValueProp,
  onValueChange,
  open: openProp,
  defaultOpen: defaultOpenProp,
  onOpen,
  onClose,
  ...restProps
}: CalendarProviderProps<T>): JSX.Element => {
  const [immediateSave, setImmediateSave] = useState(false)
  const [internalCalendarValue, setInternalCalendarValue] = useState<
    T | undefined
  >(valueProp ?? defaultValueProp)

  const visibleDuration = useMemo(
    () => ({
      months: visibleMonths,
    }),
    [visibleMonths]
  )

  const [value, setValue] = useControllableState({
    prop: valueProp,
    defaultProp: defaultValueProp,
    onChange: onValueChange,
  })

  const [open = false, setOpen] = useControllableState({
    prop: openProp,
    defaultProp: defaultOpenProp,
    onChange: state => {
      if (state) {
        onOpen?.()
      } else {
        onClose?.()
      }
    },
  })

  const onCalendarDateClick = useCallback(
    (value: T): void => {
      if (immediateSave) {
        setInternalCalendarValue(value)
      } else {
        setValue(value)
        setOpen(false)
      }
    },
    [setValue, setOpen, immediateSave, setInternalCalendarValue]
  )

  const onSaveClick = (): void => {
    setValue(internalCalendarValue)
    setOpen(false)
  }

  return (
    <CalendarContext.Provider
      value={{
        ...restProps,
        visibleDuration,
        value: value,
        setValue,
        open,
        setOpen,
        onCalendarDateClick,
        setImmediateSave,
        onSaveClick,
        // value should not be undefined otherwise react-aria calendar will complain in console
        calendarValue: (immediateSave ? internalCalendarValue : value) ?? null,
      }}
    >
      {children}
    </CalendarContext.Provider>
  )
}

export const useCalendarContext = <
  T extends DateRange | DateValue
>(): CalendarContextProps<T> => useContext(CalendarContext)
