import { styled } from '@mirohq/design-system-stitches'
import { textFieldStyles } from '@mirohq/design-system-base-text-field'
import { focus } from '@mirohq/design-system-styles'
import type { ComponentPropsWithRef } from 'react'

const disabledAndReadonlySelectors =
  ':read-only, :disabled, [aria-disabled="true"]'

export const StyledInput = styled('input', {
  all: 'unset',
  display: 'block',
  maxWidth: '$11',
  height: '$8',
  padding: '0 $50',
  textAlign: 'center',

  ...textFieldStyles.variants.idle,
  ...focus.css(textFieldStyles.variants.focused),

  '&:read-only': textFieldStyles.variants.readOnly,

  '&:disabled, &[aria-disabled="true"]': {
    ...textFieldStyles.variants.disabled,
    ...textFieldStyles.base.disabled,
  },

  [`&:not(${disabledAndReadonlySelectors})`]: {
    _hover: textFieldStyles.variants.hovered,
  },

  [`&:not(${disabledAndReadonlySelectors})::placeholder`]:
    textFieldStyles.base.placeholder.color,

  '&::-webkit-outer-spin-button, &::-webkit-inner-spin-button': {
    '-webkit-appearance': 'none',
    margin: 0,
  },
  '&[type=number]': {
    '-moz-appearance': 'textfield',
  },
})
export type StyledInputProps = ComponentPropsWithRef<typeof StyledInput>
