import React from 'react'
import type { ElementRef } from 'react'
import { mergeRefs } from '@mirohq/design-system-utils'
import { useAriaDisabled } from '@mirohq/design-system-use-aria-disabled'

import { StyledInput } from './input.styled'
import type { StyledInputProps } from './input.styled'
import { useInputContext } from '../hooks/use-input-context'

export type InputProps = Omit<
  StyledInputProps,
  // these props should be set in the root
  'aria-disabled' | 'disabled' | 'readOnly' | 'required' | 'onChange'
>

export const Input = React.forwardRef<
  ElementRef<typeof StyledInput>,
  InputProps
>(
  (
    {
      id,
      'aria-describedby': ariaDescribedBy,
      'aria-invalid': ariaInvalid,
      ...restProps
    },
    forwardRef
  ) => {
    const {
      'aria-disabled': ariaDisabled,
      disabled,
      inputRef,
      required,
      readOnly,
      onChange,
    } = useInputContext()

    const elementProps = useAriaDisabled({
      ...restProps,
      ariaDisabled,
    })

    return (
      <StyledInput
        {...elementProps}
        id={id}
        readOnly={readOnly}
        aria-describedby={ariaDescribedBy}
        aria-invalid={ariaInvalid}
        disabled={disabled === true || undefined}
        required={required === true || undefined}
        onChange={onChange}
        ref={mergeRefs([inputRef, forwardRef])}
      />
    )
  }
)
