import React from 'react'
import type { ElementRef, ComponentPropsWithRef } from 'react'
import {
  IconCheckMark,
  IconExclamationPointCircle,
  IconInformationMarkCircle,
} from '@mirohq/design-system-icons'

import { useCalloutContext } from '../hooks/use-callout-context'
import { StyledIconSlot } from './icon-slot.styled'

export type StyledIconSlotProps = ComponentPropsWithRef<typeof StyledIconSlot>

export interface IconSlotProps extends StyledIconSlotProps {
  /**
   * The icon.
   */
  children?: React.ReactNode
}

const iconVariant = {
  success: <IconCheckMark />,
  warning: <IconExclamationPointCircle />,
  danger: <IconExclamationPointCircle />,
  information: <IconInformationMarkCircle />,
  default: <IconCheckMark />,
}

export const IconSlot = React.forwardRef<
  ElementRef<typeof StyledIconSlot>,
  IconSlotProps
>(({ children, ...restProps }, forwardRef) => {
  const { variant } = useCalloutContext()

  let formattedChildren = children
  if (React.Children.count(children) < 1) {
    formattedChildren =
      iconVariant[variant as keyof typeof iconVariant] ?? iconVariant.default
  }

  return (
    <StyledIconSlot {...restProps} ref={forwardRef} variant={variant}>
      {formattedChildren}
    </StyledIconSlot>
  )
})
