import { createContext, useContext } from 'react'

import type { StyledButtonProps } from '../button.styled'

interface ButtonProps {
  size?: StyledButtonProps['size']
}

interface ButtonContextProps extends ButtonProps {}

export interface ButtonProviderProps extends ButtonProps {
  children: React.ReactNode
}

const ButtonContext = createContext<ButtonContextProps>({} as any)

export const ButtonProvider = ({
  children,
  ...restProps
}: ButtonProviderProps): JSX.Element => (
  <ButtonContext.Provider
    value={{
      ...restProps,
    }}
  >
    {children}
  </ButtonContext.Provider>
)

export const useButtonContext = (): ButtonContextProps =>
  useContext(ButtonContext)
