import type { PointerEventHandler, DragEventHandler } from 'react'
import { isVirtualPointerEvent } from '@react-aria/utils'
import { isOverTarget } from '@mirohq/design-system-utils'

import type { FocusableElement, InteractionProps, PointerType } from './types'
import {
  disableTextSelection,
  restoreTextSelection,
} from './utils/text-selection'
import { handleFocus } from './utils/focus'

interface PointerEventHandlers {
  onPointerDown: PointerEventHandler<FocusableElement>
  onMouseDown: PointerEventHandler<FocusableElement>
  onPointerUp: PointerEventHandler<FocusableElement>
  onDragStart: DragEventHandler<FocusableElement>
}

export const getEvents = ({
  triggerPressStart,
  triggerPressUp,
  triggerPressEnd,
  state,
  addListener,
  removeAllListeners,
  createEvent,
  cancel,
  propsRef,
  document: customDocument,
}: InteractionProps): PointerEventHandlers => {
  const {
    disabled = false,
    preventFocusOnPress = false,
    allowTextSelectionOnPress = false,
    shouldCancelOnPointerExit = false,
  } = propsRef.current ?? {}

  // Safari on iOS < 13.2 does not implement pointerenter/pointerleave events correctly.
  // Use pointer move events instead to implement our own hit testing.
  // See https://bugs.webkit.org/show_bug.cgi?id=199803
  const onDocumentPointerMove = (e: PointerEvent): void => {
    if (e.pointerId !== state.activePointerId) {
      return
    }

    if (state.target === null) {
      return
    }

    if (isOverTarget(e, state.target)) {
      if (!state.isOverTarget) {
        state.isOverTarget = true
        triggerPressStart(
          createEvent(state.target, e),
          state.pointerType as PointerType
        )
      }
    } else if (state.isOverTarget) {
      state.isOverTarget = false
      triggerPressEnd(
        createEvent(state.target, e),
        state.pointerType as PointerType,
        false
      )
      if (shouldCancelOnPointerExit) {
        cancel(e, state)
      }
    }
  }

  const onDocumentPointerUp = (e: PointerEvent): void => {
    if (state.target === null) {
      return
    }

    if (
      e.pointerId === state.activePointerId &&
      state.isPressed &&
      e.button === 0
    ) {
      if (isOverTarget(e, state.target)) {
        triggerPressEnd(
          createEvent(state.target, e),
          state.pointerType as PointerType,
          true
        )
      } else if (state.isOverTarget) {
        triggerPressEnd(
          createEvent(state.target, e),
          state.pointerType as PointerType,
          false
        )
      }

      state.isPressed = false
      state.isOverTarget = false
      state.activePointerId = null
      state.pointerType = null
      removeAllListeners()
      if (!allowTextSelectionOnPress) {
        restoreTextSelection(state.target)
      }
    }
  }

  const onDocumentPointerCancel = (e: PointerEvent): void => {
    cancel(e, state)
  }

  const onPointerDown: PointerEventHandler<FocusableElement> = e => {
    // Executes the subsequent code if the primary mouse button was pressed
    // and the event was triggered within the current target element.
    if (e.button !== 0 || !e.currentTarget.contains(e.target as Element)) {
      return
    }

    // iOS safari fires pointer events from VoiceOver with incorrect coordinates/target.
    // Ignore and let the onClick handler take care of it instead.
    // https://bugs.webkit.org/show_bug.cgi?id=222627
    // https://bugs.webkit.org/show_bug.cgi?id=223202
    if (isVirtualPointerEvent(e.nativeEvent)) {
      state.pointerType = 'virtual'
      return
    }

    state.pointerType = e.pointerType

    if (!state.isPressed) {
      state.isPressed = true
      state.isOverTarget = true
      state.activePointerId = e.pointerId
      state.target = e.currentTarget

      // Due to browser inconsistencies, especially on mobile browsers, we prevent
      // default on pointer down and handle focusing the pressable element ourselves.
      handleFocus({
        event: e,
        disabled,
        preventFocusOnPress,
        shouldPreventDefault,
      })

      if (!allowTextSelectionOnPress) {
        disableTextSelection(state.target)
      }

      triggerPressStart(e, state.pointerType)

      addListener(customDocument, 'pointermove', onDocumentPointerMove, false)
      addListener(customDocument, 'pointerup', onDocumentPointerUp, false)
      addListener(
        customDocument,
        'pointercancel',
        onDocumentPointerCancel,
        false
      )
    }
  }

  const onMouseDown: PointerEventHandler<FocusableElement> = e => {
    if (!e.currentTarget.contains(e.target as Element)) {
      return
    }

    if (e.button === 0) {
      // Chrome and Firefox on touch Windows devices require mouse down events
      // to be canceled in addition to pointer events, or an extra asynchronous
      // focus event will be fired.
      if (
        shouldPreventDefault(e.currentTarget as Element) &&
        preventFocusOnPress !== 'auto'
      ) {
        e.preventDefault()
      }
    }
  }

  const onPointerUp: PointerEventHandler<FocusableElement> = e => {
    // iOS fires pointerup with zero width and height, so check the pointerType recorded during pointerdown.
    if (
      !e.currentTarget.contains(e.target as Element) ||
      state.pointerType === 'virtual'
    ) {
      return
    }

    // Only handle left clicks
    // Safari on iOS sometimes fires pointerup events, even
    // when the touch isn't over the target, so double check.
    if (e.button === 0 && isOverTarget(e, e.currentTarget)) {
      triggerPressUp(e, state.pointerType ?? e.pointerType)
    }
  }

  const onDragStart: DragEventHandler<FocusableElement> = e => {
    if (!e.currentTarget.contains(e.target as Element)) {
      return
    }

    // Safari does not call onPointerCancel when a drag starts, whereas Chrome and Firefox do.
    cancel(e, state)
  }

  return { onPointerDown, onMouseDown, onPointerUp, onDragStart }
}

const shouldPreventDefault = (target: Element): boolean =>
  // We cannot prevent default if the target is a draggable element.
  !(target instanceof HTMLElement) || !target.draggable
