import React, { useEffect } from 'react'
import type { ElementRef } from 'react'
import { Value as RadixValue } from '@radix-ui/react-select'
import type { StrictComponentProps } from '@mirohq/design-system-stitches'

import { useSelectContext } from '../hooks/use-select-context'
import { StyledValue } from './value.styled'

export type StyledValueProps = StrictComponentProps<typeof RadixValue>

export interface ValueProps extends StyledValueProps {
  /**
   * The content that will be rendered inside the Select.Value when no value or
   * defaultValue is set.
   */
  placeholder?: React.ReactNode

  /**
   * The controlled content that will be rendered inside the Select.Value.
   */
  children?: React.ReactNode
}

export const Value = React.forwardRef<
  ElementRef<typeof StyledValue>,
  ValueProps
>(({ placeholder, ...restProps }, forwardRef) => {
  const { setPlaceholder } = useSelectContext()

  useEffect(() => {
    setPlaceholder(placeholder)
  }, [setPlaceholder, placeholder])

  return (
    <StyledValue ref={forwardRef}>
      <RadixValue {...restProps} placeholder={placeholder} ref={forwardRef} />
    </StyledValue>
  )
})
