import type { BaseButtonProps } from '@mirohq/design-system-base-button'
import { Spinner } from '@mirohq/design-system-spinner'
import type { SpinnerProps } from '@mirohq/design-system-spinner'
import { booleanify } from '@mirohq/design-system-utils'
import React from 'react'
import type { ElementRef, ForwardRefExoticComponent } from 'react'

import type { StyledButtonProps } from './button.styled'
import {
  StyledButton,
  StyledHiddenContent,
  StyledSpinnerBox,
} from './button.styled'
import { ButtonProvider } from './hooks/use-button-context'
import { IconSlot } from './partials/icon-slot'
import { Label } from './partials/label'

export type ButtonProps = BaseButtonProps & {
  /**
   * Change the button style.
   * @default 'primary'
   */
  variant?: StyledButtonProps['variant']

  /**
   * Change the button size.
   * @default 'large'
   */
  size?: StyledButtonProps['size']

  /**
   * Make button border rounded.
   * @default false
   */
  rounded?: StyledButtonProps['rounded']

  /**
   * Add spinner and disable.
   * @default false
   */
  loading?: boolean

  /**
   * Make width 100%.
   * @default false
   */
  fluid?: StyledButtonProps['fluid']
}

export const Button = React.forwardRef<ElementRef<'button' | 'a'>, ButtonProps>(
  (
    {
      variant = 'primary',
      size = 'large',
      loading = false,
      rounded = false,
      fluid = false,
      'aria-disabled': ariaDisabled,
      asChild = false,
      children,
      ...restProps
    },
    forwardRef
  ) => {
    let spinnerSize: SpinnerProps['size'] = 'medium'

    if (typeof size === 'string' && ['small', 'medium'].includes(size)) {
      spinnerSize = 'small'
    }

    const shouldHaveAriaDisabled = booleanify(ariaDisabled) || loading

    let formattedChildren = children

    if (loading) {
      const spinnerTestId =
        process.env.NODE_ENV === 'test' ? 'button-spinner' : undefined

      if (
        asChild &&
        React.Children.toArray(children).length === 1 &&
        React.isValidElement(children)
      ) {
        // when using asChild we need to render element to merge props with first,
        // and spinner wrapper should be rendered between the element and its content
        const firstLevelChild = React.Children.only(children)

        const { children: secondLevelChildren, ...childProps } =
          firstLevelChild.props

        formattedChildren = React.cloneElement(firstLevelChild, {
          ...childProps,
          children: (
            <>
              <StyledSpinnerBox data-testid={spinnerTestId}>
                <Spinner size={spinnerSize} />
              </StyledSpinnerBox>
              <StyledHiddenContent>{secondLevelChildren}</StyledHiddenContent>
            </>
          ),
        })
      } else {
        formattedChildren = (
          <>
            <StyledSpinnerBox data-testid={spinnerTestId}>
              <Spinner size={spinnerSize} />
            </StyledSpinnerBox>
            <StyledHiddenContent>{children}</StyledHiddenContent>
          </>
        )
      }
    }

    return (
      <ButtonProvider size={size}>
        <StyledButton
          {...restProps}
          asChild={asChild}
          variant={variant}
          rounded={rounded}
          fluid={fluid}
          size={size}
          // without undefined it will be aria-disabled="false" in html
          aria-disabled={shouldHaveAriaDisabled ? true : undefined}
          ref={forwardRef}
        >
          {formattedChildren}
        </StyledButton>
      </ButtonProvider>
    )
  }
) as ForwardRefExoticComponent<ButtonProps> & Partials

// Partials
// -----------------------------------------------------------------------------

export interface Partials {
  IconSlot: typeof IconSlot
  Label: typeof Label
}

Button.IconSlot = IconSlot
Button.Label = Label
