import type { ComponentPropsWithRef } from 'react'
import { Primitive } from '@mirohq/design-system-primitive'
import { styled } from '@mirohq/design-system-stitches'
import { mapKeysToVariants, createConstants } from '@mirohq/design-system-utils'

import { SIZES as GRID_SIZES } from '../grid.styled'

const ADDITIONAL_COLUMNS_ROWS_SIZES = createConstants(-1)
const SIZES = { ...ADDITIONAL_COLUMNS_ROWS_SIZES, ...GRID_SIZES }

export const StyledItem = styled(Primitive.div, {
  variants: {
    columnStart: mapKeysToVariants(SIZES, key => ({ gridColumnStart: key })),
    rowStart: mapKeysToVariants(SIZES, key => ({ gridRowStart: key })),
    columnEnd: mapKeysToVariants(SIZES, key => ({ gridColumnEnd: key })),
    rowEnd: mapKeysToVariants(SIZES, key => ({ gridRowEnd: key })),
  },
})

export type StyledItemProps = ComponentPropsWithRef<typeof StyledItem>
