import React, { createContext, useContext, useState, useCallback } from 'react'
import { addPropsToChildren } from '@mirohq/design-system-utils'

interface ItemContext {
  leftSlotMount: () => void
  leftSlotDestroy: () => void
}

interface ItemProviderProps {
  children: React.ReactNode
}

const Context = createContext<ItemContext>({
  leftSlotMount: () => {},
  leftSlotDestroy: () => {},
})

export const ItemProvider = ({ children }: ItemProviderProps): JSX.Element => {
  const [hasSlot, setHasSlot] = useState(false)

  const leftSlotMount = useCallback(() => {
    setHasSlot(true)
  }, [])

  const leftSlotDestroy = useCallback(() => {
    setHasSlot(false)
  }, [])

  const formattedChildren = hasSlot
    ? children
    : addPropsToChildren(children, () => true, {
        'data-no-left-slot': '',
      })

  return (
    <Context.Provider
      value={{
        leftSlotMount,
        leftSlotDestroy,
      }}
    >
      {formattedChildren}
    </Context.Provider>
  )
}

export const useItem = (): ItemContext => useContext(Context)
