import React from 'react'
import type { ElementRef, ReactNode, ReactElement } from 'react'
import type { Numeric } from '@mirohq/design-system-types'

import {
  StyledBadgeContainer,
  StyledBadgeWrapper,
  StyledBadge,
  StyledDot,
} from './badge.styled'
import type { StyledBadgeProps } from './badge.styled'

export interface BadgeProps extends StyledBadgeProps {
  /**
   * Content of the badge.
   */
  content?: string

  /**
   * Handle whether the badge should be shown, if no content is provided a dot
   * is displayed.
   * @default true
   */
  show?: boolean

  /**
   * Element that shows the badge
   */
  children?: ReactNode

  /**
   * By default the badge is filled with blue, inverted set it to white.
   */
  inverted?: boolean

  /**
   * Move the badge position to the left/right.
   * @default 0
   */
  offsetX?: Numeric

  /**
   * Move the badge position to the top/bottom.
   * @default 0
   */
  offsetY?: Numeric
}

export const Badge = React.forwardRef<
  ElementRef<typeof StyledBadge>,
  BadgeProps
>(
  (
    { show = true, content, children, offsetX = 0, offsetY = 0, ...restProps },
    forwardRef
  ) => {
    if (!show) {
      return children as ReactElement
    }

    let offset = {}

    if (+offsetX !== 0 || +offsetY !== 0) {
      offset = {
        UNSAFE_style: {
          transform: `translate(${offsetX}px, ${offsetY}px)`,
        },
      }
    }

    const badgeProps = {
      ...restProps,
      ...offset,
    }

    return (
      <StyledBadgeContainer
        data-testid={process.env.NODE_ENV === 'test' ? 'badge' : undefined}
      >
        <StyledBadgeWrapper>
          {content !== undefined ? (
            <StyledBadge {...badgeProps} ref={forwardRef}>
              {content}
            </StyledBadge>
          ) : (
            <StyledDot {...badgeProps} ref={forwardRef} />
          )}
        </StyledBadgeWrapper>
        {children}
      </StyledBadgeContainer>
    )
  }
)
