import { BaseButton, sizes } from '@mirohq/design-system-base-button'
import { styles as baseIconStyles } from '@mirohq/design-system-base-icon'
import { Primitive } from '@mirohq/design-system-primitive'
import { styled } from '@mirohq/design-system-stitches'
import type { CSS } from '@mirohq/design-system-stitches'
import { focus } from '@mirohq/design-system-styles'
import type { ComponentPropsWithRef } from 'react'

import { StyledIconSlot } from './partials/icon-slot'

const disabledSelector = '&[disabled], &[aria-disabled="true"]'

// we might face className collision because of empty CSS in StyledIconSlot
// https://github.com/stitchesjs/stitches/issues/976
const iconSlotSelector = `& ${StyledIconSlot}`
const externalIconSelector =
  '& svg:not([data-icon-component]), & img:not([data-icon-component])'

const solidDisabled: CSS = {
  [disabledSelector]: {
    backgroundColor: '$background-neutrals-disabled',
    color: '$text-neutrals-disabled',

    [iconSlotSelector]: {
      color: '$icon-neutrals-disabled',
    },
  },
}

const outlineDisabled: CSS = {
  [disabledSelector]: {
    backgroundColor: '$background-neutrals',
    borderColor: '$border-neutrals-disabled',
    color: '$text-neutrals-disabled',

    [iconSlotSelector]: {
      color: '$icon-neutrals-disabled',
    },
  },
}

const ghostDisabled: CSS = {
  [disabledSelector]: {
    color: '$text-neutrals-disabled',
    backgroundColor: '$transparent',

    [iconSlotSelector]: {
      color: '$icon-neutrals-disabled',
    },
  },
}

const LABEL_OFFSET = 2

export const StyledButton = styled(BaseButton, {
  whiteSpace: 'nowrap',
  textOverflow: 'ellipsis',
  textAlign: 'center',
  fontWeight: '$semibold',
  position: 'relative',
  width: 'fit-content',
  maxWidth: '100%',

  lineHeight: 1,
  border: '1px solid transparent', // to make outline and solid/ghost variants the same width

  [`& ${StyledIconSlot}:first-child`]: {
    marginLeft: -LABEL_OFFSET,
    marginRight: `calc($50 + ${LABEL_OFFSET}px)`,
  },
  [`& ${StyledIconSlot}:last-child`]: {
    marginRight: -LABEL_OFFSET,
    marginLeft: `calc($50 + ${LABEL_OFFSET}px)`,
  },
  ...focus.css({
    boxShadow: '$focus',
  }),

  variants: {
    variant: {
      primary: {
        backgroundColor: '$background-primary-prominent',
        color: '$text-neutrals-inverted',
        '&[data-pressed]': {
          backgroundColor: '$background-primary-prominent-pressed',
        },

        [iconSlotSelector]: {
          color: '$icon-primary-inverted',
        },

        '&[data-hovered]': {
          backgroundColor: '$background-primary-prominent-hover',
        },

        ...solidDisabled,
      },
      tertiary: {
        color: '$text-primary',
        border: '1.5px solid $border-primary',

        [iconSlotSelector]: {
          color: '$icon-primary',
        },

        '&[data-hovered]': {
          backgroundColor: '$background-primary-subtle-hover',
          borderColor: '$border-primary-hover',
          color: '$text-primary-hover',

          [iconSlotSelector]: {
            color: '$icon-primary-hover',
          },
        },
        '&[data-pressed]': {
          backgroundColor: '$background-primary-subtle-active',
          borderColor: '$border-primary-active',
          color: '$text-primary-active',

          [iconSlotSelector]: {
            color: '$icon-primary-active',
          },
        },
        ...outlineDisabled,
      },
      'ghost-prominent': {
        backgroundColor: 'transparent',
        color: '$text-primary',

        '&[data-hovered]': {
          backgroundColor: '$background-primary-subtle-hover',
          color: '$text-primary-hover',

          [iconSlotSelector]: {
            color: '$icon-primary-hover',
          },
        },
        '&[data-pressed]': {
          backgroundColor: '$background-primary-subtle-active',
          color: '$text-primary-active',

          [iconSlotSelector]: {
            color: '$icon-primary-active',
          },
        },
        ...ghostDisabled,
      },
      secondary: {
        color: '$text-neutrals',
        backgroundColor: '$button-background-secondary',
        '&[data-hovered]': {
          color: '$text-neutrals-hover',
          backgroundColor: '$button-background-secondary-hover',
        },
        '&[data-pressed]': {
          color: '$text-neutrals-active',
          backgroundColor: '$button-background-secondary-pressed',
        },

        [iconSlotSelector]: {
          color: '$icon-neutrals',
        },

        ...solidDisabled,
      },
      'outline-subtle': {
        backgroundColor: '$background-neutrals',
        border: '1px solid $border-neutrals',
        color: '$text-neutrals',

        [iconSlotSelector]: {
          color: '$icon-neutrals',
        },

        '&[data-hovered]': {
          backgroundColor: '$background-neutrals-subtle-hover',
          borderColor: '$border-neutrals-hover',
        },
        '&[data-pressed]': {
          backgroundColor: '$background-neutrals-subtle-active',
          borderColor: '$border-neutrals-active',
        },
        ...outlineDisabled,
      },
      ghost: {
        color: '$text-neutrals',

        [iconSlotSelector]: {
          color: '$icon-neutrals',
        },

        '&[data-hovered]': {
          backgroundColor: '$background-neutrals-hover',
          color: '$text-neutrals-hover',
        },
        '&[data-pressed]': {
          backgroundColor: '$background-neutrals-pressed',
          color: '$text-neutrals-pressed',
        },

        ...ghostDisabled,
      },
      danger: {
        backgroundColor: '$background-danger-prominent',
        color: '$text-danger-inverted',

        [iconSlotSelector]: {
          color: '$icon-danger-inverted',
        },

        '&[data-hovered]': {
          backgroundColor: '$background-danger-prominent-hover',
        },
        '&[data-pressed]': {
          backgroundColor: '$background-danger-prominent-active',
        },
        ...solidDisabled,
      },
      'danger-secondary': {
        borderColor: '$border-danger',
        color: '$text-danger',

        [iconSlotSelector]: {
          color: '$icon-danger',
        },

        '&[data-hovered]': {
          borderColor: '$border-danger-hover',
          backgroundColor: '$background-danger-subtle-hover',
          [iconSlotSelector]: {
            color: '$icon-danger-hover',
          },
        },
        '&[data-pressed]': {
          borderColor: '$border-danger-active',
          backgroundColor: '$background-danger-subtle-active',
          [iconSlotSelector]: {
            color: '$icon-danger-active',
          },
        },
        ...outlineDisabled,
      },
      'ghost-danger': {
        backgroundColor: 'transparent',
        color: '$text-danger',

        [iconSlotSelector]: {
          color: '$icon-danger',
        },

        '&[data-hovered]': {
          backgroundColor: '$background-danger-subtle',
          color: '$text-danger-hover',

          [iconSlotSelector]: {
            color: '$icon-danger-hover',
          },
        },
        '&[data-pressed]': {
          backgroundColor: '$background-danger-subtle-hover',
          color: '$text-danger-active',

          [iconSlotSelector]: {
            color: '$icon-danger-active',
          },
        },
        ...ghostDisabled,
      },
    },
    size: {
      'x-large': {
        height: sizes.xLarge,
        fontSize: '$200',
        paddingX: `calc($200 + ${LABEL_OFFSET}px)`,

        [externalIconSelector]: {
          ...baseIconStyles.size.medium,
        },
      },
      large: {
        height: sizes.large,
        fontSize: '$200',
        paddingX: `calc($150 + ${LABEL_OFFSET}px)`,

        [externalIconSelector]: {
          ...baseIconStyles.size.medium,
        },
      },
      medium: {
        height: sizes.medium,
        fontSize: '$175',
        paddingX: `calc($100 + ${LABEL_OFFSET}px)`,

        [externalIconSelector]: {
          ...baseIconStyles.size.small,
        },
      },
      small: {
        fontSize: '$175',
        height: '$6',
        paddingX: `calc($100 + ${LABEL_OFFSET}px)`,

        [externalIconSelector]: {
          ...baseIconStyles.size.small,
        },
      },
    },
    rounded: {
      true: {
        borderRadius: '$round',
      },
    },
    fluid: {
      true: {
        display: 'flex',
        justifyContent: 'center',
        maxWidth: '100%',
        width: '100%',
      },
    },
  },
})

export const StyledHiddenContent = styled(Primitive.span, {
  visibility: 'hidden',
})

export const StyledSpinnerBox = styled(Primitive.div, {
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  position: 'absolute',
  top: 0,
  left: 0,
  bottom: 0,
  right: 0,
  margin: 'auto',
})

export type StyledButtonProps = ComponentPropsWithRef<typeof StyledButton>
