import React from 'react'
import type { ElementRef } from 'react'

import { StyledBox } from './box.styled'
import type { StyledBoxProps } from './box.styled'

export interface BoxProps extends StyledBoxProps {
  /**
   * Sets width to 100%.
   * @default false
   */
  fluid?: StyledBoxProps['fluid']
}

export const Box = React.forwardRef<ElementRef<typeof StyledBox>, BoxProps>(
  ({ fluid = false, ...restProps }, forwardRef) => (
    <StyledBox fluid={fluid} {...restProps} ref={forwardRef} />
  )
)
