import React, { useRef } from 'react'
import { useCalendarCell, useFocusRing, mergeProps } from 'react-aria'
import type { CalendarDate } from '@internationalized/date'
import {
  isSameDay,
  isSameMonth,
  isToday,
  getLocalTimeZone,
} from '@internationalized/date'
import type { CalendarState, RangeCalendarState } from 'react-stately'

import type { StyledCellProps } from './cell.styled'
import { StyledCell, StyledCellContent } from './cell.styled'

export interface CellProps extends StyledCellProps {
  state: RangeCalendarState | CalendarState
  date: CalendarDate
  currentMonth: CalendarDate
}

export const Cell = (props: CellProps): React.ReactNode => {
  let { state, date, currentMonth } = props
  const ref = useRef(null)
  const { cellProps, buttonProps, isSelected, formattedDate } = useCalendarCell(
    { date },
    state,
    ref
  )

  const isOutsideMonth = !isSameMonth(currentMonth, date)

  state = state as RangeCalendarState

  // The start and end date of the selected range will have
  // an emphasized appearance.
  const isSelectionStart =
    state.highlightedRange !== undefined && state.highlightedRange !== null
      ? isSameDay(date, state.highlightedRange.start)
      : isSelected
  const isSelectionEnd =
    state.highlightedRange !== undefined && state.highlightedRange !== null
      ? isSameDay(date, state.highlightedRange.end)
      : isSelected

  const { focusProps } = useFocusRing()

  // when it's both start and end, it's single date
  const isRange = isSelected && (!isSelectionStart || !isSelectionEnd)

  const rangeVariant = isRange
    ? isSelectionStart
      ? 'start'
      : isSelectionEnd
      ? 'end'
      : 'middle'
    : undefined

  return (
    <StyledCell {...cellProps}>
      <StyledCellContent
        {...mergeProps(buttonProps, focusProps)}
        ref={ref}
        selected={isSelected && (isSelectionStart || isSelectionEnd)}
        today={isToday(date, getLocalTimeZone())}
        range={rangeVariant}
        hidden={isOutsideMonth}
      >
        {formattedDate}
      </StyledCellContent>
    </StyledCell>
  )
}
