import React from 'react'
import type { ElementRef } from 'react'

import { StyledThumb } from './thumb.styled'
import type { StyledThumbProps } from './thumb.styled'

export interface ThumbProps extends StyledThumbProps {
  /**
   * Used to force mounting when more control is needed. Useful when
   * controlling animation with React animation libraries.
   */
  forceMount?: true
}

export const Thumb = React.forwardRef<
  ElementRef<typeof StyledThumb>,
  ThumbProps
>((props, forwardRef) => <StyledThumb {...props} ref={forwardRef} />)
