import React, { useRef, useMemo } from 'react'
import type { ElementRef, ForwardRefExoticComponent } from 'react'
import {
  booleanify,
  booleanishAttrValue,
  mergeRefs,
} from '@mirohq/design-system-utils'
import { usePress } from '@mirohq/design-system-use-press'
import { useHover } from '@react-aria/interactions'
import { mergeProps } from '@react-aria/utils'

import type { StyledBaseInputProps } from './base-input.styled'
import { StyledBaseInput } from './base-input.styled'
import { Input } from './partials/input'
import type { InputProviderProps } from './hooks/use-input-context'
import { InputProvider, useInputContext } from './hooks/use-input-context'
import { ActionButton } from './partials/action-button'
import { IconSlot } from './partials/icon-slot'

type BaseInputRootProps = Omit<
  StyledBaseInputProps,
  // these props come from useInputContext, we don't pass it to BaseInput directly
  | 'hovered'
  | 'focused'
  | 'valid'
  | 'readOnly'
  | 'disabled'
  | 'ariaDisabled'
  // these shouldn't be applied to div
  | 'value'
  | 'defaultValue'
  | 'placeholder'
>

const Root = React.forwardRef<
  ElementRef<typeof StyledBaseInput>,
  BaseInputRootProps
>(({ children, size, ...restProps }, forwardRef) => {
  const ref = useRef<HTMLDivElement>(null)
  const isPointerDownRef = useRef(false)

  const {
    valid,
    'aria-disabled': ariaDisabled,
    disabled,
    readOnly,
    focused,
    setHovered,
    inputRef,
    actionButtonRef,
    setFocused,
    setActive,
    active,
    variant,
  } = useInputContext()
  const { hoverProps, isHovered: hovered } = useHover({
    onHoverChange: setHovered,
  })

  const { pressProps } = usePress({
    onPressStart: e => {
      const { target } = e.originalEvent as PointerEvent

      const shouldPrevent =
        document.activeElement === inputRef.current &&
        target !== inputRef.current // to not stop text selection

      // to not lose focus on container click
      if (shouldPrevent) {
        e.originalEvent.preventDefault()
      }
    },
    onPress: e => {
      // For pointer events, only focus on completed press (not drag-away)
      if (e.pointerType === 'keyboard') return

      const { target } = e.originalEvent as PointerEvent

      const isActionButton =
        target === actionButtonRef.current ||
        (actionButtonRef.current?.contains(target as Node) ?? false)

      const shouldFocusInput =
        !isActionButton && !booleanify(disabled) && !booleanify(focused)

      if (shouldFocusInput) {
        inputRef.current?.focus()
      }
    },
    onPressEnd: e => {
      // For keyboard events, restore focus when child elements handle presses
      // (e.g., chip removal in combobox)
      if (e.pointerType !== 'keyboard') return

      const shouldFocusInput = !booleanify(disabled) && !booleanify(focused)

      if (shouldFocusInput) {
        inputRef.current?.focus()
      }
    },
    preventFocusOnPress: 'auto',
  })

  const onPointerDownHandler = React.useCallback(() => {
    // Track that pointer was used to interact with the input
    isPointerDownRef.current = true
  }, [])

  const onFocusHandler = React.useCallback(
    (e: React.FocusEvent<HTMLDivElement>) => {
      if (!isPointerDownRef.current && e.target === inputRef.current) {
        setFocused(true)
      }

      setActive(true)
    },
    [setActive, setFocused, inputRef]
  )

  const onBlurHandler = React.useCallback(() => {
    isPointerDownRef.current = false
    setFocused(false)
    setActive(false)
  }, [setActive, setFocused])

  const elementProps = useMemo(
    () =>
      mergeProps(
        restProps,
        {
          onFocus: onFocusHandler,
          onBlur: onBlurHandler,
          onPointerDown: onPointerDownHandler,
        },
        hoverProps,
        pressProps
      ),
    [
      restProps,
      hoverProps,
      pressProps,
      onFocusHandler,
      onBlurHandler,
      onPointerDownHandler,
    ]
  )

  return (
    <StyledBaseInput
      {...elementProps}
      data-invalid={booleanishAttrValue(valid === false)}
      data-valid={booleanishAttrValue(valid === true)}
      data-form-element='input'
      ref={mergeRefs([ref, forwardRef])}
      size={size}
      variant={variant}
      active={active}
      hovered={hovered}
      focused={focused}
      valid={valid}
      disabled={booleanify(disabled)}
      ariaDisabled={booleanify(ariaDisabled)}
      readOnly={booleanify(readOnly)}
      data-testid={
        process.env.NODE_ENV === 'test' ? 'input-container' : undefined
      }
    >
      {children}
    </StyledBaseInput>
  )
})

export type BaseInputProps = BaseInputRootProps & InputProviderProps

export const BaseInput = React.forwardRef<
  ElementRef<typeof StyledBaseInput>,
  BaseInputProps
>(
  (
    {
      'aria-disabled': ariaDisabled,
      disabled,
      valid,
      readOnly,
      required,
      onChange,
      variant,
      ...restProps
    },
    forwardRef
  ) => (
    <InputProvider
      valid={valid}
      disabled={disabled}
      aria-disabled={ariaDisabled}
      readOnly={readOnly}
      required={required}
      onChange={onChange}
      variant={variant}
    >
      <Root {...restProps} ref={forwardRef} />
    </InputProvider>
  )
) as ForwardRefExoticComponent<BaseInputProps> & Partials

// Partials
// -----------------------------------------------------------------------------
export interface Partials {
  Input: typeof Input
  ActionButton: typeof ActionButton
  IconSlot: typeof IconSlot
}

BaseInput.Input = Input
BaseInput.ActionButton = ActionButton
BaseInput.IconSlot = IconSlot
