import React from 'react'
import type { ElementRef } from 'react'

import { StyledGroup } from './group.styled'
import type { StyledGroupProps } from './group.styled'

export interface GroupProps extends StyledGroupProps {}

export const Group = React.forwardRef<
  ElementRef<typeof StyledGroup>,
  GroupProps
>((props, forwardRef) => <StyledGroup {...props} ref={forwardRef} />)
