import React, { useEffect } from 'react'
import type { ElementRef, ForwardRefExoticComponent } from 'react'
import { ComboboxProvider as AriakitComboboxProvider } from '@ariakit/react'
import type { FormElementProps } from '@mirohq/design-system-base-form'
import { useFormFieldContext } from '@mirohq/design-system-base-form'
import { booleanify } from '@mirohq/design-system-utils'
import * as RadixPopover from '@radix-ui/react-popover'
import {
  BaseTooltipProvider,
  useBaseTooltipContext,
} from '@mirohq/design-system-base-tooltip'
import { usePrevious } from '@mirohq/design-system-use-previous'

import { Trigger } from './partials/trigger'
import { Content } from './partials/content'
import { Input } from './partials/input'
import { Item } from './partials/item'
import { Portal } from './partials/portal'
import { Group } from './partials/group'
import { GroupLabel } from './partials/group-label'
import { Value } from './partials/value'
import { Separator } from './partials/separator'
import { StyledComboboxContent, StyledNativeSelect } from './combobox.styled'
import type { StyledComboboxProps } from './combobox.styled'
import {
  ComboboxProvider,
  useComboboxContext,
} from './hooks/use-combobox-context'
import type { Direction } from './types'
import { NoResult } from './partials/no-result'
import { SearchInput } from './partials/search-input'

export interface ComboboxProps extends FormElementProps, StyledComboboxProps {
  /**
   * The value of the combobox when initially rendered. Use when you do not need
   * to control the state of the combobox.
   */
  defaultValue?: string[] | string

  /**
   * The controlled value of the combobox. Should be used in conjunction with
   * onValueChange.
   */
  value?: string[] | string

  /**
   * Event handler called when the value changes.
   */
  onValueChange?: (value: string[] | string) => void

  /**
   * The controlled search value to filter items. Should be used in conjunction with
   * onSearchValueChange.
   */
  searchValue?: string

  /**
   * Event handler called when the trigger input value changes.
   */
  onSearchValueChange?: (value: string) => void

  /**
   * The open state of the combobox when it is initially rendered. Use when you do
   * not need to control its open state.
   * @default false
   */
  defaultOpen?: boolean

  /**
   * The controlled open state of the combobox. Must be used in conjunction with
   * onOpen and onClose.
   */
  open?: boolean

  /**
   * Event handler called when the combobox opens.
   */
  onOpen?: () => void

  /**
   * Event handler called when the combobox closes.
   */
  onClose?: () => void

  /**
   * The reading direction of the combobox when applicable. If omitted, inherits
   * globally from DirectionProvider or assumes LTR (left-to-right) reading
   * mode.
   * @default 'ltr'
   */
  direction?: Direction

  /**
   * The content of the combobox
   */
  children?: React.ReactNode

  /**
   * Enables/disabled automatic filtering.
   * @default true
   */
  autoFilter?: boolean

  /**
   * The name of the combobox. Submitted with its owning form as part of a
   * name/value pair.
   */
  name?: string

  /**
   * Allow single or multiple selection.
   * @default true
   */
  multiple?: boolean
}

const Root = React.forwardRef<
  ElementRef<typeof StyledComboboxContent>,
  ComboboxProps
>(
  (
    {
      multiple = true,
      value: valueProp,
      onValueChange,
      name,
      children,
      ...restProps
    },
    forwardRef
  ) => {
    const {
      openState,
      setOpenState,
      value = [],
      setValue,
      required,
      readOnly,
      'aria-disabled': ariaDisabled,
      disabled,
      direction,
      triggerRef,
    } = useComboboxContext()

    const {
      setRequired,
      setDisabled,
      setAriaDisabled,
      setReadOnly,
      formElementRef,
    } = useFormFieldContext<HTMLSelectElement>()

    useEffect(() => {
      setRequired?.(required)
      setDisabled?.(disabled)
      setAriaDisabled?.(ariaDisabled)
      setReadOnly?.(readOnly)
    }, [
      readOnly,
      disabled,
      ariaDisabled,
      required,
      setRequired,
      setDisabled,
      setAriaDisabled,
      setReadOnly,
    ])

    const { ignoreNextTooltip } = useBaseTooltipContext()
    const prevOpen = usePrevious(openState)

    useEffect(() => {
      if (prevOpen === true && !openState) {
        ignoreNextTooltip()
      }
    }, [ignoreNextTooltip, openState, prevOpen])

    const onSetSelectedValue = (newValue: string | string[]): void => {
      // If no defaultValue is provided the newValue is always an array so we need validate the types
      if (!multiple && Array.isArray(newValue)) {
        setValue(newValue[0])
      } else if (multiple && !Array.isArray(newValue)) {
        setValue([newValue])
      } else {
        setValue(newValue)
      }
    }

    const onOpenChange = (value: boolean): void => {
      if (!booleanify(readOnly)) {
        setOpenState(value)
      }
    }

    const isFormControl = Boolean(triggerRef.current?.closest('form'))

    const getOptionValue = (
      itemValue: string | string[]
    ): JSX.Element | JSX.Element[] => {
      if (multiple && Array.isArray(itemValue)) {
        return (value as string[]).map(itemValue => (
          <option key={itemValue} value={itemValue}>
            {itemValue}
          </option>
        ))
      } else {
        return (
          <option key={itemValue as string} value={itemValue}>
            {itemValue}
          </option>
        )
      }
    }

    return (
      <RadixPopover.Root open={openState} onOpenChange={onOpenChange}>
        <AriakitComboboxProvider
          open={openState}
          setOpen={onOpenChange}
          selectedValue={value}
          setSelectedValue={onSetSelectedValue}
        >
          <StyledComboboxContent
            ref={forwardRef}
            {...restProps}
            dir={direction}
            data-form-element='select'
          >
            {children}
          </StyledComboboxContent>
        </AriakitComboboxProvider>
        {isFormControl && (
          <StyledNativeSelect
            multiple={multiple}
            autoComplete='off'
            name={name}
            tabIndex={-1}
            aria-hidden='true'
            ref={formElementRef}
            required={required}
            disabled={disabled}
            aria-disabled={ariaDisabled}
            value={value}
            // since we don't support autoComplete there is no way this select value will change,
            // although we still need to provide `onChange` since we use `value` and not `defaultValue`
            onChange={() => {}}
          >
            {value.length === 0 ? (
              <option value='' />
            ) : (
              // since we don't support autoComplete we can render here only selected values
              getOptionValue(value)
            )}
          </StyledNativeSelect>
        )}
      </RadixPopover.Root>
    )
  }
)

export const Combobox = React.forwardRef<
  ElementRef<typeof StyledComboboxContent>,
  ComboboxProps
>(
  (
    {
      'aria-disabled': ariaDisabled,
      defaultOpen = false,
      open,
      valid,
      disabled,
      readOnly,
      required,
      value,
      defaultValue,
      onOpen,
      onClose,
      searchValue,
      onSearchValueChange,
      onValueChange,
      direction = 'ltr',
      autoFilter = true,
      ...restProps
    },
    forwardRef
  ) => (
    <ComboboxProvider
      defaultValue={defaultValue}
      value={value}
      onValueChange={onValueChange}
      searchValue={searchValue}
      onSearchValueChange={onSearchValueChange}
      defaultOpen={defaultOpen}
      open={open}
      onOpen={onOpen}
      onClose={onClose}
      valid={valid}
      required={required}
      disabled={disabled}
      readOnly={readOnly}
      aria-disabled={ariaDisabled}
      direction={direction}
      autoFilter={autoFilter}
    >
      <BaseTooltipProvider>
        <Root {...restProps} value={value} ref={forwardRef} />
      </BaseTooltipProvider>
    </ComboboxProvider>
  )
) as ForwardRefExoticComponent<ComboboxProps> & Partials

export interface Partials {
  Portal: typeof Portal
  Trigger: typeof Trigger
  Input: typeof Input
  Content: typeof Content
  Item: typeof Item
  Group: typeof Group
  GroupLabel: typeof GroupLabel
  Value: typeof Value
  Separator: typeof Separator
  NoResult: typeof NoResult
  SearchInput: typeof SearchInput
}

Combobox.Portal = Portal
Combobox.Trigger = Trigger
Combobox.Input = Input
Combobox.Content = Content
Combobox.Item = Item
Combobox.Group = Group
Combobox.GroupLabel = GroupLabel
Combobox.Value = Value
Combobox.Separator = Separator
Combobox.NoResult = NoResult
Combobox.SearchInput = SearchInput
