import { BaseButton, sizes } from '@mirohq/design-system-base-button'
import { styles as baseIconStyles } from '@mirohq/design-system-base-icon'
import { styled } from '@mirohq/design-system-stitches'
import { focus } from '@mirohq/design-system-styles'
import type { ComponentPropsWithRef } from 'react'

const activeSelector = '&:active, &[data-pressed]'
const disabledSelector = '&[disabled], &[aria-disabled="true"]'
const iconSelector = '& svg, & img'

export const StyledIconButton = styled(BaseButton, {
  justifyContent: 'center',
  ...focus.css({
    boxShadow: '$focus',
  }),
  variants: {
    variant: {
      primary: {
        backgroundColor: '$background-primary-prominent',
        color: '$icon-primary-inverted',

        _hover: {
          backgroundColor: '$background-primary-prominent-hover',
        },
        [activeSelector]: {
          backgroundColor: '$background-primary-prominent-active',
        },
        [disabledSelector]: {
          backgroundColor: '$background-neutrals-disabled',
          color: '$icon-neutrals-disabled',
        },
      },
      secondary: {
        backgroundColor: '$background-neutrals-subtle',
        color: '$icon-neutrals',

        _hover: {
          backgroundColor: '$background-neutrals-subtle-hover',
        },
        [disabledSelector]: {
          backgroundColor: '$background-neutrals-disabled',
          color: '$icon-neutrals-disabled',
        },
        [activeSelector]: {
          backgroundColor: '$background-neutrals-subtle-pressed',
        },
      },
      outline: {
        backgroundColor: '$background-neutrals',
        color: '$icon-neutrals',
        border: '1px solid $border-neutrals',

        _hover: {
          backgroundColor: '$background-neutrals-subtle-hover',
          borderColor: '$border-neutrals-hover',
        },
        [activeSelector]: {
          backgroundColor: '$background-neutrals-subtle-active',
          borderColor: '$border-neutrals-active',
        },
        [disabledSelector]: {
          backgroundColor: '$background-neutrals',
          color: '$icon-neutrals-disabled',
          borderColor: '$border-neutrals-disabled',
        },
      },
      ghost: {
        color: '$icon-neutrals',
        backgroundColor: 'transparent',

        _hover: {
          backgroundColor: '$background-neutrals-hover',
        },
        [disabledSelector]: {
          color: '$icon-neutrals-disabled',
          backgroundColor: '$transparent',
        },
        [activeSelector]: {
          backgroundColor: '$background-neutrals-pressed',
        },
      },
      danger: {
        color: '$icon-neutrals',
        backgroundColor: '$transparent',

        _hover: {
          color: '$icon-danger-hover',
          backgroundColor: '$background-danger-subtle-hover',
        },
        [activeSelector]: {
          color: '$icon-danger-pressed',
          backgroundColor: '$background-danger-subtle-pressed',
        },
        [disabledSelector]: {
          color: '$icon-neutrals-disabled',
          backgroundColor: '$background-neutrals-disabled',
        },
      },
    },
    size: {
      medium: {
        height: sizes.medium,
        width: sizes.medium,
        [iconSelector]: {
          ...baseIconStyles.size.small,
        },
      },
      large: {
        height: sizes.large,
        width: sizes.large,
        [iconSelector]: {
          ...baseIconStyles.size.medium,
        },
      },
      'x-large': {
        height: sizes.xLarge,
        width: sizes.xLarge,
        [iconSelector]: {
          ...baseIconStyles.size.medium,
        },
      },
    },
  },
})

export type StyledIconButtonProps = ComponentPropsWithRef<
  typeof StyledIconButton
>
