import React from 'react'
import type { ElementRef } from 'react'
import { styled } from '@mirohq/design-system-stitches'
import { Heading } from '@mirohq/design-system-typography'
import type { HeadingProps } from '@mirohq/design-system-typography'

const StyledTitle = styled(Heading, { lineHeight: '$300' })

export interface TitleProps extends Omit<HeadingProps, 'level' | 'styledAs'> {
  level?: HeadingProps['level']
}

export const Title = React.forwardRef<ElementRef<typeof Heading>, TitleProps>(
  ({ level = 4, ...props }, forwardRef) => (
    <StyledTitle {...props} ref={forwardRef} level={level} styledAs='h4' />
  )
)

Title.displayName = 'Callout.Title'
