import React from 'react'
import type { ElementRef } from 'react'
import {
  IconCheckMark,
  IconMinus,
  IconProhibit,
} from '@mirohq/design-system-icons'
import { booleanify } from '@mirohq/design-system-utils'

import { StyledCheckboxItem, StyledIndicator } from './checkbox-item.styled'
import type { StyledCheckboxItemProps } from './checkbox-item.styled'
import { RightSlot } from './right-slot'
import { useAriaDisabled } from '../hooks/use-aria-disabled'
import { ItemProvider } from '../hooks/use-item'

export interface CheckboxItemProps
  extends Omit<StyledCheckboxItemProps, 'onChange'> {
  /**
   * The checked state of the item.
   */
  checked: boolean | 'indeterminate'

  /**
   * Event handler called when the checked state changes.
   */
  onChange: (checked: boolean) => void

  /**
   * When true, prevents the user from interacting with the item.
   */
  disabled?: boolean

  /**
   * Event handler called when the user selects an item (via mouse of keyboard).
   */
  onSelect?: (event: Event) => void

  /**
   * Closes the dropdown when the user selects an item.
   * @default false
   */
  closeOnSelect?: boolean

  /**
   * Optional text used for typeahead purposes. By default the typeahead
   * behavior will use the .textContent of the item.
   * Use this when the content is complex, or you have non-textual content
   * inside.
   */
  textValue?: string

  /**
   * The style of the checkbox in the menu item.
   * @default 'ghost'
   */
  variant?: StyledCheckboxItemProps['variant']
}

export const CheckboxItem = React.forwardRef<
  ElementRef<typeof StyledCheckboxItem>,
  CheckboxItemProps
>(
  (
    {
      children,
      checked,
      onChange,
      disabled,
      closeOnSelect = false,
      variant = 'ghost',
      ...restProps
    },
    forwardRef
  ) => {
    const elementProps = useAriaDisabled(restProps, { closeOnSelect })
    const { 'aria-disabled': ariaDisabled } = restProps
    const iconCss = { square: '100%', display: 'block' }
    const isAriaDisabled = booleanify(ariaDisabled ?? false)
    const disabledUnchecked =
      (disabled === true || isAriaDisabled) && checked === false

    return (
      <ItemProvider>
        <StyledCheckboxItem
          {...elementProps}
          ref={forwardRef}
          checked={checked}
          disabled={disabled}
          onCheckedChange={onChange}
          variant={variant}
        >
          {children}
          <RightSlot>
            <StyledIndicator>
              {disabledUnchecked && <IconProhibit css={iconCss} />}
              {checked === true && <IconCheckMark css={iconCss} />}
              {checked === 'indeterminate' && <IconMinus css={iconCss} />}
            </StyledIndicator>
          </RightSlot>
        </StyledCheckboxItem>
      </ItemProvider>
    )
  }
)
