import { useCallback } from 'react'
import type { KeyboardEvent } from 'react'
import { useDirection } from '@radix-ui/react-direction'

interface UseToolbarKeyboardNavigationProps {
  orientation?: 'horizontal' | 'vertical'
  dir?: 'ltr' | 'rtl'
  loop?: boolean
  onKeyDown?: (event: KeyboardEvent<HTMLDivElement>) => void
}

/**
 * Custom keyboard navigation for Radix Toolbar that allows navigation to aria-disabled items.
 *
 * By default, Radix UI Toolbar skips items with aria-disabled="true" during keyboard navigation.
 * This hook intercepts arrow key events and manually handles focus management when aria-disabled
 * items are involved, while delegating to Radix for normal navigation.
 */
export const useToolbarKeyboardNavigation = ({
  orientation = 'horizontal',
  dir,
  loop = true,
  onKeyDown,
}: UseToolbarKeyboardNavigationProps): ((
  event: KeyboardEvent<HTMLDivElement>
) => void) => {
  const localDir = useDirection(dir ?? 'ltr')

  return useCallback(
    (event: KeyboardEvent<HTMLDivElement>) => {
      const { key, currentTarget } = event
      const isHorizontal = orientation === 'horizontal'
      const isRTL = localDir === 'rtl'

      const isForward =
        (isHorizontal && key === (isRTL ? 'ArrowLeft' : 'ArrowRight')) ||
        (!isHorizontal && key === 'ArrowDown')

      const isBackward =
        (isHorizontal && key === (isRTL ? 'ArrowRight' : 'ArrowLeft')) ||
        (!isHorizontal && key === 'ArrowUp')

      if (!isForward && !isBackward) {
        onKeyDown?.(event)
        return
      }

      // Find all navigable items (using Radix's collection attribute)
      const items = Array.from(
        currentTarget.querySelectorAll('[data-radix-collection-item]')
      ).filter(item => !item.hasAttribute('disabled')) as HTMLElement[]

      // Find currently focused item
      const currentIndex = items.findIndex(item =>
        item.contains(document.activeElement)
      )

      if (items.length === 0 || currentIndex === -1) {
        return onKeyDown?.(event)
      }

      // Calculate next item index
      let nextIndex: number
      if (isForward) {
        nextIndex = currentIndex + 1
        if (nextIndex >= items.length) {
          nextIndex = loop ? 0 : currentIndex
        }
      } else {
        nextIndex = currentIndex - 1
        if (nextIndex < 0) {
          nextIndex = loop ? items.length - 1 : currentIndex
        }
      }

      // Check if aria-disabled items are involved
      const currentIsAriaDisabled =
        items[currentIndex]?.getAttribute('aria-disabled') === 'true'
      const nextIsAriaDisabled =
        items[nextIndex]?.getAttribute('aria-disabled') === 'true'

      // Only override Radix if aria-disabled items are involved
      // Otherwise, let Radix handle it with all their edge cases and optimizations
      if (!currentIsAriaDisabled && !nextIsAriaDisabled) {
        onKeyDown?.(event)
        return
      }

      // Take control: prevent Radix's handler and manually move focus
      event.preventDefault()
      event.stopPropagation()

      if (nextIndex !== currentIndex) {
        items[nextIndex]?.focus()
      }

      onKeyDown?.(event)
    },
    [orientation, localDir, loop, onKeyDown]
  )
}
