import React from 'react'
import { useLayoutEffect } from '@mirohq/design-system-use-layout-effect'

export const useDocumentFragment = (): DocumentFragment | undefined => {
  const [fragment, setFragment] = React.useState<DocumentFragment>()

  // we need `useLayoutEffect` here because `DocumentFragment` doesn't exist on the server
  useLayoutEffect(() => {
    setFragment(new DocumentFragment())
  }, [])

  return fragment
}
