export const FORM_FIELD_STATUS = Object.freeze({
  // the field value is valid
  valid: 'valid',
  // the field value is invalid
  invalid: 'invalid',
  // the field value has been changed
  dirty: 'dirty',
  // the field value has not been changed
  pristine: 'pristine',
  // the field has been focused and blurred
  touched: 'touched',
})

export type FormFieldStatus = keyof typeof FORM_FIELD_STATUS
