import React, { useCallback } from 'react'
import { IconChevronDown, IconCross } from '@mirohq/design-system-icons'
import { Trigger as RadixTrigger } from '@radix-ui/react-popover'

import type { StyledActionButtonProps } from './trigger-action-button.styled'
import { StyledActionButton } from './trigger-action-button.styled'
import { useComboboxContext } from '../hooks/use-combobox-context'

export type InputActionButtonProps = Omit<StyledActionButtonProps, 'label'> & {
  openActionLabel: string
  closeActionLabel: string
  clearable?: boolean
  clearActionLabel?: string
}

export const InputActionButton = ({
  openActionLabel,
  closeActionLabel,
  clearActionLabel,
  clearable,
  size,
}: InputActionButtonProps): React.ReactNode => {
  const { openState, setOpenState, value = [], setValue } = useComboboxContext()

  const isEmpty = value.length === 0

  const onToggleClick = useCallback(
    (event: React.MouseEvent<HTMLButtonElement>) => {
      if (openState) {
        // open action will be performed by radix since it's RadixTrigger
        setOpenState(false)
      }

      event.stopPropagation()
    },
    [setOpenState, openState]
  )

  const onClearClick = useCallback(
    (event: React.MouseEvent<HTMLButtonElement>) => {
      setValue([])

      event.stopPropagation()
    },
    [setValue]
  )

  if (isEmpty || clearable === false || clearActionLabel === undefined) {
    return (
      <RadixTrigger asChild aria-haspopup='listbox'>
        <StyledActionButton
          label={openState ? closeActionLabel : openActionLabel}
          size={size}
          onClick={onToggleClick}
          aria-hidden
          tabIndex={-1}
        >
          <IconChevronDown size='small' />
        </StyledActionButton>
      </RadixTrigger>
    )
  }

  return (
    <StyledActionButton
      label={clearActionLabel}
      size={size}
      onClick={onClearClick}
    >
      <IconCross size='small' />
    </StyledActionButton>
  )
}
