import { styled } from '@mirohq/design-system-stitches'
import { Root } from '@radix-ui/react-checkbox'
import { Primitive } from '@mirohq/design-system-primitive'
import {
  IconProhibit,
  IconCheckMark,
  IconMinus,
} from '@mirohq/design-system-icons'
import type { StrictComponentProps } from '@mirohq/design-system-stitches'

export const StyledCheckboxContent = styled(Primitive.div, {
  display: 'block',
  pointerEvents: 'none',
  borderRadius: '$50',
  position: 'relative',
  margin: 'auto',
  boxSizing: 'border-box',
})

const checkboxContentSelector = `& ${StyledCheckboxContent}`

const checkedAndIndeterminate = '&[aria-checked=true], &[data-indeterminate]'
const ariaDisabledCheckedIndeterminate = [
  '&[aria-disabled=true][aria-checked=true]',
  '&:disabled[aria-checked=true]',
  '&[aria-disabled=true][data-indeterminate]',
  '&:disabled[data-indeterminate]',
].join(' ,')
const hoverCheckedIndeterminate =
  '&:hover[aria-checked=true], &:hover[data-indeterminate]'
const ariaDisabledUnChecked =
  '&[aria-disabled=true][aria-checked=false], &:disabled[aria-checked=false]'
const hoverCheckedIndeterminateDisabled = [
  '&:hover[aria-checked=true][aria-disabled=true]',
  '&:hover[aria-checked=true]&:disabled',
  '&:hover[data-indeterminate][aria-disabled=true]',
  '&:hover[data-indeterminate]&:disabled',
].join(' ,')

const hoverAriaDisabled = [
  '&:hover[aria-checked=false][aria-disabled=true]',
  '&:hover[aria-checked=false]&:disabled',
  ...hoverCheckedIndeterminateDisabled.split(' ,'),
].join(' ,')

export const StyledCheckbox = styled(Root, {
  all: 'unset',
  square: '$6',
  color: '$icon-primary-inverted',
  cursor: 'pointer',

  '&[aria-disabled=true], &:disabled': {
    cursor: 'default',
  },
  '&:focus, &[data-focused]': {
    [checkboxContentSelector]: {
      boxShadow: '$focus',
      '@media (forced-colors: active)': {
        outline: 'Highlight solid',
      },
    },
  },
  [hoverCheckedIndeterminateDisabled]: {
    [checkboxContentSelector]: {
      backgroundColor: '$background-neutrals-disabled',
    },
  },

  [ariaDisabledCheckedIndeterminate]: {
    [checkboxContentSelector]: {
      backgroundColor: '$background-neutrals-disabled',
    },
  },
  [ariaDisabledUnChecked]: {
    [checkboxContentSelector]: {
      backgroundColor: '$background-neutrals',
    },
  },

  [`${hoverAriaDisabled},${ariaDisabledCheckedIndeterminate},${ariaDisabledUnChecked}`]:
    {
      [checkboxContentSelector]: {
        color: '$icon-neutrals-disabled',
        '@media (forced-colors: active)': {
          color: 'GrayText',
        },
      },
    },
  variants: {
    variant: {
      'solid-prominent': {
        '&[aria-checked=false]': {
          [checkboxContentSelector]: {
            border: '1px solid $border-neutrals-controls',
          },
        },
        [checkedAndIndeterminate]: {
          [checkboxContentSelector]: {
            backgroundColor: '$background-primary-prominent',
            color: '$icon-primary-inverted',
          },
        },
        [hoverCheckedIndeterminate]: {
          [checkboxContentSelector]: {
            backgroundColor: '$background-primary-prominent-hover',
          },
        },
        '&:hover[aria-checked=false]': {
          [checkboxContentSelector]: {
            border: '1px solid $border-primary',
          },
        },
        [ariaDisabledUnChecked]: {
          [checkboxContentSelector]: {
            border: '1px solid $border-neutrals-controls-disabled',
          },
        },
      },
      'solid-subtle': {
        [checkedAndIndeterminate]: {
          [checkboxContentSelector]: {
            backgroundColor: '$background-primary-subtle',
            color: '$icon-primary',
          },
        },
        '&[aria-checked=false]': {
          [checkboxContentSelector]: {
            backgroundColor: '$background-primary-subtle',
            '@media (forced-colors: active)': {
              border: '1px solid transparent',
            },
          },
        },
        [`${hoverCheckedIndeterminate}, &:hover[aria-checked=false]`]: {
          [checkboxContentSelector]: {
            backgroundColor: '$background-primary-subtle-hover',
            color: '$icon-primary-hover',
          },
        },
        [ariaDisabledUnChecked]: {
          [checkboxContentSelector]: {
            backgroundColor: '$background-neutrals-disabled',
          },
        },
      },
      ghost: {
        '&[aria-checked=false]': {
          [checkboxContentSelector]: {
            '@media (forced-colors: active)': {
              border: '1px solid transparent',
            },
          },
        },
        [checkedAndIndeterminate]: {
          [checkboxContentSelector]: {
            color: '$icon-primary',
          },
        },
        [hoverCheckedIndeterminate]: {
          [checkboxContentSelector]: {
            color: '$icon-primary-hover',
          },
        },
        [ariaDisabledCheckedIndeterminate]: {
          [checkboxContentSelector]: {
            backgroundColor: '$background-neutrals',
          },
        },
        [hoverCheckedIndeterminateDisabled]: {
          [checkboxContentSelector]: {
            backgroundColor: '$background-neutrals',
          },
        },
      },
    },
    size: {
      large: {
        [checkboxContentSelector]: {
          square: '$5',
        },
      },
      small: {
        [checkboxContentSelector]: {
          square: '$4',
        },
      },
    },
  },
})

const markStyles = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%,-50%)',
  variants: {
    size: {
      large: { width: '18px' },
      small: { width: '12px' },
    },
  },
}

export const StyledDisabledMark = styled(IconProhibit, markStyles)

export const StyledCheckMark = styled(IconCheckMark, markStyles)

export const StyledIndeterminateMark = styled(IconMinus, markStyles)

export type StyledCheckboxProps = StrictComponentProps<typeof StyledCheckbox>
