import React from 'react'
import type { DropdownMenuPortalProps } from '@radix-ui/react-dropdown-menu'
import { Portal as RadixPortal } from '@radix-ui/react-dropdown-menu'

export interface PortalProps extends DropdownMenuPortalProps {
  /**
   * Used to force mounting when more control is needed. Useful when controlling
   * animation with React animation libraries. If used on this part, it will be
   * inherited by DropdownMenu.Content and DropdownMenu.SubContent respectively.
   */
  forceMount?: true

  /**
   * Specify a container element to portal the content into.
   */
  container?: HTMLElement | null
}

export const Portal = (props: PortalProps): React.ReactNode => (
  <RadixPortal {...props} />
)
