import type { DragEventHandler, TouchEventHandler } from 'react'
import { isOverTarget } from '@mirohq/design-system-utils'

import {
  disableTextSelection,
  restoreTextSelection,
} from './utils/text-selection'
import type { FocusableElement, InteractionProps, PointerType } from './types'
import { handleFocus } from './utils/focus'

interface TouchEventHandlers {
  onTouchStart: TouchEventHandler<FocusableElement>
  onTouchMove: TouchEventHandler<FocusableElement>
  onTouchEnd: TouchEventHandler<FocusableElement>
  onTouchCancel: TouchEventHandler<FocusableElement>
  onDragStart: DragEventHandler<FocusableElement>
}

export const getEvents = ({
  triggerPressStart,
  triggerPressUp,
  triggerPressEnd,
  addListener,
  removeAllListeners,
  state,
  propsRef,
  cancel,
  window: customWindow,
}: InteractionProps): TouchEventHandlers => {
  const {
    disabled = false,
    shouldCancelOnPointerExit = false,
    preventFocusOnPress = false,
    allowTextSelectionOnPress = false,
  } = propsRef.current ?? {}

  const onScroll = (e: Event): void => {
    if (state.isPressed && (e.target as Element).contains(state.target)) {
      cancel(
        {
          currentTarget: state.target as Element,
          shiftKey: false,
          ctrlKey: false,
          metaKey: false,
          altKey: false,
          event: e,
        },
        state
      )
    }
  }

  const onTouchStart: TouchEventHandler<FocusableElement> = e => {
    if (!e.currentTarget.contains(e.target as Element)) {
      return
    }

    const touch = getTouchFromEvent(e.nativeEvent)
    if (touch === null) {
      return
    }

    state.activePointerId = touch.identifier
    state.ignoreEmulatedMouseEvents = true
    state.isOverTarget = true
    state.isPressed = true
    state.target = e.currentTarget
    state.pointerType = 'touch'

    // Due to browser inconsistencies, especially on mobile browsers, we prevent default
    // on the emulated mouse event and handle focusing the pressable element ourselves.
    handleFocus({
      event: e,
      disabled,
      preventFocusOnPress,
    })

    if (!allowTextSelectionOnPress) {
      disableTextSelection(state.target)
    }

    triggerPressStart(e, state.pointerType)

    addListener(customWindow, 'scroll', onScroll, true)
  }

  const onTouchMove: TouchEventHandler<FocusableElement> = e => {
    if (!e.currentTarget.contains(e.target as Element)) {
      return
    }

    if (!state.isPressed) {
      return
    }

    const touch = getTouchById(e.nativeEvent, state.activePointerId)
    if (touch !== null && isOverTarget(touch, e.currentTarget)) {
      if (!state.isOverTarget) {
        state.isOverTarget = true
        triggerPressStart(e, state.pointerType as PointerType)
      }
    } else if (state.isOverTarget) {
      state.isOverTarget = false
      triggerPressEnd(e, state.pointerType as PointerType, false)
      if (shouldCancelOnPointerExit) {
        cancel(e, state)
      }
    }
  }

  const onTouchEnd: TouchEventHandler<FocusableElement> = e => {
    if (!e.currentTarget.contains(e.target as Element)) {
      return
    }

    if (!state.isPressed) {
      return
    }

    if (state.target === null) {
      return
    }

    const touch = getTouchById(e.nativeEvent, state.activePointerId)
    if (touch != null && isOverTarget(touch, e.currentTarget)) {
      triggerPressUp(e, state.pointerType as PointerType)
      triggerPressEnd(e, state.pointerType as PointerType, true)
    } else if (state.isOverTarget) {
      triggerPressEnd(e, state.pointerType as PointerType, false)
    }

    state.isPressed = false
    state.activePointerId = null
    state.isOverTarget = false
    state.ignoreEmulatedMouseEvents = true
    if (!allowTextSelectionOnPress) {
      restoreTextSelection(state.target)
    }
    removeAllListeners()
  }

  const onTouchCancel: TouchEventHandler<FocusableElement> = e => {
    if (!e.currentTarget.contains(e.target as Element)) {
      return
    }

    if (state.isPressed) {
      cancel(e, state)
    }
  }

  const onDragStart: DragEventHandler<FocusableElement> = e => {
    if (!e.currentTarget.contains(e.target as Element)) {
      return
    }

    // Safari does not call onPointerCancel when a drag starts, whereas Chrome and Firefox do.
    cancel(e, state)
  }

  return {
    onTouchStart,
    onTouchMove,
    onTouchEnd,
    onTouchCancel,
    onDragStart,
  }
}

const getTouchFromEvent = (event: TouchEvent): Touch | null => {
  const { targetTouches } = event
  if (targetTouches.length > 0) {
    return targetTouches[0]
  }
  return null
}

const getTouchById = (
  event: TouchEvent,
  pointerId: null | number
): null | Touch => {
  const changedTouches = event.changedTouches
  for (let i = 0; i < changedTouches.length; i++) {
    const touch = changedTouches[i]
    if (touch.identifier === pointerId) {
      return touch
    }
  }
  return null
}
