import React from 'react'
import type { PopoverPortalProps } from '@radix-ui/react-popover'
import { Portal as RadixPortal } from '@radix-ui/react-popover'

export interface PortalProps extends PopoverPortalProps {
  /**
   *  Used to force mounting when more control is needed. Useful when
   * controlling animation with React animation libraries. If used on this part,
   * it will be inherited by Popover.Content.
   */
  forceMount?: true

  /**
   * Specify a container element to portal the content into.
   */
  container?: HTMLElement | null
}

export const Portal = (props: PortalProps): React.ReactNode => (
  <RadixPortal {...props} />
)
