import React from 'react'
import type {
  ElementRef,
  ForwardRefExoticComponent,
  RefAttributes,
} from 'react'
import { BaseInput } from '@mirohq/design-system-base-input'

import { ClearAction } from './clear-action'
import type { ClearableInputProps, ClearProps } from './types'
import { useBaseInputProps } from './hooks/use-base-input-props'
import { ActionButton } from './partials/action-button'
import { IconSlot } from './partials/icon-slot'

export type InputProps = ClearableInputProps &
  RefAttributes<ElementRef<'input'>>

export const Input = React.forwardRef<ElementRef<'input'>, InputProps>(
  ({ children, ...restProps }, forwardRef) => {
    const {
      clearable = false,
      onClearPress,
      clearLabel,
      ...elementProps
    } = restProps as ClearProps

    const { baseInputProps, inputProps } = useBaseInputProps(
      elementProps,
      forwardRef
    )

    return (
      <BaseInput {...baseInputProps}>
        <BaseInput.Input {...inputProps} />
        {children}
        <ClearAction
          clearable={clearable}
          clearLabel={clearLabel}
          onClearPress={onClearPress}
          value={inputProps.value}
        />
      </BaseInput>
    )
  }
) as ForwardRefExoticComponent<InputProps> & Partials

export interface Partials {
  ActionButton: typeof ActionButton
  IconSlot: typeof IconSlot
}

Input.ActionButton = ActionButton
Input.IconSlot = IconSlot
