import type { MouseEventHandler } from 'react'
import { isVirtualClick } from '@react-aria/utils'
import { isOverTarget } from '@mirohq/design-system-utils'

import type { FocusableElement, InteractionProps, PointerType } from './types'
import { handleFocus } from './utils/focus'

interface MouseEventHandlers {
  onClick: MouseEventHandler<FocusableElement>
  onMouseDown: MouseEventHandler<FocusableElement>
  onMouseEnter: MouseEventHandler<FocusableElement>
  onMouseLeave: MouseEventHandler<FocusableElement>
  onMouseUp: MouseEventHandler<FocusableElement>
}

export const getEvents = ({
  triggerPressStart,
  triggerPressUp,
  triggerPressEnd,
  state,
  addListener,
  removeAllListeners,
  propsRef,
  cancel,
  createEvent,
  document: customDocument,
}: InteractionProps): MouseEventHandlers => {
  const {
    disabled = false,
    shouldCancelOnPointerExit = false,
    preventFocusOnPress = false,
  } = propsRef.current ?? {}
  const onDocumentMouseUp = (e: MouseEvent): void => {
    // Only handle left clicks
    if (e.button !== 0) {
      return
    }

    state.isPressed = false
    removeAllListeners()

    if (state.ignoreEmulatedMouseEvents) {
      state.ignoreEmulatedMouseEvents = false
      return
    }

    if (state.target === null) {
      return
    }

    if (isOverTarget(e, state.target)) {
      triggerPressEnd(
        createEvent(state.target, e),
        state.pointerType as PointerType,
        true
      )
    } else if (state.isOverTarget) {
      triggerPressEnd(
        createEvent(state.target, e),
        state.pointerType as PointerType,
        false
      )
    }

    state.isOverTarget = false
  }

  const onClick: MouseEventHandler<FocusableElement> = e => {
    if (!e.currentTarget.contains(e.target as Element)) {
      return
    }

    // When pressing button with "Space" the event order will be:
    // onkeydown > onkeyup > onclick
    // Only onkeydown will repeat when holding the keydown
    // Because of that the press events are locked until the onClick is released
    if (state.ignoreClickAfterPress) {
      state.ignoreClickAfterPress = false
      return
    }

    if (e.button === 0) {
      if (disabled) {
        e.preventDefault()
      }

      // If triggered from a screen reader or by using element.click(),
      // trigger as if it were a keyboard click.
      if (
        !state.ignoreEmulatedMouseEvents &&
        (state.pointerType === 'virtual' || isVirtualClickHandler(e))
      ) {
        // Ensure the element receives focus (VoiceOver on iOS does not do this)
        handleFocus({
          event: e,
          disabled,
          preventFocusOnPress,
        })

        triggerPressStart(e, 'virtual')
        triggerPressUp(e, 'virtual')
        triggerPressEnd(e, 'virtual', true)
      }

      state.ignoreEmulatedMouseEvents = false
    }
  }

  const onMouseDown: MouseEventHandler<FocusableElement> = e => {
    // Only handle left clicks
    if (e.button !== 0 || !e.currentTarget.contains(e.target as Element)) {
      return
    }

    // Due to browser inconsistencies, especially on mobile browsers, we prevent
    // default on mouse down and handle focusing the pressable element ourselves.
    handleFocus({
      event: e,
      disabled,
      preventFocusOnPress,
      shouldPreventDefault,
    })

    if (state.ignoreEmulatedMouseEvents) {
      return
    }

    state.isPressed = true
    state.isOverTarget = true
    state.target = e.currentTarget
    state.pointerType = isVirtualClickHandler(e) ? 'virtual' : 'mouse'

    triggerPressStart(e, state.pointerType)

    addListener(customDocument, 'mouseup', onDocumentMouseUp, false)
  }

  const onMouseEnter: MouseEventHandler<FocusableElement> = e => {
    if (!e.currentTarget.contains(e.target as Element)) {
      return
    }

    if (state.isPressed && !state.ignoreEmulatedMouseEvents) {
      state.isOverTarget = true
      triggerPressStart(e, state.pointerType as PointerType)
    }
  }

  const onMouseLeave: MouseEventHandler<FocusableElement> = e => {
    if (!e.currentTarget.contains(e.target as Element)) {
      return
    }

    if (state.isPressed && !state.ignoreEmulatedMouseEvents) {
      state.isOverTarget = false
      triggerPressEnd(e, state.pointerType as PointerType, false)
      if (shouldCancelOnPointerExit) {
        cancel(e, state)
      }
    }
  }

  const onMouseUp: MouseEventHandler<FocusableElement> = e => {
    if (!e.currentTarget.contains(e.target as Element)) {
      return
    }

    if (!state.ignoreEmulatedMouseEvents && e.button === 0) {
      triggerPressUp(e, state.pointerType as PointerType)
    }
  }

  return {
    onClick,
    onMouseDown,
    onMouseEnter,
    onMouseLeave,
    onMouseUp,
  }
}

const isVirtualClickHandler = (
  e: React.MouseEvent<Element, MouseEvent>
): boolean => {
  if (e?.nativeEvent === undefined) {
    return false
  }

  const nativeEvent = e.nativeEvent as any

  if (nativeEvent.mozInputSource === undefined) {
    nativeEvent.mozInputSource = null
  }

  return isVirtualClick(nativeEvent)
}

const shouldPreventDefault = (target: Element): boolean =>
  // We cannot prevent default if the target is a draggable element.
  !(target instanceof HTMLElement) || !target.draggable
