import React, { useEffect } from 'react'
import type { ElementRef } from 'react'
import { useFormFieldContext } from '@mirohq/design-system-base-form'

import type { StyledHelperProps } from './helper.styled'
import { StyledHelper } from './helper.styled'

export interface HelperProps extends StyledHelperProps {
  /**
   * The content
   */
  children: React.ReactNode
}

export const Helper = React.forwardRef<
  ElementRef<typeof StyledHelper>,
  HelperProps
>((props, forwardRef) => {
  const { helperId, setHelperId } = useFormFieldContext()

  const id = props.id ?? helperId
  useEffect(() => setHelperId(id), [setHelperId, id])

  return <StyledHelper {...props} id={id} ref={forwardRef} />
})
