import type { ComponentPropsWithRef } from 'react'
import { Primitive } from '@mirohq/design-system-primitive'
import { styled } from '@mirohq/design-system-stitches'
import { focus } from '@mirohq/design-system-styles'

export const StyledCell = styled(Primitive.td, {
  square: '$10',
  marginTop: '$50',
  position: 'relative',
})

export const StyledCellContent = styled(Primitive.div, {
  cursor: 'pointer',
  square: '$10',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  borderRadius: '$50',
  color: '$text-neutrals',

  ...focus.css({
    top: '0',
    left: '0',
    position: 'absolute',
    zIndex: '1',
    boxShadow: '$focus',
    outline: '1px solid transparent',
  }),

  '&[aria-disabled="true"]': {
    textDecoration: 'line-through',
    color: '$text-neutrals-disabled',
    cursor: 'default',
  },

  '&:not([aria-disabled="true"])': {
    _hover: {
      backgroundColor: '$background-neutrals-subtle-hover',
      color: '$text-neutrals',
    },
  },

  variants: {
    today: {
      true: {
        backgroundColor: '$background-neutrals-subtle-hover',
        color: '$text-neutrals',
      },
    },
    selected: {
      true: {
        backgroundColor: '$background-primary-prominent-selected',
        color: '$text-neutrals-inverted',

        '&:not([aria-disabled="true"])': {
          _hover: {
            backgroundColor: '$background-primary-prominent-hover',
            color: '$text-neutrals-inverted',
          },
        },
      },
    },
    range: {
      middle: {
        backgroundColor: '$background-primary-subtle-selected',
        color: '$text-neutrals',
        borderRadius: 0,

        '&:not([aria-disabled="true"])': {
          _hover: {
            backgroundColor: '$background-primary-subtle-hover',
          },
        },
      },
      start: {
        borderTopRightRadius: 0,
        borderBottomRightRadius: 0,
      },
      end: {
        borderTopLeftRadius: 0,
        borderBottomLeftRadius: 0,
      },
    },
    hidden: {
      true: {
        display: 'none',
      },
    },
  },
})

export type StyledCellProps = ComponentPropsWithRef<typeof StyledCell>
