import type { ElementRef } from 'react'
import React from 'react'
import type { CalendarDate, DateDuration } from '@internationalized/date'
import { getWeeksInMonth } from '@internationalized/date'
import type { AriaCalendarGridProps, DateRange } from 'react-aria'
import { useLocale, useCalendarGrid } from 'react-aria'
import type { CalendarState, RangeCalendarState } from 'react-stately'

import { Cell } from './cell'
import type { StyledGridProps } from './grid.styled'
import { StyledDays, StyledGrid } from './grid.styled'

export interface GridProps extends Omit<StyledGridProps, 'onChange'> {
  state: RangeCalendarState | CalendarState
  offset?: DateDuration
  onChange?: (value: DateRange) => void
}

export const Grid = React.forwardRef<ElementRef<typeof StyledGrid>, GridProps>(
  (props, forwardRef) => {
    const { locale } = useLocale()
    const { state, offset = {} } = props
    const startDate: CalendarDate = state.visibleRange.start.add(offset)
    const { gridProps, headerProps, weekDays } = useCalendarGrid(
      props as AriaCalendarGridProps,
      state
    )

    // Get the number of weeks in the month so we can render the proper number of rows.
    const weeksInMonth = getWeeksInMonth(startDate, locale)

    return (
      <StyledGrid ref={forwardRef} {...gridProps} cellPadding='0'>
        <thead {...headerProps}>
          <tr>
            {weekDays.map((day, i) => (
              <StyledDays key={i}>
                <div>{day}</div>
              </StyledDays>
            ))}
          </tr>
        </thead>
        <tbody>
          {Array.from(Array(weeksInMonth).keys()).map(weekIndex => (
            <tr key={weekIndex}>
              {state
                .getDatesInWeek(weekIndex, startDate)
                .map((date: CalendarDate | null, i: number) =>
                  date !== null ? (
                    <Cell
                      key={i}
                      state={state}
                      date={date}
                      currentMonth={startDate}
                    />
                  ) : (
                    <td key={i} />
                  )
                )}
            </tr>
          ))}
        </tbody>
      </StyledGrid>
    )
  }
)
