import type { KeyboardEventHandler, HTMLAttributes } from 'react'
import type {
  Booleanish,
  DOMEventAttributes,
} from '@mirohq/design-system-types'
import { booleanify, removeEventProps } from '@mirohq/design-system-utils'

export const EVENTS_EXCEPTIONS: Array<keyof DOMEventAttributes<HTMLElement>> = [
  'onAnimationEnd',
  'onAnimationEndCapture',
  'onAnimationIteration',
  'onAnimationIterationCapture',
  'onAnimationStartCapture',
  'onBlur',
  'onBlurCapture',
  'onDragEnter',
  'onDragEnterCapture',
  'onDragExit',
  'onDragExitCapture',
  'onDragLeave',
  'onDragLeave',
  'onDragLeaveCapture',
  'onDragLeaveCapture',
  'onDragOver',
  'onDragOver',
  'onDragOverCapture',
  'onDragOverCapture',
  'onFocus',
  'onFocusCapture',
  'onGotPointerCapture',
  'onGotPointerCaptureCapture',
  'onLostPointerCapture',
  'onLostPointerCaptureCapture',
  'onMouseEnter',
  'onMouseLeave',
  'onMouseMove',
  'onMouseMoveCapture',
  'onMouseOut',
  'onMouseOutCapture',
  'onMouseOver',
  'onMouseOverCapture',
  'onPointerEnter',
  'onPointerLeave',
  'onPointerMove',
  'onPointerMoveCapture',
  'onPointerOut',
  'onPointerOutCapture',
  'onPointerOver',
  'onPointerOverCapture',
  'onTouchMove',
  'onTouchMoveCapture',
  'onTransitionEnd',
  'onTransitionEndCapture',
  'onWheel',
  'onWheelCapture',
  'onScroll',
  'onScrollCapture',
]

const getKeyboardEventHandler =
  ({
    allowArrows = false,
    allowEscape = true,
  }): KeyboardEventHandler<HTMLElement> =>
  e => {
    const allowedKeys = ['Tab']

    if (allowArrows) {
      allowedKeys.push('ArrowUp', 'ArrowDown', 'ArrowLeft', 'ArrowRight')
    }

    if (allowEscape) {
      allowedKeys.push('Escape')
    }

    if (!allowedKeys.includes(e.key)) {
      e.preventDefault()
    }
  }

interface AriaDisabledExceptions {
  exceptions?: typeof EVENTS_EXCEPTIONS
  allowArrows?: boolean
  allowEscape?: boolean
}

/**
 * This hook should be used to process a component props and make sure that only
 * some events are available when the component is disabled.
 * Keyboard events are prevented except `Tab` to still enable keyboard navigation.
 * Also the following events are allowed:
 * - PointerMove, PointerEnter, PointerLeave
 * - Drag/Drop
 * - Focus/Blur
 * - Wheel
 * - Scroll
 * - AnimationStart, AnimationEnd, TransitionEnd
 */
export const useAriaDisabled = <
  T extends HTMLAttributes<any> & {
    ariaDisabled?: Booleanish
  },
  R extends Omit<T, 'ariaDisabled'>
>(
  { ariaDisabled, ...props }: T,
  { exceptions = [], ...allowedKeys }: AriaDisabledExceptions = {}
): R | ReturnType<typeof removeEventProps<R, typeof EVENTS_EXCEPTIONS>> => {
  props['aria-disabled'] ??= ariaDisabled

  if (!booleanify(props['aria-disabled'])) {
    return props as R
  }

  const formattedProps = removeEventProps(props, [
    ...EVENTS_EXCEPTIONS,
    ...exceptions,
  ])

  const keyboardEventHandler = getKeyboardEventHandler(allowedKeys)

  formattedProps.onKeyDown = keyboardEventHandler
  formattedProps.onKeyUp = keyboardEventHandler
  formattedProps.onChange ??= () => {}

  // @ts-expect-error: the custom events are only known internally in the hook
  return formattedProps
}
