import React from 'react'
import type { ElementRef, AnchorHTMLAttributes } from 'react'
import { mergeProps } from '@react-aria/utils'
import { booleanify } from '@mirohq/design-system-utils'
import { useHover } from '@react-aria/interactions'
import type { HoverEvents } from '@react-types/shared'
import { usePress } from '@mirohq/design-system-use-press'
import type { PressProps } from '@mirohq/design-system-use-press'
import { useAriaDisabled } from '@mirohq/design-system-use-aria-disabled'
import { Primitive } from '@mirohq/design-system-primitive'

import { StyledBaseButton } from './base-button.styled'
import type { StyledBaseButtonProps } from './base-button.styled'

// DO NOT export it in the index
// Fix TS2742, option 3.1 in the comment bellow
// https://github.com/microsoft/TypeScript/issues/47663#issuecomment-1519138189
export type { HoverEvents } from '@react-types/shared'

type LinkAttrs = 'download' | 'href' | 'media' | 'ping' | 'target'
type LinkProps = Pick<AnchorHTMLAttributes<'a'>, LinkAttrs>
export type BaseButtonProps = StyledBaseButtonProps &
  PressProps &
  HoverEvents &
  LinkProps

export const BaseButton = React.forwardRef<
  ElementRef<'button' | 'a'>,
  BaseButtonProps
>(
  (
    {
      disabled = false,
      'aria-disabled': ariaDisabled,
      children,
      asChild,
      role,
      onHoverStart,
      onHoverEnd,
      onHoverChange,
      tabIndex,
      ...restProps
    },
    forwardRef
  ) => {
    const asLink = 'href' in restProps && restProps.href !== undefined
    const { href, target, rel = '' } = restProps

    const ariaDisabledProps = useAriaDisabled({
      ariaDisabled,
      ...restProps,
    })

    const { pressProps } = usePress({
      preventFocusOnPress: true,
      disabled: disabled || booleanify(ariaDisabled),
      ...ariaDisabledProps,
    })

    const { hoverProps, isHovered } = useHover({
      onHoverStart,
      onHoverEnd,
      onHoverChange,
    })

    const elementProps = mergeProps(pressProps, hoverProps)

    let relProp: string | undefined = rel

    if (asLink && target === '_blank') {
      if (!relProp.includes('noreferrer')) {
        relProp = `noreferrer ${relProp}`
      }

      if (!relProp.includes('noopener')) {
        relProp = `noopener ${relProp}`
      }

      relProp = relProp.trim()
    }

    if (relProp === '') {
      relProp = undefined
    }

    const tabIndexProps =
      typeof tabIndex === 'number'
        ? {
            tabIndex,
          }
        : {}

    if (disabled && (asLink || asChild === true)) {
      tabIndexProps.tabIndex = -1
    }

    return (
      <StyledBaseButton
        {...elementProps}
        {...tabIndexProps}
        data-hovered={isHovered ? '' : (elementProps as any)['data-hovered']}
        asChild={asLink || asChild}
        disabled={disabled}
        role={role ?? (asChild === true ? 'button' : undefined)}
        aria-disabled={booleanify(ariaDisabled) ? 'true' : ariaDisabled}
        // Button can be an <a> but TS will never know because it will only happen in runtime via asChild
        // @ts-expect-error
        ref={forwardRef}
      >
        {asLink ? (
          <Primitive.a
            href={href}
            target={target}
            rel={relProp}
            asChild={asChild}
          >
            {children}
          </Primitive.a>
        ) : (
          children
        )}
      </StyledBaseButton>
    )
  }
)
