import React from 'react'
import type { ElementRef } from 'react'
import { Arrow as RadixArrow } from '@radix-ui/react-popover'

import { LINE_HEIGHT, LINE_WIDTH, StyledLine } from './line.styled'
import type { StyledLineProps } from './line.styled'

export interface LineProps extends StyledLineProps {}

export const Line = React.forwardRef<ElementRef<typeof StyledLine>, LineProps>(
  (props, forwardRef) => (
    // We have to use RadixArrow with asChild here instead of styling it,
    // otherwise it's not possible to pass children, it will use arrow svg.
    // In this case asChild won't merge the nodes, but will apply some properties to the first child,
    // so we also need to add an additional span to avoid Stitches error.
    // In the end we get 2 nodes: Radix's span with positioning, span with radix svg props and our svg with styling
    <RadixArrow asChild>
      <span>
        <StyledLine ref={forwardRef} {...props} aria-hidden viewBox='0 0 6 50'>
          <rect
            x={LINE_WIDTH / 2 - 0.5}
            y='0'
            width='1'
            height={LINE_HEIGHT - LINE_WIDTH}
          />
          <circle
            cx={LINE_WIDTH / 2}
            cy={LINE_HEIGHT - LINE_WIDTH / 2}
            r={LINE_WIDTH / 2}
          />
        </StyledLine>
      </span>
    </RadixArrow>
  )
)
