import type { ElementRef } from 'react'
import React from 'react'
import { IconCross } from '@mirohq/design-system-icons'
import { Tooltip } from '@mirohq/design-system-tooltip'

import type { StyledClearActionProps } from './clear-action.styled'
import { StyledClearAction } from './clear-action.styled'

interface ClearActionProps extends Omit<StyledClearActionProps, 'children'> {
  label: string
}

export const ClearAction = React.forwardRef<
  ElementRef<typeof StyledClearAction>,
  ClearActionProps
>(({ 'aria-label': ariaLabel, label, ...restProps }, forwardRef) => (
  <Tooltip>
    <Tooltip.Trigger asChild>
      <StyledClearAction
        {...restProps}
        ref={forwardRef}
        aria-label={ariaLabel ?? label}
      >
        <IconCross />
      </StyledClearAction>
    </Tooltip.Trigger>
    <Tooltip.Content>{label}</Tooltip.Content>
  </Tooltip>
))
