import React, { createContext, useContext } from 'react'

import type { Variant } from './types'

interface PopoverValues {
  children: React.ReactNode
  variant?: Variant
}

interface PopoverContextProps {
  variant?: Variant
}

const PopoverContext = createContext<PopoverContextProps>({} as any)

/**
 * The PopoverProvider is used to manage the state of the popover,
 * when it opens or closes, and what triggers it.
 */
export const PopoverProvider = ({
  children,
  variant,
}: PopoverValues): React.ReactNode => (
  <PopoverContext.Provider
    value={{
      variant,
    }}
  >
    {children}
  </PopoverContext.Provider>
)

/**
 * The usePopoverContext hook is used to access the state of the popover.
 */
export const usePopoverContext = (): PopoverContextProps =>
  useContext(PopoverContext)
