import React from 'react'
import type { ElementRef, ForwardRefExoticComponent } from 'react'

import type { StyledToolbarProps } from './toolbar.styled'
import { StyledToolbar } from './toolbar.styled'
import { Icon } from './partials/icon'
import { Separator } from './partials/separator'
import { Link } from './partials/link'
import { Item } from './partials/item'
import { useToolbarKeyboardNavigation } from './hooks/use-toolbar-keyboard-navigation'

export interface ToolbarProps extends StyledToolbarProps {
  /**
   * @deprecated Please use variant for non-floating toolbar
   */
  unstyled?: boolean

  /**
   * When floating, the toolbar has shadow and border to differentiate it from the surface
   * @default floating
   */
  variant?: StyledToolbarProps['variant']

  /**
   * The orientation of the toolbar
   * @default 'horizontal'
   */
  orientation?: 'horizontal' | 'vertical'

  /**
   * The reading direction of the toolbar
   * @default 'ltr'
   */
  dir?: 'ltr' | 'rtl'

  /**
   * When true, keyboard navigation will loop from last tab to first, and vice versa
   * @default true
   */
  loop?: boolean
}

export const Toolbar = React.forwardRef<
  ElementRef<typeof StyledToolbar>,
  ToolbarProps
>(
  (
    {
      unstyled,
      orientation = 'horizontal',
      variant = 'floating',
      dir = 'ltr',
      loop = true,
      onKeyDown,
      ...restProps
    },
    forwardRef
  ) => {
    const variantProp =
      unstyled === undefined ? variant : unstyled ? 'flat' : 'floating'

    const handleKeyDown = useToolbarKeyboardNavigation({
      orientation,
      dir,
      loop,
      onKeyDown,
    })

    return (
      <StyledToolbar
        variant={variantProp}
        orientation={orientation}
        dir={dir}
        loop={loop}
        onKeyDown={handleKeyDown}
        ref={forwardRef}
        {...restProps}
      />
    )
  }
) as ForwardRefExoticComponent<ToolbarProps> & Partials

// Partials
// -----------------------------------------------------------------------------

export interface Partials {
  Icon: typeof Icon
  Item: typeof Item
  Link: typeof Link
  Separator: typeof Separator
}

Toolbar.Icon = Icon
Toolbar.Item = Item
Toolbar.Link = Link
Toolbar.Separator = Separator
