import React, { useRef } from 'react'
import type { ElementRef } from 'react'
import { IconCheckMark } from '@mirohq/design-system-icons'
import {
  ItemText as RadixItemText,
  ItemIndicator as RadixItemIndicator,
} from '@radix-ui/react-select'
import {
  booleanify,
  mergeRefs,
  handleVirtualClick,
} from '@mirohq/design-system-utils'
import { StyledItemCheck } from '@mirohq/design-system-base-select'

import { StyledItem, StyledItemText } from './item.styled'
import type { StyledItemProps } from './item.styled'

export interface ItemProps extends StyledItemProps {
  /**
   * The value given as data when submitted with a name.
   */
  value: string

  /**
   * When true, prevents the user from interacting with the item.
   * @default false
   */
  disabled?: boolean

  /**
   * Optional text used for typeahead purposes. By default the typeahead
   * behavior will use the Select's item text. Use this when the content is
   * complex, or you have non-textual content inside.
   */
  textValue?: string
}

export const Item = React.forwardRef<ElementRef<typeof StyledItem>, ItemProps>(
  (
    {
      disabled = false,
      textValue,
      'aria-disabled': ariaDisabled,
      onKeyDown,
      onPointerUp,
      onPointerMove,
      onPointerLeave,
      children,
      asChild,
      onClick,
      ...restProps
    },
    forwardRef
  ) => {
    const ref = useRef<HTMLDivElement>(null)

    return (
      <StyledItem
        {...restProps}
        aria-disabled={booleanify(ariaDisabled) ? ariaDisabled : undefined}
        onClick={e => {
          handleVirtualClick(e, ref)
          onClick?.(e)
        }}
        onKeyDown={e => {
          if (
            booleanify(ariaDisabled) &&
            e.code !== 'ArrowUp' &&
            e.code !== 'ArrowDown' &&
            e.code !== 'Escape'
          ) {
            e.preventDefault()
            e.stopPropagation()
            return
          }

          onKeyDown?.(e)
        }}
        onPointerUp={e => {
          if (booleanify(ariaDisabled)) {
            e.preventDefault()
            return
          }

          onPointerUp?.(e)
        }}
        onPointerMove={e => {
          if (booleanify(ariaDisabled)) {
            e.preventDefault()
            return
          }

          onPointerMove?.(e)
        }}
        onPointerLeave={e => {
          if (booleanify(ariaDisabled)) {
            e.preventDefault()
            return
          }

          onPointerLeave?.(e)
        }}
        textValue={textValue}
        disabled={disabled}
        ref={mergeRefs([ref, forwardRef])}
      >
        <RadixItemIndicator asChild>
          <StyledItemCheck>
            <IconCheckMark size='small' />
          </StyledItemCheck>
        </RadixItemIndicator>
        <RadixItemText asChild>
          <StyledItemText asChild={asChild}>{children}</StyledItemText>
        </RadixItemText>
      </StyledItem>
    )
  }
)
