import React from 'react'
import type {
  ElementRef,
  ForwardRefExoticComponent,
  RefAttributes,
} from 'react'
import { IconMagnifyingGlass } from '@mirohq/design-system-icons'
import { BaseInput } from '@mirohq/design-system-base-input'

import { ClearAction } from './clear-action'
import type { ClearableInputProps, ClearProps } from './types'
import { useBaseInputProps } from './hooks/use-base-input-props'
import { ActionButton } from './partials/action-button'

export type InputSearchProps = ClearableInputProps &
  RefAttributes<ElementRef<'input'>>

export const InputSearch = React.forwardRef<
  ElementRef<'input'>,
  InputSearchProps
>(({ children, onChange, ...restProps }, forwardRef) => {
  const {
    clearable = false,
    onClearPress,
    clearLabel,
    value,
    ...elementProps
  } = restProps as ClearProps
  const { baseInputProps, inputProps } = useBaseInputProps(
    { ...elementProps, onChange, value },
    forwardRef
  )

  const hasQuery = inputProps.value !== ''

  return (
    <BaseInput {...baseInputProps} variant='subtle'>
      <BaseInput.Input {...inputProps} ref={forwardRef} />
      {children}
      {hasQuery && (
        <ClearAction
          clearable={clearable}
          clearLabel={clearLabel}
          onClearPress={onClearPress}
          value={inputProps.value}
        />
      )}
      <BaseInput.IconSlot>
        <IconMagnifyingGlass />
      </BaseInput.IconSlot>
    </BaseInput>
  )
}) as ForwardRefExoticComponent<InputSearchProps> & Partials

export interface Partials {
  ActionButton: typeof ActionButton
}

InputSearch.ActionButton = ActionButton
