import React from 'react'
import type { ElementRef } from 'react'
import { mergeRefs, booleanify } from '@mirohq/design-system-utils'
import { Tooltip } from '@mirohq/design-system-tooltip'

import { StyledActionButton } from './action-button.styled'
import type { StyledActionButtonProps } from './action-button.styled'
import { useInputContext } from '../hooks/use-input-context'

export type ActionButtonProps = StyledActionButtonProps &
  (
    | {
        label: string
        'aria-label'?: string
      }
    // force aria-label if label is not present
    | {
        label?: never
        'aria-label': string
      }
  )

export const ActionButton = React.forwardRef<
  ElementRef<typeof StyledActionButton>,
  ActionButtonProps
>(({ 'aria-label': ariaLabel, label, children, ...restProps }, forwardRef) => {
  const {
    valid,
    hovered,
    editable,
    'aria-disabled': ariaDisabled,
    disabled,
    readOnly,
    actionButtonRef,
  } = useInputContext()

  const showInputSlot = valid === undefined || booleanify(hovered)

  const hasPressEvent = Object.keys(restProps).some(key =>
    key.startsWith('onPress')
  )
  const customDisabled = restProps.disabled

  const actionButton = (
    <StyledActionButton
      type='button'
      {...restProps}
      aria-label={ariaLabel ?? label}
      disabled={hasPressEvent ? customDisabled : customDisabled ?? !editable}
      disableAppearance={
        hasPressEvent
          ? !booleanify(readOnly) && customDisabled
          : booleanify(disabled ?? ariaDisabled)
      }
      readOnlyAppearance={readOnly}
      ref={mergeRefs([forwardRef, actionButtonRef])}
    >
      {children}
    </StyledActionButton>
  )

  if (!showInputSlot) return <></>

  if (
    (label === undefined || label === '') &&
    ariaLabel !== undefined &&
    ariaLabel !== ''
  ) {
    return actionButton
  }

  return (
    <Tooltip>
      <Tooltip.Trigger asChild>{actionButton}</Tooltip.Trigger>
      <Tooltip.Content>{label}</Tooltip.Content>
    </Tooltip>
  )
})
