import React, { useRef } from 'react'
import type { ComponentPropsWithRef } from 'react'
import { useLayoutEffect } from '@mirohq/design-system-use-layout-effect'

import { useContent } from '../hooks/use-content'
import { StyledRightSlot } from './right-slot.styled'

export const RightSlot = (
  props: ComponentPropsWithRef<typeof StyledRightSlot>
): JSX.Element => {
  const { rightSlotMount, rightSlotDestroy } = useContent()

  const ref = useRef<HTMLDivElement>(null)

  useLayoutEffect(() => {
    if (ref.current !== null) {
      const width = ref.current.getBoundingClientRect().width
      const index = rightSlotMount(width)

      return () => rightSlotDestroy(index)
    }

    return () => {}
  }, [rightSlotMount, rightSlotDestroy, ref])

  return <StyledRightSlot ref={ref} {...props} />
}
