import * as RadixTabs from '@radix-ui/react-tabs'
import { useControllableState } from '@radix-ui/react-use-controllable-state'
import React from 'react'

import { IconSlot } from './partials/icon-slot'
import { List } from './partials/list'
import { TabsContextProvider } from './partials/provider'
import { StyledRoot } from './partials/root.styled'
import type { StyledRootProps } from './partials/root.styled'
import { Trigger } from './partials/trigger'
import type { StyledTriggerProps } from './partials/trigger.styled'

export interface TabsProps
  extends Omit<StyledRootProps, 'onValueChange' | 'onChange'> {
  /**
   * Change the Tabs style
   * @default 'tabs'
   */
  variant?: StyledTriggerProps['variant']
  /**
   * The value of the tab that should be active when initially rendered.
   * Use when you do not need to control the state of the tabs.
   */
  defaultValue?: string
  /**
   * The controlled value of the tab to activate.
   * Should be used in conjunction with onChange.
   */
  value?: string
  /**
   * Event handler called when the value changes.
   * @param id the value associated with the newly selected tab.
   */
  onChange?: (id: string) => void
  /**
   * The tabs size.
   */
  size?: StyledTriggerProps['size']
}

export const Tabs = React.forwardRef<
  React.ElementRef<typeof StyledRoot>,
  TabsProps
>(
  (
    {
      variant = 'tabs',
      size = 'large',
      onChange,
      defaultValue,
      value,
      ...props
    },
    forwardRef
  ) => {
    const [currentValue, setCurrentValue] = useControllableState({
      prop: value,
      defaultProp: defaultValue,
      onChange,
    })

    return (
      <TabsContextProvider
        variant={variant}
        size={size}
        onValueChange={setCurrentValue}
      >
        <StyledRoot
          {...props}
          ref={forwardRef}
          value={currentValue}
          onValueChange={setCurrentValue}
          activationMode='manual'
        />
      </TabsContextProvider>
    )
  }
) as React.ForwardRefExoticComponent<TabsProps> & Partials

interface Partials {
  List: typeof List
  Trigger: typeof Trigger
  Content: typeof RadixTabs.Content
  IconSlot: typeof IconSlot
}

Tabs.List = List
Tabs.Trigger = Trigger
Tabs.Content = RadixTabs.Content
Tabs.IconSlot = IconSlot
