import React from 'react'
import type { ElementRef, ForwardRefExoticComponent } from 'react'
import { IconCross } from '@mirohq/design-system-icons'

import { StyledChip, StyledChipButton, StyledChipContent } from './chip.styled'
import type { StyledChipProps } from './chip.styled'
import { ImageSlot } from './partials/image-slot'

interface RemovableProps {
  /**
   * Event handler called when the chip's remove button is clicked.
   */
  onRemove: (event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => void

  /**
   * Remove button label to make the button recognizable by the screen readers.
   */
  removeAriaLabel: string
}

export type ChipProps = (StyledChipProps & {
  /**
   * Show a button to remove the Chip
   * @default true
   */
  removable?: boolean
}) &
  (
    | RemovableProps
    | { removable: false; removeAriaLabel?: never; onRemove?: never }
  )

export const Chip = React.forwardRef<ElementRef<typeof StyledChip>, ChipProps>(
  (
    { children, removable = true, onRemove, removeAriaLabel, ...restProps },
    forwardRef
  ) => (
    <StyledChip {...restProps} ref={forwardRef}>
      <StyledChipContent>{children}</StyledChipContent>
      {removable && (
        <StyledChipButton
          type='button'
          onClick={onRemove}
          aria-label={removeAriaLabel}
        >
          <IconCross size='small' />
        </StyledChipButton>
      )}
    </StyledChip>
  )
) as ForwardRefExoticComponent<ChipProps> & Partials

export interface Partials {
  ImageSlot: typeof ImageSlot
}

Chip.ImageSlot = ImageSlot
