import { Combobox as AriakitComboboxTrigger } from '@ariakit/react'
import { booleanify, mergeRefs } from '@mirohq/design-system-utils'
import { Anchor as RadixAnchor } from '@radix-ui/react-popover'
import type { ElementRef } from 'react'
import React from 'react'
import { Primitive } from '@mirohq/design-system-primitive'

import { useComboboxContext } from '../hooks/use-combobox-context'
import type { InputProps } from './input'
import { Input } from './input'

interface TriggerAsChildProps {
  /**
   * Render the trigger as a custom component, passed in as a child
   */
  asChild: true
  /**
   * The content that will be rendered inside the Combobox.Trigger in stead of the Combobox.Input, when asChild is true.
   */
  children: React.ReactNode
}

type TriggerDefaultProps = InputProps & {
  /**
   * Render the trigger as a custom component, passed in as a child
   */
  asChild?: false
}

export type TriggerProps = TriggerAsChildProps | TriggerDefaultProps

export const Trigger = React.forwardRef<ElementRef<'div'>, TriggerProps>(
  (props, forwardRef) => {
    const comboboxContextProps = useComboboxContext()

    const {
      'aria-disabled': ariaDisabled,
      disabled,
      readOnly,
      triggerRef,
      setOpenState,
    } = comboboxContextProps

    return (
      <RadixAnchor
        ref={mergeRefs([triggerRef, forwardRef])}
        onClick={() => {
          if (
            !booleanify(disabled) &&
            !booleanify(ariaDisabled) &&
            !booleanify(readOnly)
          ) {
            setOpenState(true)
          }
        }}
      >
        <AriakitComboboxTrigger
          // Ariakit's default `canShow` reads `event.currentTarget.value.length`
          // which crashes when the trigger is a non-input element (e.g. asChild
          // with a div or chip). Setting these to `true` bypasses `canShow`
          // entirely. Open state is already managed by RadixAnchor's onClick.
          // See: https://ariakit.org/reference/combobox#showonclick
          showOnClick
          showOnKeyPress
          render={({ ref: ariakitTriggerRef, ...ariakitProps }) => (
            <Primitive.span asChild ref={ariakitTriggerRef} {...ariakitProps}>
              {props.asChild === true ? props.children : <Input {...props} />}
            </Primitive.span>
          )}
        />
      </RadixAnchor>
    )
  }
)
