import React from 'react'
import type { TooltipPortalProps } from '@radix-ui/react-tooltip'
import { Portal as RadixPortal } from '@radix-ui/react-tooltip'

export interface PortalProps extends TooltipPortalProps {
  /**
   * Used to force mounting when more control is needed. Useful when controlling
   * animation with React animation libraries. If used on this part, it will be
   * inherited by Tooltip.Content.
   */
  forceMount?: true

  /**
   * Specify a container element to portal the content into.
   */
  container?: HTMLElement | null
}

export const Portal = (props: PortalProps): React.ReactNode => (
  <RadixPortal {...props} />
)
