import React from 'react'
import { IconCross } from '@mirohq/design-system-icons'
import type { ElementRef } from 'react'

import { StyledClose } from './close.styled'
import { usePopoverContext } from '../use-popover-context'
import type { StyledCloseProps } from './close.styled'

export interface CloseProps extends StyledCloseProps {
  /**
   * The appearance of close's button.
   */
  variant?: StyledCloseProps['variant']
}

export const Close = React.forwardRef<
  ElementRef<typeof StyledClose>,
  CloseProps
>((props, forwardRef) => {
  const { variant } = usePopoverContext()

  return (
    <StyledClose
      {...props}
      ref={forwardRef}
      data-testid='close-icon'
      variant={variant}
    >
      <IconCross size='small' />
    </StyledClose>
  )
})
