import React from 'react'
import type { ElementRef } from 'react'

import type { StyledListProps } from './list.styled'
import { ListStyled } from './list.styled'
import { TabsContext } from './provider'

export type ListProps = StyledListProps & {}

export const List = React.forwardRef<ElementRef<typeof ListStyled>, ListProps>(
  ({ children, ...restProps }, forwardRef) => {
    const { variant, size } = React.useContext(TabsContext)

    return (
      <ListStyled {...restProps} variant={variant} size={size} ref={forwardRef}>
        {children}
      </ListStyled>
    )
  }
)
