import type { ElementRef } from 'react'
import React, { useEffect } from 'react'
import { Button } from '@mirohq/design-system-button'
import type { BaseButtonProps } from '@mirohq/design-system-base-button'

import { useCalendarContext } from '../use-calendar-context'

export type SaveButtonProps = BaseButtonProps & {
  /**
   * Event handler called when the calendar value is saved
   */
  onSave?: () => void
}

export const SaveButton = React.forwardRef<
  ElementRef<typeof Button>,
  SaveButtonProps
>(({ onSave, children, ...restProps }, forwardRef) => {
  const { setImmediateSave, onSaveClick } = useCalendarContext()

  useEffect(() => {
    setImmediateSave(true)

    return () => setImmediateSave(false)
  }, [setImmediateSave])

  const onSaveHandler = (): void => {
    onSaveClick()
    if (onSave !== undefined) {
      onSave()
    }
  }

  return (
    <Button
      ref={forwardRef}
      {...restProps}
      variant='primary'
      size='medium'
      onClick={onSaveHandler}
    >
      {children}
    </Button>
  )
}) as React.ForwardRefExoticComponent<SaveButtonProps>
