import React from 'react'
import type { ElementRef } from 'react'

import type { StyledRangeProps } from './range.styled'
import { StyledRange } from './range.styled'
import { useSliderContext } from '../hooks/use-slider-context'

export interface RangeProps extends StyledRangeProps {}

export const Range = React.forwardRef<
  ElementRef<typeof StyledRange>,
  RangeProps
>((props, forwardRef) => {
  const { disabled, ariaDisabled } = useSliderContext()

  return (
    <StyledRange
      {...props}
      disabled={disabled}
      ariaDisabled={ariaDisabled}
      ref={forwardRef}
    />
  )
})
