import React from 'react'
import type { ElementRef } from 'react'
import { useHover } from '@react-aria/interactions'
import type { HoverEvents } from '@react-types/shared'
import { mergeProps } from '@react-aria/utils'
import { usePress } from '@mirohq/design-system-use-press'
import { booleanify } from '@mirohq/design-system-utils'
import type { PressEvents } from '@mirohq/design-system-use-press'
import { useAriaDisabled } from '@mirohq/design-system-use-aria-disabled'

import { StyledBaseItem } from './base-item.styled'
import type { StyledBaseItemProps } from './base-item.styled'

export interface BaseItemProps
  extends StyledBaseItemProps,
    HoverEvents,
    PressEvents {
  disabled?: boolean
}

export const BaseItem = React.forwardRef<
  ElementRef<typeof StyledBaseItem>,
  BaseItemProps
>(
  (
    {
      disabled = false,
      'aria-disabled': ariaDisabled,
      asChild,
      onHoverStart,
      onHoverEnd,
      onHoverChange,
      ...restProps
    },
    forwardRef
  ) => {
    const elementProps = useAriaDisabled(
      {
        ...restProps,
        ariaDisabled,
      },
      { allowArrows: true }
    )

    const { pressProps } = usePress({
      preventFocusOnPress: 'auto',
      disabled: disabled || booleanify(ariaDisabled),
      ...elementProps,
    })

    const { hoverProps, isHovered } = useHover({
      onHoverStart,
      onHoverEnd,
      onHoverChange,
    })

    return (
      <StyledBaseItem
        {...mergeProps(pressProps, hoverProps)}
        data-hovered={isHovered ? '' : undefined}
        asChild={asChild}
        disabled={disabled}
        aria-disabled={booleanify(ariaDisabled) ? 'true' : ariaDisabled}
        ref={forwardRef}
      />
    )
  }
)
