import React from 'react'
import type { ElementRef, ForwardRefExoticComponent } from 'react'

import { StyledGrid } from './grid.styled'
import type { StyledGridProps } from './grid.styled'
import { Item } from './partials/item'

export interface GridProps extends StyledGridProps {
  /**
   * Defines how elements are aligned along the block (column) axis.
   */
  align?: StyledGridProps['align']

  /**
   * Defines how elements are aligned along the inline (row) axis.
   */
  justify?: StyledGridProps['justify']

  /**
   * Aligns grid items along the inline (row) axis.
   * This value applies to all grid items inside the container.
   */
  justifyItems?: StyledGridProps['justifyItems']

  /**
   * Defines how elements are automatically placed in the grid.
   */
  flow?: StyledGridProps['flow']

  /**
   * Defines the width of the gutter between both columns and rows. Uses the
   * space token.
   */
  gap?: StyledGridProps['gap']

  /**
   * Defines the width of the gutter between rows. Uses the
   * space token.
   */
  gapX?: StyledGridProps['gapX']

  /**
   * Defines the width of the gutter between columns. Uses the
   * space token.
   */
  gapY?: StyledGridProps['gapY']

  /**
   * Defines the number of columns in the grid.
   */
  columns?: StyledGridProps['columns']

  /**
   * Defines the number of rows in the grid.
   */
  rows?: StyledGridProps['rows']
}

export const Grid = React.forwardRef<ElementRef<typeof StyledGrid>, GridProps>(
  (props, forwardRef) => <StyledGrid {...props} ref={forwardRef} />
) as ForwardRefExoticComponent<GridProps> & Partials

// Partials
// -----------------------------------------------------------------------------

export interface Partials {
  Item: typeof Item
}

Grid.Item = Item
