import type { ComponentPropsWithRef } from 'react'
import { Primitive } from '@mirohq/design-system-primitive'
import { styled } from '@mirohq/design-system-stitches'

import { StyledLabel } from './label.styled'
import { StyledDescription } from './description.styled'
import { StyledHelper } from './helper.styled'
import { StyledMessage } from './message.styled'

export const StyledField = styled(Primitive.div, {
  marginBottom: '$300',
  position: 'relative',
  '&[data-disabled]': {
    [`${StyledLabel}, ${StyledDescription}, ${StyledHelper}, ${StyledMessage}`]:
      {
        color: '$text-neutrals-disabled',
        svg: {
          color: '$icon-neutrals-disabled',
        },
      },
  },
  '&[data-readonly]': {
    [`${StyledLabel}, ${StyledDescription}, ${StyledHelper}, ${StyledMessage}`]:
      {
        color: '$text-neutrals-subtle',
        svg: {
          color: '$icon-neutrals-disabled',
        },
      },
  },
})

export type StyledFieldProps = ComponentPropsWithRef<typeof StyledField>
