import React, { useEffect } from 'react'
import type { ElementRef } from 'react'
import {
  useFormFieldContext,
  FORM_FIELD_STATUS,
} from '@mirohq/design-system-base-form'
import {
  IconExclamationPointCircle,
  IconCheckMark,
} from '@mirohq/design-system-icons'

import type { StyledMessageProps } from './message.styled'
import { StyledMessage } from './message.styled'

export interface MessageProps extends Omit<StyledMessageProps, 'status'> {
  chidren?: React.ReactNode
}

export const Message = React.forwardRef<
  ElementRef<typeof StyledMessage>,
  MessageProps
>(({ children, ...restProps }, forwardRef) => {
  const { messageId, setMessageId, status } = useFormFieldContext()

  const id = restProps.id ?? messageId
  useEffect(() => setMessageId(id), [setMessageId, id])

  if (
    status !== FORM_FIELD_STATUS.invalid &&
    status !== FORM_FIELD_STATUS.valid
  ) {
    return null
  }

  return (
    <StyledMessage
      role='alert'
      {...restProps}
      id={id}
      status={status}
      ref={forwardRef}
    >
      {status === 'valid' && (
        <IconCheckMark size='small' color='icon-success' />
      )}
      {status === 'invalid' && (
        <IconExclamationPointCircle size='small' color='icon-danger' />
      )}
      {children}
    </StyledMessage>
  )
})
