import { useLocalStorage } from '@mirohq/design-system-use-local-storage'
import type { Nullable } from '@mirohq/design-system-types'

interface UseTheme {
  theme: Nullable<string>
  setTheme: (theme: Nullable<string>) => void
}

export const useTheme = (): UseTheme => {
  const [theme, setTheme] = useLocalStorage<Nullable<string>>('theme', null)
  return { theme, setTheme }
}
