import React, {
  createContext,
  useContext,
  useState,
  useRef,
  useCallback,
} from 'react'
import { addPropsToChildren } from '@mirohq/design-system-utils'

import type { ContainerSpacing } from '../types'

interface ContentContext {
  rightSlotMount: (width: number) => number
  rightSlotDestroy: (index: number) => void
  containerSpacing: ContainerSpacing
}

interface ContentProviderProps {
  children: React.ReactNode
  containerSpacing?: ContainerSpacing
}

const Context = createContext<ContentContext>({
  rightSlotMount: () => 0,
  rightSlotDestroy: () => {},
  containerSpacing: 'medium',
})

export const ContentProvider = ({
  children,
  containerSpacing = 'medium',
}: ContentProviderProps): JSX.Element => {
  const [maxWidth, setMaxWidth] = useState<number>(0)

  // we need maxRef, so we don't rerender RightSlot whenever we change maxWidth
  const maxRef = useRef<number>(0)

  // rightSlot index to remove width from map on destroy
  const indexRef = useRef<number>(0)

  const widthMapRef = useRef<Map<number, number>>(new Map())

  const updateMaxWith = useCallback((value: number) => {
    maxRef.current = value
    setMaxWidth(value)
  }, [])

  // do not use state here, otherwise it will case many rerenders in RightSlot
  const rightSlotMount = useCallback(
    (width: number): number => {
      indexRef.current++

      widthMapRef.current.set(indexRef.current, width)

      if (width > maxRef.current) {
        updateMaxWith(width)
      }

      return indexRef.current
    },
    [updateMaxWith]
  )

  // do not use state here, otherwise it will case many rerenders in RightSlot
  const rightSlotDestroy = useCallback(
    (index: number): void => {
      widthMapRef.current.delete(index)

      if (widthMapRef.current.size === 0) {
        updateMaxWith(0)
      } else {
        const maximum = Math.max(...Array.from(widthMapRef.current.values()))
        updateMaxWith(maximum)
      }
    },
    [updateMaxWith]
  )

  const formattedChildren = addPropsToChildren(children, () => true, {
    UNSAFE_style: {
      '--right-slot-max-width': `${Math.ceil(maxWidth)}px`,
    },
  })

  return (
    <Context.Provider
      value={{
        rightSlotMount,
        rightSlotDestroy,
        containerSpacing,
      }}
    >
      {formattedChildren}
    </Context.Provider>
  )
}

export const useContent = (): ContentContext => useContext(Context)
