import React from 'react'
import { styled } from '@mirohq/design-system-stitches'
import { Primitive } from '@mirohq/design-system-primitive'
import type { ElementRef, ComponentPropsWithRef } from 'react'
import { addPropsToChildren } from '@mirohq/design-system-utils'
import type {
  IconReactElement,
  IconSizes,
} from '@mirohq/design-system-base-icon'
import { isIconComponent } from '@mirohq/design-system-base-icon'
import type { ExtractValidKeys } from '@mirohq/design-system-types'

import { useButtonContext } from '../hooks/use-button-context'
import type { StyledButtonProps } from '../button.styled'

export const StyledIconSlot = styled(Primitive.div, {})
export type StyledIconSlotProps = ComponentPropsWithRef<typeof StyledIconSlot>

type ButtonSize = ExtractValidKeys<StyledButtonProps['size']>

const buttonIconSizes: { [key in ButtonSize]: IconSizes } = {
  small: 'small',
  medium: 'small',
  large: 'medium',
  'x-large': 'medium',
}

export interface IconSlotProps extends StyledIconSlotProps {
  /**
   * The icon.
   */
  children: React.ReactNode
}

export const IconSlot = React.forwardRef<
  ElementRef<typeof StyledIconSlot>,
  IconSlotProps
>(({ children, ...restProps }, forwardRef) => {
  const { size } = useButtonContext()

  const formattedChildren = addPropsToChildren(
    children,
    child => isIconComponent(child as IconReactElement),
    {
      'data-icon-component': '',
      size: buttonIconSizes[size as ButtonSize],
    }
  )

  return (
    <StyledIconSlot {...restProps} ref={forwardRef} aria-hidden asChild>
      {formattedChildren}
    </StyledIconSlot>
  )
})
