import React, { useEffect } from 'react'
import type { ElementRef } from 'react'
import { useFormFieldContext } from '@mirohq/design-system-base-form'

import type { StyledDescriptionProps } from './description.styled'
import { StyledDescription } from './description.styled'

export interface DescriptionProps extends StyledDescriptionProps {
  /**
   * The content
   */
  children: React.ReactNode
}

export const Description = React.forwardRef<
  ElementRef<typeof StyledDescription>,
  DescriptionProps
>((props, forwardRef) => {
  const { descriptionId, setDescriptionId } = useFormFieldContext()

  const id = props.id ?? descriptionId
  useEffect(() => setDescriptionId(id), [setDescriptionId, id])

  return <StyledDescription {...props} id={id} ref={forwardRef} />
})
