import React from 'react'
import type { ElementRef } from 'react'
import { useLayoutEffect } from '@mirohq/design-system-use-layout-effect'

import { useItem } from '../hooks/use-item'
import type { StyledIllustrationSlotProps } from './illustration-slot.styled'
import { StyledIllustrationSlot } from './illustration-slot.styled'

export const IllustrationSlot = React.forwardRef<
  ElementRef<typeof StyledIllustrationSlot>,
  StyledIllustrationSlotProps
>((props, forwardRef) => {
  const { leftSlotMount, leftSlotDestroy } = useItem()

  useLayoutEffect(() => {
    leftSlotMount()
    return () => leftSlotDestroy()
  }, [leftSlotMount, leftSlotDestroy])

  return <StyledIllustrationSlot ref={forwardRef} {...props} />
})
