import React from 'react'
import type { ElementRef } from 'react'

import type { StyledSeparatorProps } from './separator.styled'
import { StyledSeparator } from './separator.styled'

export interface SeparatorProps extends StyledSeparatorProps {}

export const Separator = React.forwardRef<
  ElementRef<typeof StyledSeparator>,
  SeparatorProps
>((props, forwardRef) => <StyledSeparator {...props} ref={forwardRef} />)
