import React, { useState, useEffect } from 'react'
import type { ElementRef } from 'react'
import {
  mergeRefs,
  booleanishAttrValue,
  stringAttrValue,
} from '@mirohq/design-system-utils'
import { useFormFieldContext } from '@mirohq/design-system-base-form'
import type { FormElementProps } from '@mirohq/design-system-base-form'
import { useAriaDisabled } from '@mirohq/design-system-use-aria-disabled'
import { useHover } from '@react-aria/interactions'
import { mergeProps } from '@react-aria/utils'

import { StyledTextarea, StyledContainer } from './textarea.styled'
import type { StyledTextareaProps } from './textarea.styled'

export interface TextareaProps
  extends FormElementProps,
    Omit<StyledTextareaProps, keyof FormElementProps> {}

export const Textarea = React.forwardRef<
  ElementRef<typeof StyledTextarea>,
  TextareaProps
>(
  (
    {
      id,
      onFocus,
      onBlur,
      disabled,
      'aria-describedby': ariaDescribedBy,
      'aria-disabled': ariaDisabled,
      'aria-invalid': ariaInvalid,
      readOnly,
      required,
      placeholder,
      valid,
      css,
      // @ts-expect-error className required when extending the component with styled()
      className,
      ...restProps
    },
    forwardRef
  ) => {
    const textareaRef = React.useRef<HTMLTextAreaElement>(null)
    const {
      formElementId,
      valid: formFieldValid,
      ariaDescribedBy: formFieldContextDescribedBy,
      ariaInvalid: formFieldAriaInvalid,
      formElementRef,
      setRequired,
      setReadOnly,
      setDisabled,
      setAriaDisabled,
    } = useFormFieldContext<HTMLTextAreaElement>()

    const elementProps = useAriaDisabled({
      ...restProps,
      ariaDisabled,
    })

    useEffect(() => {
      setRequired?.(required)
      setReadOnly?.(readOnly)
      setDisabled?.(disabled)
      setAriaDisabled?.(ariaDisabled)
    }, [
      disabled,
      ariaDisabled,
      readOnly,
      required,
      setRequired,
      setReadOnly,
      setDisabled,
      setAriaDisabled,
    ])

    const [focused, setFocused] = useState(false)
    const { hoverProps, isHovered: hovered } = useHover({})

    const onFocusHandler = React.useCallback(
      (e: React.FocusEvent<HTMLTextAreaElement>) => {
        setFocused(true)
        onFocus?.(e)
      },
      [setFocused, onFocus]
    )
    const onBlurHandler = React.useCallback(
      (e: React.FocusEvent<HTMLTextAreaElement>) => {
        setFocused(false)
        onBlur?.(e)
      },
      [setFocused, onBlur]
    )

    const {
      overflow,
      resize,
      caretColor,
      lineHeight,
      letterSpacing,
      textIndent,
      whiteSpace,
      wordWrap,
      direction,
      tabSize,
      ...containerCss
    } = css ?? {}

    const textareaCss = {
      overflow,
      resize,
      caretColor,
      lineHeight,
      letterSpacing,
      textIndent,
      whiteSpace,
      wordWrap,
      direction,
      tabSize,
    }

    return (
      <StyledContainer
        css={containerCss}
        // @ts-expect-error className required when extending the component with styled()
        className={className}
      >
        <StyledTextarea
          data-focused={booleanishAttrValue(focused)}
          data-hovered={booleanishAttrValue(hovered)}
          {...mergeProps(elementProps, hoverProps)}
          id={id ?? formElementId}
          valid={valid ?? formFieldValid}
          readOnly={readOnly}
          aria-describedby={stringAttrValue(
            ariaDescribedBy,
            formFieldContextDescribedBy
          )}
          aria-invalid={ariaInvalid ?? formFieldAriaInvalid}
          disabled={disabled === true || undefined}
          required={required === true || undefined}
          placeholder={placeholder}
          onFocus={onFocusHandler}
          onBlur={onBlurHandler}
          data-form-element='textarea'
          ref={mergeRefs([textareaRef, formElementRef, forwardRef])}
          css={textareaCss}
        />
      </StyledContainer>
    )
  }
)
