  variants: {
    debug: {
      true: {
        color: '#06D834 !important',
      },
      false: {},
    },
    size: {
      normal: textStyles.size.normal,
      small: textStyles.size.small,
      mini: textStyles.size.mini,
    },
    weight: {
      normal: textStyles.weight.normal,
      bold: textStyles.weight.bold,
    },
  },
