import { useState, useRef, useEffect } from 'react'
import { mergeProps } from '@react-aria/utils'
import { useListeners } from '@mirohq/design-system-use-listeners'
import { useLogger } from '@mirohq/design-system-use-logger'
import { useWindowContext } from '@mirohq/design-system-use-window-context'

import { getEvents as getMouseEvents } from './mouse-events'
import { getEvents as getKeyboardEvents } from './keyboard-events'
import { getEvents as getTouchEvents } from './touch-events'
import { getEvents as getPointerEvents } from './pointer-events'
import type {
  Cancel,
  CreateEvent,
  DOMAttributes,
  InteractionProps,
  PointerType,
  PressHookProps,
  PressResult,
  PressState,
  TriggerPressEnd,
  TriggerPressStart,
  TriggerPressUp,
} from './types'
import { restoreTextSelection } from './utils/text-selection'
import { patchStopPropagation } from './utils/stop-propagation'

export const usePress = (props: PressHookProps): PressResult => {
  const {
    onPressStart,
    onPressEnd,
    onPressChange,
    onPress,
    onPressUp,
    disabled,
    pressed,
    preventFocusOnPress = false,
    shouldCancelOnPointerExit,
    allowTextSelectionOnPress = false,
    ...domProps
  } = props

  const { document: customDocument, window: customWindow } = useWindowContext()

  const { printLog } = useLogger('USE_PRESS_LOGGER', false)

  const propsRef = useRef<PressHookProps>()
  propsRef.current = {
    onPressStart,
    onPressEnd,
    onPressChange,
    onPress,
    onPressUp,
    disabled,
    preventFocusOnPress,
    shouldCancelOnPointerExit,
    allowTextSelectionOnPress,
  }

  const [isPressed, setIsPressed] = useState(false)
  const ref = useRef<PressState>({
    isPressed: false,
    ignoreEmulatedMouseEvents: false,
    target: null,
    pointerType: null,
    isOverTarget: false,
    activePointerId: null,
    didFirePressStart: false,
    ignoreClickAfterPress: false,
  })

  const { addListener, removeAllListeners } = useListeners()

  const state = ref.current

  const triggerPressStart: TriggerPressStart = (event, pointerType) => {
    printLog('triggerPressStart', { event, pointerType })

    const { onPressStart, onPressChange, disabled } =
      propsRef.current as PressHookProps

    if (disabled === true || state.didFirePressStart) {
      return
    }

    onPressStart?.({
      type: 'pressstart',
      pointerType,
      target: event.currentTarget as Element,
      shiftKey: event.shiftKey,
      metaKey: event.metaKey,
      ctrlKey: event.ctrlKey,
      altKey: event.altKey,
      originalEvent: 'event' in event ? event.event : event,
    })

    onPressChange?.(true)

    state.didFirePressStart = true
    setIsPressed(true)
  }

  const triggerPressUp: TriggerPressUp = (event, pointerType) => {
    printLog('triggerPressUp', { event, pointerType })

    const { onPressUp, disabled } = propsRef.current as PressHookProps

    if (disabled === true) {
      return
    }

    onPressUp?.({
      type: 'pressup',
      pointerType,
      target: event.currentTarget as Element,
      shiftKey: event.shiftKey,
      metaKey: event.metaKey,
      ctrlKey: event.ctrlKey,
      altKey: event.altKey,
      originalEvent: 'event' in event ? event.event : event,
    })
  }

  const triggerPressEnd: TriggerPressEnd = (event, pointerType, wasPressed) => {
    printLog('triggerPressEnd', { event, pointerType })

    const { onPressEnd, onPressChange, onPress, disabled } =
      propsRef.current as PressHookProps

    if (!state.didFirePressStart) {
      return
    }

    const originalEvent = 'event' in event ? event.event : event

    state.didFirePressStart = false

    const isPropagationStopped =
      'isPropagationStopped' in originalEvent &&
      originalEvent.isPropagationStopped()

    patchStopPropagation(originalEvent)

    if (!isPropagationStopped) {
      onPressEnd?.({
        type: 'pressend',
        pointerType,
        target: event.currentTarget as Element,
        shiftKey: event.shiftKey,
        metaKey: event.metaKey,
        ctrlKey: event.ctrlKey,
        altKey: event.altKey,
        originalEvent,
      })
    }

    onPressChange?.(false)

    setIsPressed(false)

    if (wasPressed && disabled !== true && !isPropagationStopped) {
      onPress?.({
        type: 'press',
        pointerType,
        target: event.currentTarget as Element,
        shiftKey: event.shiftKey,
        metaKey: event.metaKey,
        ctrlKey: event.ctrlKey,
        altKey: event.altKey,
        originalEvent,
      })
    }
  }

  const createEvent: CreateEvent = (target, e) => ({
    currentTarget: target,
    shiftKey: e.shiftKey,
    ctrlKey: e.ctrlKey,
    metaKey: e.metaKey,
    altKey: e.altKey,
    event: 'event' in e ? e.event : e,
  })

  const cancel: Cancel = (e, state) => {
    if (state.target === null) {
      return
    }

    if (state.isPressed) {
      if (state.isOverTarget) {
        triggerPressEnd(
          createEvent(state.target, e),
          state.pointerType as PointerType,
          false
        )
      }
      state.isPressed = false
      state.isOverTarget = false
      state.activePointerId = null
      state.pointerType = null
      removeAllListeners()
      if (!allowTextSelectionOnPress) {
        restoreTextSelection(state.target)
      }
    }
  }

  const interactionProps: InteractionProps = {
    triggerPressStart,
    triggerPressUp,
    triggerPressEnd,
    addListener,
    removeAllListeners,
    state,
    propsRef,
    cancel,
    createEvent,
    window: customWindow,
    document: customDocument,
  }

  const isPressedProp = (pressed ?? false) || isPressed

  let pressProps: DOMAttributes = {
    ...getMouseEvents(interactionProps),
    ...getKeyboardEvents(interactionProps),
    'data-pressed': isPressedProp ? '' : undefined,
  }

  if (typeof PointerEvent !== 'undefined') {
    pressProps = {
      ...pressProps,
      ...getPointerEvents(interactionProps),
    }
  } else {
    pressProps = {
      ...pressProps,
      ...getTouchEvents(interactionProps),
    }
  }

  // Remove user-select: none in case component unmounts immediately after pressStart
  useEffect(
    () => () => {
      if (!allowTextSelectionOnPress) {
        restoreTextSelection(ref?.current?.target ?? undefined)
      }
    },
    [allowTextSelectionOnPress]
  )

  return {
    isPressed: isPressedProp,
    pressProps: mergeProps(domProps, pressProps),
  }
}
