import type { ElementRef } from 'react'
import React from 'react'

import { StyledScrollbar } from './scrollbar.styled'
import type { StyledScrollbarProps } from './scrollbar.styled'

export interface ScrollbarProps extends StyledScrollbarProps {
  /**
   * Used to force mounting when more control is needed. Useful when controlling
   * animation with React animation libraries.
   */
  forceMount?: true

  /**
   * The orientation of the scrollbar.
   * @default 'vertical'
   */
  orientation?: 'horizontal' | 'vertical'
}

export const Scrollbar = React.forwardRef<
  ElementRef<typeof StyledScrollbar>,
  ScrollbarProps
>((props, forwardRef) => <StyledScrollbar {...props} ref={forwardRef} />)
