import { styled } from '@mirohq/design-system-stitches'
import type { StrictComponentProps } from '@mirohq/design-system-stitches'
import * as RadixDropdownMenu from '@radix-ui/react-dropdown-menu'
import { Primitive } from '@mirohq/design-system-primitive'
import { IconProhibit } from '@mirohq/design-system-icons'

import { itemDefaults } from '../styles/item'

export const StyledRadioContainer = styled(Primitive.div, {
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  width: '$4',
  height: '$4',
  boxSizing: 'border-box',
  border: '1px solid $border-neutrals',
  borderRadius: '$round',
})

export const StyledPill = styled(Primitive.div, {
  display: 'none',
  width: '$2',
  height: '$2',
  borderRadius: '$round',
})

export const StyledProhibited = styled(IconProhibit, {
  display: 'none',
  width: '$3 !important',
})

export const StyledRadioItem = styled(RadixDropdownMenu.RadioItem, {
  ...itemDefaults,
  [`&[aria-checked=true] ${StyledRadioContainer}`]: {
    color: '$icon-primary',
    borderColor: '$border-primary',

    [`& ${StyledPill}`]: {
      display: 'block',
      backgroundColor: '$background-primary-prominent-selected',
    },
  },

  [`&:is(:hover,[data-hovered]):not([aria-disabled="true"],[data-disabled]) ${StyledRadioContainer}`]:
    {
      [`& ${StyledPill}`]: {
        backgroundColor: '$background-primary-prominent-hover',
      },
    },

  [`
    &[aria-disabled="true"] ${StyledRadioContainer},
    &[data-disabled] ${StyledRadioContainer}
  `]: {
    color: '$icon-neutrals-disabled',
    borderColor: '$border-neutrals-disabled',

    [`& ${StyledPill}`]: {
      backgroundColor: '$icon-neutrals-disabled',
    },
  },
  '&[aria-checked=false]': {
    [`
      &[aria-disabled="true"] ${StyledProhibited},
      &[data-disabled] ${StyledProhibited}
    `]: {
      display: 'flex',
    },
  },
  [`${StyledRadioContainer}`]: {
    borderWidth: '2px',
    borderColor: '$border-neutrals-controls',
  },
})

export type StyledRadioItemProps = StrictComponentProps<typeof StyledRadioItem>
