import React from 'react'
import type { ElementRef } from 'react'
import { useLocalStorage } from '@mirohq/design-system-use-local-storage'

import { StyledText } from './text.styled'
import type { StyledTextProps } from './text.styled'

export interface TextProps extends Omit<StyledTextProps, 'debug'> {
  /**
   * The content.
   */
  children: React.ReactNode

  /**
   * Change the text font-size.
   */
  size?: StyledTextProps['size']

  /**
   * Change the text font-weight.
   */
  weight?: StyledTextProps['weight']
}

export const Text = React.forwardRef<ElementRef<typeof StyledText>, TextProps>(
  ({ size, weight, ...restProps }, forwardRef) => {
    const [debug] = useLocalStorage('DEBUG_TEXT', false)

    return (
      <StyledText
        {...restProps}
        ref={forwardRef}
        debug={debug}
        size={size}
        weight={weight}
      />
    )
  }
)
