import React, { useCallback } from 'react'
import { createPortal } from 'react-dom'

import { useDocumentFragment } from './use-document-fragment'

/**
 * Render content in DocumentFragment so items can send its text value to context,
 * otherwise we will have empty chips.
 * Content won't be visible in DOM.
 */
export const useInvisibleContent = (): ((
  children: React.ReactNode
) => React.ReactElement | null) => {
  const fragment = useDocumentFragment()

  return useCallback(
    (children: React.ReactNode): React.ReactElement | null =>
      fragment !== undefined
        ? createPortal(<div>{children}</div>, fragment)
        : null,
    [fragment]
  )
}
