import type { ElementRef } from 'react'
import React from 'react'
import { useLocalStorage } from '@mirohq/design-system-use-local-storage'

import * as Styled from './heading.styled'
import type { StyledHeadingProps } from './heading.styled'

type Level = 1 | 2 | 3 | 4 | 5
type Tags = 'h1' | 'h2' | 'h3' | 'h4' | 'h5'

export interface HeadingProps extends Omit<StyledHeadingProps, 'debug'> {
  /**
   * The sematic heading level used in the HTML markup.
   */
  level: Level

  /**
   * The way the sematic tag looks, it changes the styling of the heading.
   */
  styledAs?: Tags
}

export const Heading = React.forwardRef<ElementRef<Tags>, HeadingProps>(
  ({ level, styledAs, children, ...props }, forwardRef) => {
    const [debug] = useLocalStorage('DEBUG_TEXT', false)

    const HeadingLevel = Styled[`H${level}`]
    const styledAsLevel =
      styledAs !== undefined ? (+styledAs.replace('h', '') as Level) : styledAs
    return (
      <HeadingLevel
        {...props}
        debug={debug}
        level={styledAsLevel ?? level}
        ref={forwardRef}
      >
        {children}
      </HeadingLevel>
    )
  }
)
