import React from 'react'
import type { ElementRef, ReactNode } from 'react'

import type { StyledNoResultProps } from './no-result.styled'
import { StyledNoResult } from './no-result.styled'
import { useComboboxContext } from '../hooks/use-combobox-context'

export interface NoResultProps extends StyledNoResultProps {
  /**
   * The content.
   */
  children?: ReactNode
}

export const NoResult = React.forwardRef<
  ElementRef<typeof StyledNoResult>,
  NoResultProps
>((props, forwardRef) => {
  const { autoFilter, searchValue, filteredItems, itemsMap } =
    useComboboxContext()

  // when filtering is disabled or idle, just checking if there are any items provided
  const noActiveFiltering =
    !autoFilter || (autoFilter && searchValue.length === 0)

  const isVisible = noActiveFiltering
    ? itemsMap.size === 0
    : filteredItems.length === 0

  if (isVisible) {
    return <StyledNoResult {...props} ref={forwardRef} />
  }

  return null
})
