import React, {
  useState,
  useCallback,
  useEffect,
  useRef,
  createContext,
} from 'react'
import { useId } from '@mirohq/design-system-use-id'
import {
  Provider as RadixProvider,
  Root as RadixTooltip,
} from '@radix-ui/react-tooltip'
import { useBaseTooltipContext } from '@mirohq/design-system-base-tooltip'

import { Content } from './partials/content'
import { Trigger } from './partials/trigger'
import { Provider, useTooltipContext } from './partials/provider'
import { Portal } from './partials/portal'
import { Hotkey } from './partials/hotkey'
import { Metadata } from './partials/metadata'

export interface TooltipProps {
  /**
   * The open state of the tooltip when it is initially rendered. Use when you
   * do not need to control its open state.
   */
  defaultOpen?: boolean

  /**
   * The current state of the tooltip.
   */
  open?: boolean

  /**
   * Event handler called when the tooltip opens.
   */
  onOpen?: () => void

  /**
   * Event handler called when the tooltip closes.
   */
  onClose?: () => void

  /**
   * Overrides the duration given to the `Provider` to customize the open delay
   * for a specific tooltip.
   * @default 200
   */
  delayDuration?: number

  /**
   * How much time a user has to enter another trigger without incurring
   * a delay again.
   * @default 500
   */
  skipDelayDuration?: number

  /**
   * Clears the delayDuration and skipDelayDuration timeouts when the component
   * is unmounted. This flag is `true` by default when using NODE_ENV=test.
   * @default false
   */
  clearDelaysOnUnmount?: boolean

  /**
   * Closes the tooltip as soon as the pointer leaves the trigger making it
   * impossible to hover the content.
   */
  disableHoverableContent?: boolean

  /**
   * The content
   */
  children: React.ReactNode
}

let delayTimer: ReturnType<typeof setTimeout> | undefined
let skipDelayTimer: ReturnType<typeof setTimeout> | undefined
let shouldSkipDelay = false

export const DEFAULT_SKIP_DELAY_DURATION = 500
export const DEFAULT_DELAY_DURATION = 300

export const TooltipTriggerContext = createContext<{
  triggerRef: React.RefObject<HTMLButtonElement>
  id?: string
}>({
  triggerRef: { current: null },
})

export const Tooltip: ((props: TooltipProps) => React.ReactNode) & Partials = ({
  defaultOpen = false,
  open,
  onOpen,
  onClose,
  skipDelayDuration,
  delayDuration,
  disableHoverableContent,
  clearDelaysOnUnmount = process.env.NODE_ENV === 'test',
  children,
}) => {
  const context = useTooltipContext()
  const isMounted = useRef(false)
  const _skipDelayDuration =
    skipDelayDuration ??
    context.skipDelayDuration ??
    DEFAULT_SKIP_DELAY_DURATION

  const triggerId = useId()

  const { preventNextOpeningRef } = useBaseTooltipContext()

  const [openState, setOpenState] = useState(defaultOpen)
  const setDelayedOpen = useCallback(
    (newOpenState: boolean) => {
      clearTimeout(delayTimer)
      clearTimeout(skipDelayTimer)

      const triggerSelector = `[data-tooltip-trigger="${triggerId}"]`

      if (!newOpenState) {
        skipDelayTimer = setTimeout(() => {
          shouldSkipDelay = false
        }, _skipDelayDuration)
        setOpenState(false)
        onClose?.()
        return
      }

      if (
        shouldSkipDelay ||
        document.querySelector(`${triggerSelector}:focus`) != null
      ) {
        setOpenState(newOpenState)
        return
      }

      delayTimer = setTimeout(() => {
        // ignore it if the the user quickly hover the trigger and leave before the timeout
        // (unfortunately JSDOM seems not to support :hover selector)
        if (
          document.querySelector(`${triggerSelector}:hover`) == null &&
          process.env.NODE_ENV !== 'test'
        ) {
          return
        }

        /* when skipDelayDuration is 0 no skip delay should happen. */
        shouldSkipDelay = _skipDelayDuration !== 0

        if (isMounted.current) {
          setOpenState(true)
          onOpen?.()
        }
      }, delayDuration ?? context.delayDuration ?? DEFAULT_DELAY_DURATION)
    },
    [
      delayDuration,
      context.delayDuration,
      _skipDelayDuration,
      onClose,
      onOpen,
      triggerId,
    ]
  )

  const handleOpenChange = useCallback(
    (newState: boolean) => {
      if (newState && preventNextOpeningRef.current) {
        preventNextOpeningRef.current = false
        return
      }

      if (open == null) {
        setDelayedOpen(newState)
        return
      }

      newState ? onOpen?.() : onClose?.()
    },
    [open, onOpen, onClose, preventNextOpeningRef, setDelayedOpen]
  )

  useEffect(
    () => () => {
      if (clearDelaysOnUnmount) {
        clearTimeout(delayTimer)
        clearTimeout(skipDelayTimer)
        shouldSkipDelay = false
      }
    },
    [clearDelaysOnUnmount]
  )

  useEffect(() => {
    isMounted.current = true
    return () => {
      isMounted.current = false
    }
  }, [])

  const triggerRef = useRef<HTMLButtonElement>(null)

  return (
    <RadixProvider delayDuration={0} skipDelayDuration={0}>
      <TooltipTriggerContext.Provider
        value={{
          id: triggerId,
          triggerRef,
        }}
      >
        <RadixTooltip
          disableHoverableContent={disableHoverableContent}
          open={open ?? openState}
          delayDuration={0} // we control it manually
          onOpenChange={handleOpenChange}
        >
          {children}
        </RadixTooltip>
      </TooltipTriggerContext.Provider>
    </RadixProvider>
  )
}

// Partials
// -----------------------------------------------------------------------------

export interface Partials {
  Trigger: typeof Trigger
  Content: typeof Content
  Provider: typeof Provider
  Portal: typeof Portal
  Hotkey: typeof Hotkey
  Metadata: typeof Metadata
}

Tooltip.Trigger = Trigger
Tooltip.Content = Content
Tooltip.Provider = Provider
Tooltip.Portal = Portal
Tooltip.Hotkey = Hotkey
Tooltip.Metadata = Metadata
