import React, { useContext } from 'react'
import type { ElementRef, DOMAttributes } from 'react'
import { mergeRefs } from '@mirohq/design-system-utils'

import { StyledTrigger } from './trigger.styled'
import type { StyledTriggerProps } from './trigger.styled'
import { TooltipTriggerContext } from '../tooltip'

export interface TriggerProps extends StyledTriggerProps {
  /**
   * temporary the same as onClick, later will be added touch events support
   */
  onPress?: DOMAttributes<HTMLElement>['onClick']
}

export const Trigger = React.forwardRef<
  ElementRef<typeof StyledTrigger>,
  TriggerProps
>(({ onPress, onClick, ...restProps }, forwardRef) => {
  const { triggerRef, id } = useContext(TooltipTriggerContext)
  return (
    <StyledTrigger
      {...restProps}
      onClick={onPress ?? onClick}
      data-tooltip-trigger={id}
      ref={mergeRefs([triggerRef, forwardRef])}
    />
  )
})

Trigger.displayName = 'Tooltip.Trigger'
