import React from 'react'
import type { ElementRef, ReactNode, DOMAttributes } from 'react'
import { mergeProps } from '@react-aria/utils'
import { booleanify, removeEventProps } from '@mirohq/design-system-utils'
import { useHover } from '@react-aria/interactions'
import type { HoverEvents } from '@react-types/shared'
import { usePress } from '@mirohq/design-system-use-press'
import type { PressProps } from '@mirohq/design-system-use-press'

import type { StyledLinkProps } from './link.styled'
import { StyledLink } from './link.styled'

// DO NOT export it in the index
// Fix TS2742, option 3.1 in the comment bellow
// https://github.com/microsoft/TypeScript/issues/47663#issuecomment-1519138189
export type { HoverEvents } from '@react-types/shared'

export interface LinkProps
  extends Omit<StyledLinkProps, 'asButton'>,
    PressProps,
    HoverEvents {
  /**
   * The content.
   */
  children: ReactNode

  /**
   * Change the link appearance.
   * @default 'primary'
   */
  variant?: StyledLinkProps['variant']
}

export const Link = React.forwardRef<ElementRef<'a' | 'button'>, LinkProps>(
  (
    {
      variant = 'primary',
      disabled = false,
      'aria-disabled': ariaDisabled,
      asChild,
      rel = '',
      target,
      href,
      onClick,
      onPress,
      children,
      tabIndex,
      onHoverStart,
      onHoverEnd,
      onHoverChange,
      ...restProps
    },
    forwardRef
  ) => {
    const asButton = href === undefined && Boolean(onPress ?? onClick)
    let elementProps: DOMAttributes<any> = { ...restProps, onClick }

    if (disabled || booleanify(ariaDisabled)) {
      elementProps = removeEventProps(
        elementProps,
        disabled ? [] : ['onBlur', 'onFocus']
      )
    }

    const { pressProps } = usePress({
      preventFocusOnPress: true,
      disabled: disabled || booleanify(ariaDisabled),
      onPress,
      ...elementProps,
    })

    const { hoverProps, isHovered } = useHover({
      onHoverStart,
      onHoverEnd,
      onHoverChange,
    })

    elementProps = mergeProps(pressProps, hoverProps)

    let relProp: string | undefined = rel

    if (!asButton && target === '_blank') {
      if (!relProp.includes('noreferrer')) {
        relProp = `noreferrer ${relProp}`
      }

      if (!relProp.includes('noopener')) {
        relProp = `noopener ${relProp}`
      }

      relProp = relProp.trim()
    }

    if (relProp === '') {
      relProp = undefined
    }

    return (
      <StyledLink
        {...elementProps}
        data-hovered={isHovered ? '' : undefined}
        asChild={asButton || asChild}
        asButton={asButton}
        href={href}
        variant={variant}
        disabled={disabled}
        aria-disabled={
          disabled || booleanify(ariaDisabled) ? 'true' : undefined
        }
        tabIndex={
          tabIndex != null
            ? tabIndex
            : disabled
            ? -1
            : asChild === true || asButton
            ? 0
            : undefined
        }
        rel={relProp}
        target={target}
        // Link can be an <button> but TS will never know because it will only happen in runtime via asChild
        // @ts-expect-error
        ref={forwardRef}
      >
        {/*
          why not use a <button />?
          buttons break the read flow see the link
          https://codepen.io/ivanbanov/pen/WNVXMMx
        */}
        {asButton ? <span role='button'>{children}</span> : children}
      </StyledLink>
    )
  }
)
