import { useEffect, useRef } from 'react'
import { createTheme, stitchesCssRoot } from '@mirohq/design-system-stitches'
import { useTheme } from '@mirohq/design-system-use-theme'
import type { Theme } from '@mirohq/design-system-themes'

interface ThemesProp {
  [Key: string]: Partial<Theme>
}

export interface ThemeProviderProps<Themes extends ThemesProp = ThemesProp> {
  /**
   * The default theme to use if no theme is provided.
   */
  defaultTheme?: keyof Themes

  /**
   * Theme config for all available themes.
   */
  themes?: Themes

  /**
   * Applies theme className to the body.
   */
  root?: boolean

  /**
   * The children to render.
   */
  children?: React.ReactNode
}

export const ThemeProvider = <Themes extends ThemesProp = ThemesProp>({
  defaultTheme,
  themes,
  children,
  root = false,
}: ThemeProviderProps<Themes>): JSX.Element => {
  let themeToUse = ''
  const { theme } = useTheme()

  const defaultThemeExists =
    defaultTheme !== undefined && themes?.[defaultTheme] !== undefined

  const currentThemeExists = theme != null && themes?.[theme] !== undefined

  if (!currentThemeExists && defaultThemeExists) {
    themeToUse = defaultTheme.toString()
  }

  if (!defaultThemeExists && theme == null) {
    console.warn(
      'Default theme not found, falling back to the Miro Design System basics.'
    )
  }

  if (currentThemeExists) {
    themeToUse = theme
  }

  const classNameTheme =
    (currentThemeExists || defaultThemeExists) && themeToUse !== ''
      ? createTheme(themes[themeToUse]).className
      : ''

  const classThemeRef = useRef<string>(classNameTheme)

  useEffect(() => {
    if (classThemeRef.current !== classNameTheme) {
      if (root && classThemeRef.current !== '') {
        document.body.classList.remove(classThemeRef.current)
      }

      classThemeRef.current = classNameTheme
    }

    if (root) {
      if (classNameTheme !== '') {
        document.body.classList.add(classNameTheme)
      }

      document.body.dataset.theme = themeToUse
    }
  }, [classNameTheme, root, themeToUse])

  if (root) {
    return <>{children}</>
  }

  return (
    <div
      data-theme-provider
      data-theme={theme === '' ? undefined : theme}
      className={`${stitchesCssRoot.className} ${classNameTheme}`}
    >
      {children}
    </div>
  )
}
