import type { ForwardRefExoticComponent } from 'react'

import { StyledForm } from './form.styled'
import type { StyledFormProps } from './form.styled'
import { Label } from './partials/label'
import { Field } from './partials/field'
import { Helper } from './partials/helper'
import { Message } from './partials/message'
import { Description } from './partials/description'

export interface FormProps extends StyledFormProps {
  /**
   * The content
   */
  children: React.ReactNode
}

export const Form = StyledForm as any as ForwardRefExoticComponent<FormProps> &
  Partials

// Partials
// -----------------------------------------------------------------------------

export interface Partials {
  Label: typeof Label
  Field: typeof Field
  Helper: typeof Helper
  Message: typeof Message
  Description: typeof Description
}

Form.Label = Label
Form.Field = Field
Form.Helper = Helper
Form.Message = Message
Form.Description = Description
