import React from 'react'
import type { ElementRef } from 'react'
import type { Booleanish } from '@mirohq/design-system-types'
import { booleanify, booleanishAttrValue } from '@mirohq/design-system-utils'
import { Thumb } from '@mirohq/design-system-base-switch'

import type { StyledSwitchProps } from './switch.styled'
import { StyledSwitch } from './switch.styled'

export interface SwitchProps extends StyledSwitchProps {
  /**
   * Change the size of the switch.
   * @default 'medium'
   */
  size?: StyledSwitchProps['size']

  /**
   * The state of the switch.
   */
  checked?: boolean

  /**
   * The checked state of the checkbox when it is initially rendered. Use when
   * you do not need to control its checked state.
   */
  defaultChecked?: boolean

  /**
   * Event handler called when the checked state equals true.
   */
  onChecked?: () => void

  /**
   * Event handler called when the checked state equals false.
   */
  onUnchecked?: () => void

  /**
   * Prevents the user from interacting with the switch.
   * @default false
   */
  disabled?: boolean

  /**
   * Indicates that the user must check the switch.
   * @default false
   */
  required?: boolean

  /**
   * The name of the switch used in the form.
   */
  name?: string

  /**
   * The data when accessing the switch by its name in the form.
   */
  value?: string

  /**
   * When true, prevents the user from interacting with the switch but focus
   * is still possible.
   */
  'aria-disabled'?: Booleanish
}

export const Switch = React.forwardRef<
  ElementRef<typeof StyledSwitch>,
  SwitchProps
>(
  (
    {
      size = 'medium',
      disabled = false,
      required = false,
      'aria-disabled': ariaDisabled,
      onChecked,
      onUnchecked,
      onClick,
      ...restProps
    },
    forwardRef
  ) => (
    <StyledSwitch
      {...restProps}
      ref={forwardRef}
      data-form-element='switch'
      size={size}
      disabled={disabled}
      required={required}
      aria-disabled={ariaDisabled}
      aria-required={undefined}
      data-required={booleanishAttrValue(required)}
      onClick={e => {
        if (booleanify(ariaDisabled)) {
          e.preventDefault()
        } else {
          onClick?.(e)
        }
      }}
      onCheckedChange={value => {
        if (value) {
          onChecked?.()
        } else {
          onUnchecked?.()
        }
      }}
    >
      <Thumb />
    </StyledSwitch>
  )
)
