import React, { useEffect } from 'react'
import * as RadixPopover from '@radix-ui/react-popover'
import {
  BaseTooltipProvider,
  useBaseTooltipContext,
} from '@mirohq/design-system-base-tooltip'
import { usePrevious } from '@mirohq/design-system-use-previous'

import { Trigger } from './partials/trigger'
import { Content } from './partials/content'
import { Close } from './partials/close'
import { Portal } from './partials/portal'
import { PopoverProvider } from './use-popover-context'
import type { Variant } from './types'

export interface PopoverProps {
  /**
   * The current controlled state of the popover.
   */
  open?: boolean

  /**
   * The initial open state of the popover. Use when you don't need to control
   * its open state.
   * @default false
   */
  defaultOpen?: boolean

  /**
   * Event handler called when the popover opens.
   */
  onOpen?: () => void

  /**
   * Event handler called when the popover closes.
   */
  onClose?: () => void

  /**
   * Defines whether the interaction with outside elements will be enabled.
   */
  interactOutside?: boolean

  /**
   * Change the popover's appearance.
   * @default 'dark'
   */
  variant?: Variant

  /**
   * The content.
   */
  children: React.ReactNode
}

const Root = ({
  open,
  onOpen,
  onClose,
  ...restProps
}: PopoverProps): React.ReactNode => {
  const { ignoreNextTooltip } = useBaseTooltipContext()
  const prevOpen = usePrevious(open)

  useEffect(() => {
    if (prevOpen === true && open === false) {
      ignoreNextTooltip()
    }
  }, [ignoreNextTooltip, open, prevOpen])

  return (
    <RadixPopover.Root
      open={open}
      {...restProps}
      onOpenChange={value => {
        if (value) {
          onOpen?.()
        } else {
          if (open === undefined) {
            ignoreNextTooltip()
          }
          onClose?.()
        }
      }}
      modal={false}
    />
  )
}

export const Popover: ((props: PopoverProps) => React.ReactNode) & Partials = ({
  open,
  defaultOpen = false,
  onOpen,
  onClose,
  variant = 'dark',
  ...restProps
}) => (
  <PopoverProvider variant={variant}>
    <BaseTooltipProvider>
      <Root
        {...restProps}
        open={open}
        defaultOpen={defaultOpen}
        onOpen={onOpen}
        onClose={onClose}
      />
    </BaseTooltipProvider>
  </PopoverProvider>
)

// Partials
// -----------------------------------------------------------------------------

export interface Partials {
  Trigger: typeof Trigger
  Content: typeof Content
  Close: typeof Close
  Portal: typeof Portal
  Anchor: typeof RadixPopover.Anchor
}

Popover.Trigger = Trigger
Popover.Content = Content
Popover.Close = Close
Popover.Portal = Portal
Popover.Anchor = RadixPopover.Anchor
