import React, {
  createContext,
  useContext,
  useMemo,
  useRef,
  useState,
} from 'react'
import { useControllableState } from '@radix-ui/react-use-controllable-state'
import { useFormFieldContext } from '@mirohq/design-system-base-form'
import type { FormElementProps } from '@mirohq/design-system-base-form'

import type { Direction } from '../types'
import type { StyledBaseInputProps } from '../partials/input.styled'
import { searchQueryMatch } from '../utils'

interface ItemData {
  displayedText: string
  groupId: string | undefined
}

interface ComboboxProviderSharedProps extends FormElementProps {
  value?: string[] | string
  direction?: Direction
  autoFilter: boolean
}

interface ComboboxContextProps extends ComboboxProviderSharedProps {
  setOpenState: React.Dispatch<React.SetStateAction<boolean | undefined>>
  openState: boolean

  setValue: React.Dispatch<React.SetStateAction<string[] | string | undefined>>

  triggerRef: React.RefObject<HTMLInputElement>
  inputRef: React.RefObject<HTMLInputElement>
  contentRef: React.RefObject<HTMLDivElement>

  searchValue: string
  setSearchValue: React.Dispatch<React.SetStateAction<string | undefined>>

  placeholder?: string
  setPlaceholder: React.Dispatch<React.SetStateAction<string | undefined>>

  size: StyledBaseInputProps['size']
  setSize: React.Dispatch<React.SetStateAction<StyledBaseInputProps['size']>>

  /**
   * includes all visible and hidden items to render Chips and get filtered items
   */
  itemsMap: Map<string, ItemData>
  setItemsMap: React.Dispatch<React.SetStateAction<Map<string, ItemData>>>

  /**
   * @returns filtered items based on itemsMap
   * @returns empty array when searchValue is empty or auto filtering is disabled
   */
  filteredItems: ItemData[]
}

interface ComboboxProviderProps extends ComboboxProviderSharedProps {
  children?: React.ReactNode
  open?: boolean
  defaultOpen?: boolean
  onOpen?: () => void
  onClose?: () => void

  defaultValue?: string[] | string
  onValueChange?: (value: string[] | string) => void

  searchValue?: string
  onSearchValueChange?: (value: string) => void
}

const ComboboxContext = createContext<ComboboxContextProps>({} as any)

export const ComboboxProvider = ({
  children,
  open: openProp,
  defaultOpen,
  onOpen,
  onClose,
  valid,
  value: valueProp,
  defaultValue: defaultValueProp,
  onValueChange,
  searchValue: searchValueProp,
  onSearchValueChange,
  autoFilter,
  ...restProps
}: ComboboxProviderProps): JSX.Element => {
  const triggerRef = useRef<HTMLInputElement>(null)
  const inputRef = useRef<HTMLInputElement>(null)
  const contentRef = useRef<HTMLDivElement>(null)

  // todo MDS-1141: remove default false value
  const [openState = false, setOpenState] = useControllableState({
    prop: openProp,
    defaultProp: defaultOpen,
    onChange: state => {
      if (state) {
        onOpen?.()
      } else {
        onClose?.()
      }
    },
  })
  const [value, setValue] = useControllableState({
    prop: valueProp,
    defaultProp: defaultValueProp,
    onChange: onValueChange,
  })

  // todo MDS-1141: remove default false value
  const [searchValue = '', setSearchValue] = useControllableState({
    prop: searchValueProp,
    defaultProp: '',
    onChange: onSearchValueChange,
  })

  const [size, setSize] = useState<StyledBaseInputProps['size']>()
  const [placeholder, setPlaceholder] = useState<string | undefined>()

  const [itemsMap, setItemsMap] = useState<Map<string, ItemData>>(new Map())

  const { valid: formFieldValid } = useFormFieldContext()

  const filteredItems = useMemo(() => {
    if (searchValue.length > 0) {
      return Array.from(itemsMap.values()).filter(item =>
        searchQueryMatch(item.displayedText, searchValue)
      )
    }

    return []
  }, [itemsMap, searchValue])

  return (
    <ComboboxContext.Provider
      value={{
        ...restProps,
        valid: valid ?? formFieldValid,
        openState,
        setOpenState,
        value,
        setValue,
        triggerRef,
        inputRef,
        contentRef,
        autoFilter,
        searchValue,
        setSearchValue,
        itemsMap,
        setItemsMap,
        filteredItems,
        placeholder,
        setPlaceholder,
        size,
        setSize,
      }}
    >
      {children}
    </ComboboxContext.Provider>
  )
}

export const useComboboxContext = (): ComboboxContextProps =>
  useContext(ComboboxContext)
