import { useEffect, useRef, useState } from 'react'

const defaultOptions: IntersectionObserverInit = {
  rootMargin: '0',
  threshold: 1,
}

interface SpyScroll {
  activeId: string | null
  setActiveId: (id: string | null) => void
}

interface SpyScrollProps {
  selectors: string[]
  defaultActiveId?: string | null
  options?: IntersectionObserverInit
}

/**
 * Custom hook that tracks the scroll position and updates the active element based on the intersection with the viewport.
 *
 * @param {SpyScrollProps} props - The configuration options for the `useSpyScroll` hook.
 * @param {string[]} props.selectors - The CSS selectors of the elements to track.
 * @param {string | null} [props.defaultActiveId=null] - The default active element ID.
 * @param {IntersectionObserverInit} [props.options=defaultOptions] - The options for the IntersectionObserver.
 * @returns {SpyScroll} - An object containing the active element ID and a function to update it.
 *
 */
export const useSpyScroll = ({
  selectors,
  defaultActiveId = null,
  options = defaultOptions,
}: SpyScrollProps): SpyScroll => {
  const [activeId, setActiveId] = useState<string | null>(defaultActiveId)

  const observer = useRef<IntersectionObserver | null>(null)
  const selector = selectors.toString()

  useEffect(() => {
    const elements = selectors.map(selector => document.querySelector(selector))

    observer.current?.disconnect()
    observer.current = new IntersectionObserver(entries => {
      entries.forEach(entry => {
        if (entry?.isIntersecting) {
          setActiveId(entry.target.getAttribute('id'))
        }
      })
    }, options)
    elements.forEach(el => {
      if (el != null) observer.current?.observe(el)
    })
    return () => observer.current?.disconnect()
  }, [selector, options, selectors])

  return { activeId, setActiveId }
}
