import React from 'react'
import type { ElementRef, ForwardRefExoticComponent } from 'react'

import { Viewport } from './partials/viewport'
import { Scrollbar } from './partials/scrollbar'
import { Thumb } from './partials/thumb'
import { Corner } from './partials/corner'
import { StyledScrollArea } from './scroll-area.styled'
import type { StyledScrollAreaProps } from './scroll-area.styled'

export interface ScrollAreaProps extends StyledScrollAreaProps {
  children: React.ReactNode

  /**
   * Describes the nature of scrollbar visibility, similar to how the scrollbar
   * preferences in MacOS control visibility of native scrollbars. "auto" means
   * that scrollbars are visible when content is overflowing on the corresponding
   * orientation. "always" means that scrollbars are always visible regardless of
   * whether the content is overflowing. "scroll" means that scrollbars are
   * visible when the user is scrolling along its corresponding orientation.
   * "hover" when the user is scrolling along its corresponding orientation and
   * when the user is hovering over the scroll area.
   * @default hover
   */
  type?: 'auto' | 'always' | 'scroll' | 'hover'

  /**
   * The reading direction of the scroll area. If omitted, inherits globally
   * from DirectionProvider or assumes LTR (left-to-right) reading mode.
   * @default 'ltr'
   */
  dir?: 'ltr' | 'rtl'

  /**
   * If type is set to either "scroll" or "hover", this prop determines the
   * length of time, in milliseconds, before the scrollbars are hidden after the
   * user stops interacting with scrollbars.
   */
  scrollHideDelay?: number
}

export const ScrollArea = React.forwardRef<
  ElementRef<typeof StyledScrollArea>,
  ScrollAreaProps
>((props, forwardRef) => (
  <StyledScrollArea {...props} ref={forwardRef} />
)) as ForwardRefExoticComponent<ScrollAreaProps> & Partials

// Partials
// -----------------------------------------------------------------------------

export interface Partials {
  Viewport: typeof Viewport
  Scrollbar: typeof Scrollbar
  Thumb: typeof Thumb
  Corner: typeof Corner
}

ScrollArea.Viewport = Viewport
ScrollArea.Scrollbar = Scrollbar
ScrollArea.Thumb = Thumb
ScrollArea.Corner = Corner
