import React, { useState } from 'react'
import type { ElementRef, ForwardRefExoticComponent } from 'react'

import { StyledCallout } from './callout.styled'
import type { StyledCalloutProps } from './callout.styled'
import { Content } from './partials/content'
import { Description } from './partials/description'
import { IconSlot } from './partials/icon-slot'
import { Title } from './partials/title'
import { Actions } from './partials/actions'
import { CalloutProvider } from './hooks/use-callout-context'
import { DismissButton } from './partials/dismiss-button'

interface DismissibleProps {
  /**
   * Whether or not the component is dismissed (hidden)
   */
  dismissed?: boolean

  /**
   * Callback invoked once dismiss button is clicked
   */
  onDismiss?: () => void

  /**
   * aria-label for the dismiss button
   */
  dismissLabel: string
}

export type CalloutProps = (Omit<StyledCalloutProps, 'dismissible'> & {
  /**
   * Change the component style.
   */
  variant: StyledCalloutProps['variant']

  /**
   * Whether or not the component should display dismiss button
   * @default true
   */
  dismissible?: boolean
}) &
  (
    | DismissibleProps // uncontrolled
    | Required<DismissibleProps> // controlled
    | ({ dismissible?: false } & { [K in keyof DismissibleProps]?: never })
  )

export const Callout = React.forwardRef<
  ElementRef<typeof StyledCallout>,
  CalloutProps
>(
  (
    {
      dismissed,
      dismissible = true,
      onDismiss,
      dismissLabel,
      children,
      variant,
      ...props
    },
    forwardRef
  ) => {
    const [internalDismissed, setInternalDismissed] = useState<boolean>(
      dismissed ?? false
    )

    if (dismissed ?? internalDismissed) {
      return null
    }

    const handleDismissed = (): void => {
      setInternalDismissed(true)
      onDismiss?.()
    }

    return (
      <CalloutProvider variant={variant}>
        <StyledCallout
          {...props}
          ref={forwardRef}
          variant={variant}
          dismissible={dismissible}
          role='alert'
        >
          {children}
          {dismissible && onDismiss != null && dismissLabel?.length > 0 && (
            <DismissButton
              aria-label={dismissLabel}
              onPress={handleDismissed}
            />
          )}
        </StyledCallout>
      </CalloutProvider>
    )
  }
) as ForwardRefExoticComponent<CalloutProps> & Partials

export interface Partials {
  Content: typeof Content
  Description: typeof Description
  IconSlot: typeof IconSlot
  Title: typeof Title
  Actions: typeof Actions
}

Callout.Content = Content
Callout.Description = Description
Callout.IconSlot = IconSlot
Callout.Title = Title
Callout.Actions = Actions
