import type { ComponentPropsWithRef } from 'react'
import { Primitive } from '@mirohq/design-system-primitive'
import { styled } from '@mirohq/design-system-stitches'
import { focus } from '@mirohq/design-system-styles'

const TRANSITION_TIME = '250ms'

const activeSelector = '&:active, &[data-pressed]'
const hoverSelector = '&:hover, &[data-hovered]'

export const StyledLink = styled(Primitive.a, {
  textDecoration: 'none',
  border: 0,
  borderBottom: '1px solid currentColor',
  cursor: 'pointer',
  transition: `border ${TRANSITION_TIME}, color ${TRANSITION_TIME}`,
  paddingX: '2px',

  ...focus.css({
    boxShadow: '$focus',
    outline: 'none',
    borderRadius: '2px',
  }),

  variants: {
    variant: {
      primary: {
        color: '$text-primary',
        borderColor: '$border-primary',
        [hoverSelector]: {
          color: '$text-primary-hover',
          borderColor: '$border-primary-hover',
        },
        [activeSelector]: {
          color: '$text-primary-active',
          borderColor: '$border-primary-active',
        },
      },
      secondary: {
        color: '$text-neutrals-subtle',
        borderColor: '$border-neutrals-text-subtle',
        [hoverSelector]: {
          color: '$text-neutrals-subtle-hover',
          borderColor: '$border-neutrals-text-subtle-hover',
        },
        [activeSelector]: {
          color: '$text-neutrals-subtle-active',
          borderColor: '$border-neutrals-text-subtle-active',
        },
      },
    },
    asButton: {
      true: {
        background: 'none',
        paddingY: '$0',
        font: 'inherit',
      },
    },
  },
})

export type StyledLinkProps = ComponentPropsWithRef<typeof StyledLink>
