import React from 'react'
import { addPropsToChildren, booleanify } from '@mirohq/design-system-utils'
import { isIconComponent } from '@mirohq/design-system-base-icon'
import type { ElementRef } from 'react'
import { useLayoutEffect } from '@mirohq/design-system-use-layout-effect'

import { StyledIconSlot } from './icon-slot.styled'
import { useInputContext } from '../hooks/use-input-context'
import type { StyledIconSlotProps } from './icon-slot.styled'

export interface IconSlotProps extends Omit<StyledIconSlotProps, 'variant'> {}

export const IconSlot = React.forwardRef<
  ElementRef<typeof StyledIconSlot>,
  StyledIconSlotProps
>(({ children, ...restProps }, forwardRef) => {
  const {
    'aria-disabled': ariaDisabled,
    disabled,
    setHasIconSlot,
    variant,
  } = useInputContext()
  const formattedChildren = addPropsToChildren(children, isIconComponent, {
    size: 'small',
    weight: 'thin',
  })

  useLayoutEffect(() => {
    setHasIconSlot(true)
    return () => setHasIconSlot(false)
  }, [setHasIconSlot])

  return (
    <StyledIconSlot
      {...restProps}
      variant={variant}
      disabled={booleanify(disabled ?? ariaDisabled)}
      ref={forwardRef}
    >
      {formattedChildren}
    </StyledIconSlot>
  )
})

IconSlot.displayName = 'IconSlot'
