import { useState, useCallback } from 'react'

/**
 * Custom hook that returns a function to force update a component.
 * This hook can be used when you need to manually trigger a re-render of a component.
 */
export const useForceUpdate = (): (() => void) => {
  const [, setState] = useState(true)
  const forceUpdate = useCallback(() => {
    setState(x => !x)
  }, [])

  return forceUpdate
}
