import type { ElementRef } from 'react'
import React, { useContext, useEffect } from 'react'

import { StyledContent, StyledArrowIcon } from './content.styled'
import type { StyledContentProps } from './content.styled'
import type { PointerDownOutsideEvent, Side, Align } from '../types'
import { TooltipTriggerContext } from '../tooltip'

export interface ContentProps extends StyledContentProps {
  /**
   * The preferred alignment against the trigger. May change when collisions
   * occur.
   * @default 'center'
   */
  align?: Align

  /**
   * An offset in pixels from the "start" or "end" alignment options.
   * @default 0
   */
  alignOffset?: number

  /**
   * The distance in pixels from the trigger.
   * @default 5
   */
  sideOffset?: number

  /** The preferred side of the trigger to render against when open.
   * Will be reversed when collisions occur and avoidCollisions is enabled.
   * @default 'top'
   */
  side?: Side

  /**
   * When true, overrides the side and align preferences to prevent collisions
   * with window edges.
   * @default true
   */
  avoidCollisions?: boolean

  /**
   * The distance in pixels from window edges where collision detection should
   * occur.
   * @default 0
   */
  collisionPadding?: number

  /**
   * Used to force mounting when more control is needed. Useful when controlling
   * animation with React animation libraries. It inherits from Tooltip.Portal.
   */
  forceMount?: true

  /**
   * The element used as the collision boundary. By default this is the
   * viewport, though you can provide additional element(s) to be included in
   * this check.
   */
  collisionBoundary?: Element | null

  /**
   * The sticky behavior on the align axis. "partial" will keep the content in
   * the boundary as long as the trigger is at least partially in the boundary
   * whilst "always" will keep the content in the boundary regardless.
   * @default 'partial'
   */
  sticky?: 'partial' | 'always'

  /**
   * Whether to hide the content when the trigger becomes fully occluded.
   * @default true (false in test environment)
   */
  hideWhenDetached?: boolean

  /**
   * Event handler called when the escape key is down. It can be prevented by
   * calling event.preventDefault.
   */
  onEscapeKeyDown?: (event: KeyboardEvent) => void

  /**
   * Event handler called when a pointer event occurs outside the bounds of the
   * component. It can be prevented by calling event.preventDefault.
   */
  onPointerDownOutside?: (event: PointerDownOutsideEvent) => void
}

export const Content = React.forwardRef<
  ElementRef<typeof StyledContent>,
  ContentProps
>(
  (
    {
      align = 'center',
      alignOffset = 0,
      avoidCollisions = true,
      collisionPadding = 0,
      children,
      side = 'top',
      sideOffset = 5,
      sticky = 'partial',
      hideWhenDetached = process.env.NODE_ENV !== 'test',
      ...restProps
    },
    forwardRef
  ) => {
    const { triggerRef } = useContext(TooltipTriggerContext)

    const removeDuplicateAriaDescription = (element: Element): void => {
      const ariaDescribedBy: string | null =
        element.getAttribute('aria-describedby')

      if (ariaDescribedBy !== null) {
        const ariaDescription: string | null | undefined =
          document.getElementById(ariaDescribedBy)?.textContent

        const areAriaLabelAndAriaDescriptionEqual =
          ariaDescription?.trim() ===
          element?.getAttribute('aria-label')?.trim()

        if (areAriaLabelAndAriaDescriptionEqual) {
          element.removeAttribute('aria-describedby')
        }
      }
    }
    useEffect((): (() => void) => {
      const element = triggerRef.current

      if (element != null) {
        const handleMutation = (mutationsList: MutationRecord[]): void => {
          for (const mutation of mutationsList) {
            if (
              mutation.type === 'attributes' &&
              mutation.attributeName === 'aria-describedby'
            ) {
              removeDuplicateAriaDescription(element)
            }
          }
        }

        const observer = new MutationObserver(handleMutation)

        observer.observe(element, {
          attributes: true,
        })

        return () => {
          observer.disconnect()
        }
      }

      return () => {}
    }, [triggerRef])

    useEffect(() => {
      if (triggerRef.current === null) return
      removeDuplicateAriaDescription(triggerRef.current)
      // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    return (
      <StyledContent
        {...restProps}
        ref={forwardRef}
        align={align}
        alignOffset={alignOffset}
        avoidCollisions={avoidCollisions}
        collisionPadding={collisionPadding}
        hideWhenDetached={hideWhenDetached}
        side={side}
        sideOffset={sideOffset}
        sticky={sticky}
      >
        {children}
        <StyledArrowIcon aria-hidden='true' />
      </StyledContent>
    )
  }
)

Content.displayName = 'Tooltip.Content'
