import type { ElementRef, ForwardRefExoticComponent } from 'react'
import React from 'react'
import type { Booleanish } from '@mirohq/design-system-types'
import { booleanify } from '@mirohq/design-system-utils'

import { Range } from './partials/range'
import { Thumb } from './partials/thumb'
import type { StyledSliderProps } from './slider.styled'
import { StyledSlider } from './slider.styled'
import { SliderProvider } from './hooks/use-slider-context'
import { Value } from './partials/value'
import { Input } from './partials/input'
import { Track } from './partials/track'

export interface SliderProps extends StyledSliderProps {
  /**
   * The maximum value for the range.
   * @default 100
   */
  max?: number

  /**
   * The children components of the slider.
   */
  children: React.ReactNode

  /**
   * The controlled value of the slider. Must be used in conjunction with onValueChange.
   */
  value?: number

  /**
   * The minimum value for the range.
   * @default 0
   */
  min?: number

  /**
   * The stepping interval.
   */
  step?: number

  /**
   * When true, prevents the user from interacting with the slider.
   */
  disabled?: boolean

  /**
   * Event handler called when the value changes at the end of an interaction.
   * Useful when you only need to capture a final value e.g. to update a backend service.
   */
  onValueCommit?: (value: number) => void

  /**
   * Event handler called when the value changes.
   */
  onValueChange?: (value: number) => void

  /**
   * When true, prevents the user from interacting with the slider but focus
   * is still possible.
   */
  'aria-disabled'?: Booleanish

  /**
   * The name of the slider. Submitted with its owning form as part of a
   * name/value pair.
   */
  name?: string
}

export const Slider = React.forwardRef<
  ElementRef<typeof StyledSlider>,
  SliderProps
>(
  (
    {
      step = 1,
      disabled = false,
      'aria-disabled': ariaDisabled = false,
      min = 0,
      max = 100,
      value,
      onValueCommit,
      onValueChange,
      children,
      name,
      ...restProps
    },
    forwardRef
  ) => (
    <SliderProvider
      value={value}
      onValueCommit={onValueCommit}
      onValueChange={onValueChange}
      step={step}
      min={min}
      max={max}
      disabled={disabled}
      ariaDisabled={booleanify(ariaDisabled)}
      name={name}
    >
      <StyledSlider
        {...restProps}
        aria-disabled={ariaDisabled}
        ref={forwardRef}
      >
        {children}
      </StyledSlider>
    </SliderProvider>
  )
) as ForwardRefExoticComponent<SliderProps> & Partials

// Partials
// -----------------------------------------------------------------------------

export interface Partials {
  Track: typeof Track
  Range: typeof Range
  Thumb: typeof Thumb
  Value: typeof Value
  Input: typeof Input
}

Slider.Track = Track
Slider.Range = Range
Slider.Thumb = Thumb
Slider.Value = Value
Slider.Input = Input
