import { focusWithoutScrolling } from '@react-aria/utils'

import type { FocusableEvent } from '../types'

// In cases that the user wants to avoid the focus after pressing the button, it
// is needed to make sure that the button is actually available and focus is properly
// removed from the latest focused element
export const handleFocus = ({
  event,
  disabled,
  preventFocusOnPress,
  shouldPreventDefault = () => false,
}: {
  event: FocusableEvent
  disabled: boolean
  preventFocusOnPress: boolean | 'auto'
  shouldPreventDefault?: (target: Element) => boolean
}): void => {
  if (disabled) {
    event?.preventDefault()
    return
  }
  if (preventFocusOnPress === 'auto') {
    return
  }

  if (shouldPreventDefault(event.currentTarget as Element)) {
    event?.preventDefault()
  }

  if (!preventFocusOnPress) {
    focusWithoutScrolling(event.currentTarget)
  }
}
