import React, { useMemo } from 'react'
import type { ElementRef, ReactNode } from 'react'
import { ComboboxItemCheck as AriakitComboboxItemCheck } from '@ariakit/react'
import { mergeProps } from '@react-aria/utils'
import { useAriaDisabled } from '@mirohq/design-system-use-aria-disabled'
import { IconCheckMark } from '@mirohq/design-system-icons'
import { booleanify } from '@mirohq/design-system-utils'
import { useLayoutEffect } from '@mirohq/design-system-use-layout-effect'
import { StyledItemCheck } from '@mirohq/design-system-base-select'

import { StyledItem } from './item.styled'
import type { StyledItemProps } from './item.styled'
import { useComboboxContext } from '../hooks/use-combobox-context'
import { useGroupContext } from '../hooks/use-group-context'
import { searchQueryMatch } from '../utils'

export interface ItemProps extends StyledItemProps {
  /**
   * The value given as data when submitted with a name.
   */
  value: string

  /**
   * When true, prevents the user from interacting with the item.
   * @default false
   */
  disabled?: boolean

  /**
   * Optional text used for typeahead purposes. By default the typeahead
   * behavior will use the Combobox's item text. Use this when the content is
   * complex, or you have non-textual content inside.
   */
  textValue?: string

  /**
   * Item's content.
   */
  children?: ReactNode
}

export const Item = React.forwardRef<ElementRef<typeof StyledItem>, ItemProps>(
  (
    { disabled = false, value, textValue, children, ...restProps },
    forwardRef
  ) => {
    const { 'aria-disabled': ariaDisabled, ...restAriaDisabledProps } =
      useAriaDisabled(restProps, { allowArrows: true })
    const {
      searchValue,
      autoFilter,
      setItemsMap,
      triggerRef,
      inputRef,
      value: comboboxValue = [],
    } = useComboboxContext()

    const { groupId } = useGroupContext()

    const displayedText = useMemo((): string => {
      if (textValue !== undefined) {
        return textValue
      }

      return typeof children === 'string' ? children : ''
    }, [textValue, children])

    useLayoutEffect(() => {
      setItemsMap(
        prevState => new Map(prevState.set(value, { displayedText, groupId }))
      )

      return () => {
        setItemsMap(prevState => {
          prevState.delete(value)
          return new Map(prevState)
        })
      }
    }, [setItemsMap, groupId, value, displayedText])

    if (
      autoFilter &&
      searchValue.length > 0 &&
      !searchQueryMatch(displayedText, searchValue)
    ) {
      return null
    }

    const scrollIntoView = (event: React.MouseEvent<HTMLDivElement>): void => {
      if (
        inputRef?.current?.parentElement != null &&
        triggerRef?.current != null
      ) {
        inputRef.current.parentElement.scrollTo({
          top: triggerRef.current.scrollHeight,
        })
      }

      if (restProps.onClick !== undefined) {
        restProps.onClick(event)
      }
    }

    // todo MDS-1142 find a better way to fix blinking.
    // We need to use custom state because Ariakit changes its state internally on click even for controlled Combobox
    // and it case `aria-selected` value and checkmark to blink.
    const isSelected = Array.isArray(comboboxValue)
      ? comboboxValue.includes(value)
      : comboboxValue === value

    return (
      <StyledItem
        {...mergeProps(restProps, restAriaDisabledProps)}
        focusable
        hideOnClick={false}
        accessibleWhenDisabled={booleanify(ariaDisabled)}
        disabled={booleanify(ariaDisabled) || disabled}
        ref={forwardRef}
        value={value}
        onClick={scrollIntoView}
        aria-selected={isSelected}
      >
        <AriakitComboboxItemCheck
          checked={isSelected}
          render={({ style, ...props }) => (
            // AriakitComboboxItemCheck adds its owm inline styles which we want to omit here
            <StyledItemCheck {...props} />
          )}
        >
          <IconCheckMark
            size='small'
            data-testid={
              process.env.NODE_ENV === 'test'
                ? 'combobox-item-check'
                : undefined
            }
          />
        </AriakitComboboxItemCheck>
        {children}
      </StyledItem>
    )
  }
)
