import { createContext, useContext, useState } from 'react'
import type { FormElementProps } from '@mirohq/design-system-base-form'

import type { StyledTriggerProps } from '../partials/trigger.styled'
import type { Direction } from '../types'

interface SelectProps extends FormElementProps {
  defaultOpen?: boolean
  direction?: Direction
  open?: boolean
  valid?: boolean
}

interface SelectContextProps extends SelectProps {
  setOpenState: React.Dispatch<React.SetStateAction<boolean | undefined>>
  openState?: boolean | undefined
  placeholder?: React.ReactNode
  setPlaceholder: React.Dispatch<React.SetStateAction<React.ReactNode>>
  size: StyledTriggerProps['size']
  setSize: React.Dispatch<React.SetStateAction<StyledTriggerProps['size']>>
  loading?: boolean
  setLoading: React.Dispatch<React.SetStateAction<boolean | undefined>>
}

export interface SelectProviderProps extends SelectProps {
  children: React.ReactNode
}

const SelectContext = createContext<SelectContextProps>({} as any)

export const SelectProvider = ({
  children,
  defaultOpen,
  open,
  valid,
  ...restProps
}: SelectProviderProps): JSX.Element => {
  const [openState, setOpenState] = useState(defaultOpen)
  const [loading, setLoading] = useState<boolean | undefined>(false)
  const [placeholder, setPlaceholder] = useState<React.ReactNode>()
  const [size, setSize] = useState<StyledTriggerProps['size']>()

  return (
    <SelectContext.Provider
      value={{
        ...restProps,
        valid,
        open,
        openState,
        setOpenState,
        placeholder,
        setPlaceholder,
        size,
        setSize,
        loading,
        setLoading,
      }}
    >
      {children}
    </SelectContext.Provider>
  )
}

export const useSelectContext = (): SelectContextProps =>
  useContext(SelectContext)
