import { booleanify } from '@mirohq/design-system-utils'
import { useAriaDisabled as DS_useAriaDisabled } from '@mirohq/design-system-use-aria-disabled'

type UseAriaDisabled = typeof DS_useAriaDisabled
type UseAriaDisabledParameters = Parameters<UseAriaDisabled>

type DropdownAriaDisabledProps<T> = Omit<T, 'onSelect'> & {
  onSelect?: (event: Event) => void
}

type UseAriaDisabledProps = DropdownAriaDisabledProps<
  UseAriaDisabledParameters[0]
>

type UseAriaDisabledExceptions = UseAriaDisabledParameters[1] & {
  closeOnSelect?: boolean | undefined
}

export const useAriaDisabled = <T extends UseAriaDisabledProps>(
  { onSelect, ...restProps }: T,
  { exceptions, closeOnSelect = true }: UseAriaDisabledExceptions = {}
): T => {
  const elementProps = DS_useAriaDisabled(restProps, {
    ...exceptions,
    allowArrows: true,
  })
  const { 'aria-disabled': ariaDisabled, onPointerMove, onClick } = elementProps

  // eslint-disable-next-line @typescript-eslint/consistent-type-assertions
  return {
    ...elementProps,

    // these events need to be manually included
    // bacause useAriaDisabled only removes the events from the props.
    // Radix uses these events to handle the dropdown menu so we need to prevent
    // it from being called when the item is aria-disabled.
    onSelect: e => {
      if (booleanify(ariaDisabled)) {
        e.preventDefault()
        return
      }

      if (!closeOnSelect) {
        e.preventDefault()
      }

      onSelect?.(e)
    },
    onPointerMove: e => {
      if (booleanify(ariaDisabled)) {
        e.preventDefault()
        return
      }

      onPointerMove?.(e)
    },
    onClick: e => {
      if (booleanify(ariaDisabled)) {
        e.preventDefault()
        return
      }

      onClick?.(e)
    },
  } as T
}
