import React from 'react'
import type { ElementRef } from 'react'

import {
  StyledPill,
  StyledProhibited,
  StyledRadioContainer,
  StyledRadioItem,
} from './radio-item.styled'
import type { StyledRadioItemProps } from './radio-item.styled'
import { RightSlot } from './right-slot'
import { useAriaDisabled } from '../hooks/use-aria-disabled'
import { ItemProvider } from '../hooks/use-item'

export interface RadioItemProps extends StyledRadioItemProps {
  /**
   * The unique value of the item.
   */
  value: string

  /**
   * When true, prevents the user from interacting with the item.
   * @default false
   */
  disabled?: boolean

  /**
   * Event handler called when the user selects an item (via mouse or keyboard).
   */
  onSelect?: (event: Event) => void

  /**
   * Closes the dropdown when the user selects an item.
   * @default false
   */
  closeOnSelect?: boolean

  /**
   * Optional text used for typeahead purposes. By default the typeahead
   * behavior will use the .textContent of the item.
   * Use this when the content is complex, or you have non-textual content
   * inside.
   */
  textValue?: string
}

export const RadioItem = React.forwardRef<
  ElementRef<typeof StyledRadioItem>,
  RadioItemProps
>(({ disabled, children, closeOnSelect = false, ...restProps }, forwardRef) => {
  const elementProps = useAriaDisabled(restProps, {
    closeOnSelect,
  })

  return (
    <ItemProvider>
      <StyledRadioItem {...elementProps} disabled={disabled} ref={forwardRef}>
        {children}
        <RightSlot>
          <StyledRadioContainer>
            <StyledPill />
            <StyledProhibited />
          </StyledRadioContainer>
        </RightSlot>
      </StyledRadioItem>
    </ItemProvider>
  )
})
