import React from 'react'
import type { ElementRef } from 'react'
import type { theme } from '@mirohq/design-system-stitches'

import { StyledFlex } from './flex.styled'
import type { StyledFlexProps } from './flex.styled'

export interface FlexProps extends StyledFlexProps {
  /**
   * Defines how elements are aligned along the cross axis.
   **/
  align?: StyledFlexProps['align']

  /**
   * Defines the direction of flex items that are placed in the flex container.
   */
  direction?: StyledFlexProps['direction']

  /**
   * Explicitly controls the space between flex items.
   */
  gap?: keyof typeof theme.space

  /**
   * Defines how elements are aligned along the main axis. Uses the space token
   **/
  justify?: StyledFlexProps['justify']

  /**
   * Lets the items wrap onto multiple lines if needed.
   */
  wrap?: StyledFlexProps['wrap']
}

export const Flex = React.forwardRef<ElementRef<typeof StyledFlex>, FlexProps>(
  (props, forwardRef) => <StyledFlex {...props} ref={forwardRef} />
)
