import React, { useState, useEffect, useRef } from 'react'
import { Trigger as RadixTrigger } from '@radix-ui/react-select'
import { useHover } from '@react-aria/interactions'
import type { HoverEvents } from '@react-types/shared'
import type { ElementRef, PointerEvent } from 'react'
import {
  booleanify,
  booleanishAttrValue,
  stringAttrValue,
  mergeRefs,
  handleVirtualClick,
} from '@mirohq/design-system-utils'
import { useFormFieldContext } from '@mirohq/design-system-base-form'
import { mergeProps } from '@react-aria/utils'
import { Spinner } from '@mirohq/design-system-spinner'

import { StyledTrigger } from './trigger.styled'
import { useSelectContext } from '../hooks/use-select-context'
import { StyledSelectIcon, StyledIconChevronDown } from './select-icon.styled'
import type { StyledTriggerProps } from './trigger.styled'

export interface TriggerProps extends StyledTriggerProps, HoverEvents {
  /**
   * The content.
   */
  children: React.ReactNode

  /**
   * The size of the trigger.
   * @default 'large'
   */
  size?: StyledTriggerProps['size']

  /**
   * The aria label of the trigger.
   */
  'aria-label'?: string
}

export const Trigger = React.forwardRef<
  ElementRef<typeof StyledTrigger>,
  TriggerProps
>(
  (
    {
      id,
      onKeyDown,
      onPointerDown,
      onFocus,
      onBlur,
      children,
      size = 'large',
      asChild = false,
      'aria-describedby': ariaDescribedBy,
      'aria-invalid': ariaInvalid,
      onHoverChange,
      onHoverStart,
      onHoverEnd,
      onClick,
      'aria-label': ariaLabel,
      ...restProps
    },
    forwardRef
  ) => {
    const ref = useRef<HTMLButtonElement>(null)
    const {
      'aria-disabled': ariaDisabled,
      disabled,
      valid: selectValid,
      setSize,
      open,
      openState,
      loading = false,
      placeholder,
    } = useSelectContext()
    const [focused, setFocused] = useState(false)
    const isOpen = open ?? openState ?? false

    const {
      formElementId,
      ariaDescribedBy: formFieldContextDescribedBy,
      ariaInvalid: formFieldAriaInvalid,
      valid: formFieldValid,
    } = useFormFieldContext()

    // validity
    const valid = formFieldValid ?? selectValid

    const { hoverProps, isHovered: hovered } = useHover({
      onHoverStart,
      onHoverEnd,
      onHoverChange: isHovering => {
        onHoverChange?.(isHovering)
      },
    })

    let triggerlabel = ''
    if (typeof placeholder === 'string') {
      triggerlabel = placeholder
    }

    const commonProps = {
      ...mergeProps(restProps, hoverProps),
      'aria-disabled': loading || ariaDisabled,
      onPointerDown: (e: PointerEvent<HTMLButtonElement>) => {
        if (booleanify(loading) || booleanify(ariaDisabled)) {
          e.preventDefault()
        } else {
          onPointerDown?.(e)
        }
      },
      onKeyDown: (e: React.KeyboardEvent<HTMLButtonElement>) => {
        if (
          (booleanify(loading) || booleanify(ariaDisabled)) &&
          e.code !== 'Tab' &&
          e.code !== 'ArrowUp' &&
          e.code !== 'ArrowDown'
        ) {
          e.preventDefault()
        } else {
          onKeyDown?.(e)
        }
      },
      onFocus: (e: React.FocusEvent<HTMLButtonElement>) => {
        onFocus?.(e)
        setFocused(true)
      },
      onBlur: (e: React.FocusEvent<HTMLButtonElement>) => {
        onBlur?.(e)
        if (!isOpen) {
          setFocused(false)
        }
      },
      'data-invalid': booleanishAttrValue(valid === false),
      'data-valid': booleanishAttrValue(valid),
      'data-focused': booleanishAttrValue(focused),
      'data-hovered': booleanishAttrValue(hovered),
      'aria-invalid': ariaInvalid ?? formFieldAriaInvalid,
      'aria-describedby': stringAttrValue(
        ariaDescribedBy,
        formFieldContextDescribedBy
      ),
      'aria-label': ariaLabel ?? triggerlabel,
      id: id ?? formElementId,
      onClick: (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        handleVirtualClick(e, ref)
        onClick?.(e)
      },
      ref: mergeRefs([ref, forwardRef]),
    }

    useEffect(() => {
      setSize(size)
      if (booleanify(disabled)) setFocused(false)
    }, [size, setSize, ariaDisabled, disabled])

    if (asChild) {
      return (
        <RadixTrigger {...commonProps} asChild>
          {children}
        </RadixTrigger>
      )
    }

    const SelectIcon = StyledIconChevronDown

    return (
      <StyledTrigger {...commonProps} size={size}>
        {loading ? (
          <Spinner css={{ flex: 1, placeSelf: 'center' }} />
        ) : (
          children
        )}
        <StyledSelectIcon asChild>
          <SelectIcon size='small' />
        </StyledSelectIcon>
      </StyledTrigger>
    )
  }
)
