import React, { useMemo, useEffect } from 'react'
import type { ElementRef } from 'react'
import { useFormFieldContext } from '@mirohq/design-system-base-form'

import { StyledAsterisk, StyledLabel } from './label.styled'
import type { StyledLabelProps } from './label.styled'

export interface LabelProps extends StyledLabelProps {
  /**
   * The content
   */
  children: React.ReactNode

  /**
   * Whether the label should float above the input
   * @default true
   * @deprecated will be removed with Design Language release
   */

  /**
   * To hide the label visually and still keep it accessible for screen readers and other assistive technology
   * @default false
   */
  visuallyHidden?: boolean
}

export const Label = React.forwardRef<
  ElementRef<typeof StyledLabel>,
  LabelProps
>(({ visuallyHidden = false, children, ...restProps }, forwardRef) => {
  const {
    setLabel,
    setIsHiddenLabel,
    formElementId,
    required = false,
  } = useFormFieldContext()

  const label = useMemo(
    () => (
      <>
        {children}
        {required && <StyledAsterisk aria-hidden='true'>*</StyledAsterisk>}
      </>
    ),
    [children, required]
  )

  useEffect(() => {
    setLabel(label)
  }, [setLabel, label])

  useEffect(
    () => setIsHiddenLabel(visuallyHidden),
    [setIsHiddenLabel, visuallyHidden]
  )

  return (
    <StyledLabel
      htmlFor={formElementId}
      {...restProps}
      ref={forwardRef}
      visuallyHidden={visuallyHidden}
    >
      {label}
    </StyledLabel>
  )
})
