import React from 'react'
import type { ElementRef, AnchorHTMLAttributes } from 'react'

import type { BaseItemProps } from './base-item'
import { StyledLink } from './link.styled'

type ItemPropsWithAnchor = Omit<BaseItemProps, 'ref'> &
  AnchorHTMLAttributes<typeof StyledLink>
export interface LinkProps extends ItemPropsWithAnchor {}

export const Link = React.forwardRef<ElementRef<'a'>, LinkProps>(
  ({ children, href, ...restProps }, forwardRef) => (
    <StyledLink {...restProps} asChild>
      <a href={href} ref={forwardRef}>
        {children}
      </a>
    </StyledLink>
  )
)
