import { focus } from '@mirohq/design-system-styles'
import { Hotkey } from '@mirohq/design-system-base-hotkey'

import { StyledItemDescription } from '../partials/item-description'
import { StyledIllustrationSlot } from '../partials/illustration-slot.styled'

export const itemDefaults = {
  all: 'unset',
  boxSizing: 'border-box',
  fontSize: 14,
  lineHeight: '20px',
  color: '$text-neutrals',
  borderRadius: '$50',
  display: 'grid',
  gridTemplateColumns:
    'auto 1fr minmax(min-content, var(--right-slot-max-width))',
  gridTemplateRows: 'auto minmax(0, 1fr)',
  gridTemplateAreas: `'left-slot item-text right-slot'
    'left-slot item-description right-slot'`,
  alignItems: 'start',
  padding: '10px $100',
  position: 'relative',
  userSelect: 'none',
  cursor: 'pointer',

  '&[data-no-left-slot]': {
    gridTemplateColumns: '1fr minmax(auto, var(--right-slot-max-width))',
    gridTemplateAreas: `'item-text right-slot'
    'item-description right-slot'`,
  },

  ...focus.css({
    boxShadow: '$focus',
    outline: '1px solid transparent',
  }),

  '&:disabled, &[aria-disabled="true"], &[data-disabled]': {
    cursor: 'default',

    [`&, & ${StyledItemDescription}, & ${Hotkey}`]: {
      color: '$text-neutrals-disabled',
    },
    [`& ${StyledIllustrationSlot}`]: {
      filter: 'grayscale(1)',
    },
  },

  '&:disabled, &[data-disabled]': {
    pointerEvents: 'none',
  },

  '&:is(:hover,[data-hovered]):not([aria-disabled="true"],[data-disabled])': {
    background: '$background-neutrals-hover',
    color: '$text-neutrals-hover',
    '&:not([aria-disabled="true"])': {
      boxShadow: 'none',
    },
  },

  '&:active:not([aria-disabled="true"])': {
    background: '$background-primary-subtle-active',
    boxShadow: 'none',
    color: '$text-primary-active',
  },

  '&[tabindex="0"]': {
    zIndex: '1',
  },
}
