import { styled } from '@mirohq/design-system-stitches'
import type { StrictComponentProps } from '@mirohq/design-system-stitches'
import * as RadixSwitch from '@radix-ui/react-switch'
import { focus } from '@mirohq/design-system-styles'
import { styles } from '@mirohq/design-system-base-switch'

export const StyledSwitch = styled(RadixSwitch.Root, {
  ...styles.default,
  cursor: 'pointer',

  ...focus.css({
    boxShadow: '$focus',
  }),

  '&[aria-checked=true]': styles.checked,

  '&:hover:not([disabled]):not([aria-disabled=true])': {
    ...styles.hovered,

    '&[aria-checked=true]': styles.checkedHovered,
  },
  '&[disabled], &[aria-disabled=true]': styles.disabled,

  variants: {
    size: {
      medium: {
        width: '$7',
        height: '$4',
      },
      large: {
        width: '$9',
        height: '$5',
      },
    },
  },
})

export type StyledSwitchProps = StrictComponentProps<typeof StyledSwitch>
