import React, { createContext, useCallback, useContext, useRef } from 'react'

interface BaseTooltipProps {
  /**
   * When set to true, the Tooltip will not be displayed once, and afterward, the value reverts to false
   * @default ref(false)
   */
  preventNextOpeningRef?: React.MutableRefObject<boolean>
}

interface BaseTooltipContextProps {
  /**
   * When set to true, the Tooltip will not be displayed once, and afterward, the value reverts to false
   */
  preventNextOpeningRef: React.MutableRefObject<boolean>

  /**
   * Sets `preventNextOpeningRef` value to true
   */
  ignoreNextTooltip: () => void
}

export interface BaseTooltipProviderProps extends BaseTooltipProps {
  children: React.ReactNode
}

const BaseTooltipContext = createContext<BaseTooltipContextProps>({
  preventNextOpeningRef: { current: false },
  ignoreNextTooltip: () => {},
})

export const BaseTooltipProvider = ({
  children,
  preventNextOpeningRef: preventNextOpeningRefProp,
  ...restProps
}: BaseTooltipProviderProps): JSX.Element => {
  const internalRef = useRef<boolean>(false)

  const preventNextOpeningRef = preventNextOpeningRefProp ?? internalRef

  const ignoreNextTooltip = useCallback(() => {
    preventNextOpeningRef.current = true
  }, [preventNextOpeningRef])

  return (
    <BaseTooltipContext.Provider
      value={{
        preventNextOpeningRef: preventNextOpeningRef,
        ignoreNextTooltip,
        ...restProps,
      }}
    >
      {children}
    </BaseTooltipContext.Provider>
  )
}

export const useBaseTooltipContext = (): BaseTooltipContextProps =>
  useContext(BaseTooltipContext)
