import type { StrictComponentProps } from '@mirohq/design-system-stitches'
import { styled } from '@mirohq/design-system-stitches'
import * as RadixDropdownMenu from '@radix-ui/react-dropdown-menu'
import { styles } from '@mirohq/design-system-base-switch'
import { Primitive } from '@mirohq/design-system-primitive'

import { itemDefaults } from '../styles/item'

export const StyledSwitch = styled(Primitive.span, {
  ...styles.default,
  width: '$7',
  height: '$4',
})

export const StyledSwitchItem = styled(RadixDropdownMenu.CheckboxItem, {
  ...itemDefaults,

  [`&[aria-checked=true] ${StyledSwitch}`]: styles.checked,
  [`&[aria-checked=true]:is(:hover,[data-hovered]):not([aria-disabled="true"],[data-disabled]) ${StyledSwitch}`]:
    styles.checkedHovered,

  [`&:is(:hover,[data-hovered]):not([aria-disabled="true"],[data-disabled]) ${StyledSwitch}`]:
    styles.hovered,
  [`
    &[aria-disabled="true"] ${StyledSwitch},
    &[data-disabled] ${StyledSwitch}
  `]: styles.disabled,
})

export type StyledSwitchItemProps = StrictComponentProps<
  typeof StyledSwitchItem
>
