import type { ComponentPropsWithRef } from 'react'
import { Primitive } from '@mirohq/design-system-primitive'
import { styled, keyframes } from '@mirohq/design-system-stitches'

const rotateAnimation = keyframes({
  '100%': { transform: 'rotate(360deg)' },
})

const strokeAnimation = keyframes({
  '0%': {
    strokeDasharray: '1, 200',
    strokeDashoffset: 0,
  },
  '50%': {
    strokeDasharray: '89, 200',
    strokeDashoffset: '-35px',
  },
  '100%': {
    strokeDasharray: '89, 200',
    strokeDashoffset: '-124px',
  },
})

export const StyledSpinner = styled(Primitive.svg, {
  display: 'block',
  animation: `${rotateAnimation} 2s linear infinite`,
  transformOrigin: 'center center',
  stroke: 'currentColor',
  '& [id=circle]': {
    fill: 'none',
    strokeMiterlimit: '10',
    strokeDasharray: '89, 200',
    strokeDashoffset: 0,
    animation: 'animate 1.5s ease-in-out infinite',
    strokeLinecap: 'round',
    '@media (prefers-reduced-motion: no-preference)': {
      animationName: strokeAnimation,
    },
  },
  variants: {
    size: {
      small: {
        width: '$4',
        height: '$4',
        '& [id=circle]': {
          strokeWidth: '6px',
        },
      },
      medium: {
        width: '$6',
        height: '$6',
        '& [id=circle]': {
          strokeWidth: '4px',
        },
      },
    },
  },
})

export type StyledSpinnerProps = ComponentPropsWithRef<typeof StyledSpinner>
