import { Primitive } from '@mirohq/design-system-primitive'
import { styled } from '@mirohq/design-system-stitches'
import type { ComponentPropsWithRef } from 'react'
import { textFieldStyles } from '@mirohq/design-system-base-text-field'

const disabledAndReadonlySelectors =
  ':read-only, :disabled, [aria-disabled="true"], [data-disabled]'

export const StyledInput = styled(Primitive.input, {
  all: 'unset',
  background: 'transparent',
  color: '$text-neutrals',
  width: '100%',
  borderRadius: '$50',
  order: 2,
  padding: '0 $50',
  height: '100%',

  [`&:not(${disabledAndReadonlySelectors})::placeholder`]:
    textFieldStyles.base.placeholder,

  '&:read-only': {
    color: textFieldStyles.variants.readOnly.color,
  },

  '&:disabled, &[aria-disabled=true], &[data-disabled]':
    textFieldStyles.base.disabled,
})

export type StyledInputProps = ComponentPropsWithRef<typeof StyledInput>
