import React from 'react'
import type { ElementRef } from 'react'

import { StyledArrow } from './arrow.styled'
import type { StyledArrowProps } from './arrow.styled'
import { usePopoverContext } from '../use-popover-context'

export interface ArrowProps extends StyledArrowProps {
  /**
   * The appearance of arrow.
   */
  variant?: StyledArrowProps['variant']
}

export const Arrow = React.forwardRef<
  ElementRef<typeof StyledArrow>,
  ArrowProps
>((props, forwardRef) => {
  const { variant } = usePopoverContext()
  return (
    <StyledArrow {...props} variant={variant} aria-hidden ref={forwardRef} />
  )
})
