import * as RadixTabs from '@radix-ui/react-tabs'
import React from 'react'
import type { ElementRef, MouseEvent } from 'react'

import { TabsContext } from './provider'
import { TriggerStyled } from './trigger.styled'
import type { StyledTriggerProps } from './trigger.styled'

export type TriggerProps = StyledTriggerProps & {
  /**
   * A unique value that associates the trigger with a content.
   */
  value: string
}

export const Trigger = React.forwardRef<
  ElementRef<typeof TriggerStyled>,
  TriggerProps
>(({ children, value, onClick, onMouseDown, ...restProps }, forwardRef) => {
  const { variant, size, onValueChange } = React.useContext(TabsContext)

  const handleMouseDown = (e: MouseEvent<HTMLButtonElement>): void => {
    // Forward user's onMouseDown handler if provided
    onMouseDown?.(e)

    if (restProps.disabled === true) {
      return
    }
    e.preventDefault()
  }

  const handleClick = (e: MouseEvent<HTMLButtonElement>): void => {
    if (restProps.disabled === true) {
      return
    }

    onClick?.(e)
    onValueChange?.(value)
  }

  return (
    <TriggerStyled
      {...restProps}
      size={size}
      preventFocusOnPress='auto'
      role='tab'
      asChild
      ref={forwardRef}
      variant={variant}
    >
      <RadixTabs.Trigger
        value={value}
        onMouseDown={handleMouseDown}
        onClick={handleClick}
      >
        {children}
      </RadixTabs.Trigger>
    </TriggerStyled>
  )
})
