import React from 'react'
import type { ElementRef } from 'react'

import type { BaseItemProps } from './base-item'
import { StyledIcon } from './icon.styled'

export interface IconProps extends BaseItemProps {
  /**
   * Renders icon in enabled/toggled state.
   * @default false
   */
  active?: boolean
}

export const Icon = React.forwardRef<ElementRef<typeof StyledIcon>, IconProps>(
  ({ active = false, ...restProps }, forwardRef) => (
    <StyledIcon {...restProps} active={active} ref={forwardRef} />
  )
)
