import React from 'react'
import type { ElementRef } from 'react'

import { useSliderContext } from '../hooks/use-slider-context'
import type { StyledValueProps } from './value.styled'
import { StyledValue } from './value.styled'

export interface ValueProps extends StyledValueProps {}

export const Value = React.forwardRef<
  ElementRef<typeof StyledValue>,
  ValueProps
>((props, forwardRef) => {
  const { value } = useSliderContext()

  return (
    <StyledValue {...props} ref={forwardRef}>
      {value}
    </StyledValue>
  )
})
