import React, { useRef } from 'react'
import { mergeRefs, handleVirtualClick } from '@mirohq/design-system-utils'
import type { ElementRef } from 'react'

import { StyledTrigger } from './trigger.styled'
import type { StyledTriggerProps } from './trigger.styled'

export interface TriggerProps extends StyledTriggerProps {}

export const Trigger = React.forwardRef<
  ElementRef<typeof StyledTrigger>,
  TriggerProps
>(({ asChild = false, onClick, ...restProps }, forwardRef) => {
  const ref = useRef<HTMLButtonElement>(null)

  return (
    <StyledTrigger
      {...restProps}
      onClick={e => {
        handleVirtualClick(e, ref)
        onClick?.(e)
      }}
      ref={mergeRefs([ref, forwardRef])}
      unstyled={!asChild}
      asChild={asChild}
    />
  )
})
