import type { KeyboardEventHandler } from 'react'

import type { FocusableElement, InteractionProps } from './types'
import { handleFocus } from './utils/focus'

interface KeyboardEventHandlers {
  onKeyDown: KeyboardEventHandler<FocusableElement>
  onKeyUp: KeyboardEventHandler<FocusableElement>
}

export const getEvents = ({
  triggerPressStart,
  triggerPressUp,
  triggerPressEnd,
  state,
  addListener,
  removeAllListeners,
  createEvent,
  propsRef,
  document: customDocument,
}: InteractionProps): KeyboardEventHandlers => {
  const { disabled = false, preventFocusOnPress = false } =
    propsRef.current ?? {}

  const onDocumentKeyUp = (e: KeyboardEvent): void => {
    if (
      state.isPressed &&
      state.target != null &&
      isValidKeyboardEvent(e, state.target)
    ) {
      if (shouldPreventDefaultKeyboard(e.target as Element)) {
        e.preventDefault()
      }

      state.isPressed = false
      const target = e.target as Element

      if (state.target === null) {
        removeAllListeners()
        return
      }

      triggerPressEnd(
        createEvent(state.target, e),
        'keyboard',
        state.target.contains(target)
      )
      removeAllListeners()

      // If the target is a link, trigger the click method to open the URL,
      // but defer triggering pressEnd until onClick event handler.
      if (
        state.target instanceof HTMLElement &&
        state.target.contains(target) &&
        (isHTMLAnchorLink(state.target) ||
          state.target.getAttribute('role') === 'link')
      ) {
        state.ignoreClickAfterPress = true
        state.target.click()
      }
    }
  }

  const onKeyDown: KeyboardEventHandler<FocusableElement> = e => {
    // Avoid virtual clicks to fire events when holding "Enter" or "Space"
    if (isEnterOrSpaceEvent(e)) {
      state.ignoreClickAfterPress = true
    }

    if (
      isValidKeyboardEvent(e.nativeEvent, e.currentTarget) &&
      e.currentTarget.contains(e.target as Element)
    ) {
      if (shouldPreventDefaultKeyboard(e.target as Element)) {
        e.preventDefault()
      }

      if (e.repeat) {
        return
      }

      handleFocus({
        event: e,
        disabled,
        preventFocusOnPress,
      })

      // If the event is repeating, it may have started on a different element
      // after which focus moved to the current element. Ignore these events and
      // only handle the first key down event.
      if (!state.isPressed) {
        state.target = e.currentTarget
        state.isPressed = true
        triggerPressStart(e, 'keyboard')

        // Focus may move before the key up event, so register the event on the document
        // instead of the same element where the key down event occurred.
        addListener(customDocument, 'keyup', onDocumentKeyUp, false)
      }
    }
  }

  const onKeyUp: KeyboardEventHandler<FocusableElement> = e => {
    // When pressing button with "Enter" the event order will be:
    // onkeydown > onclick > onkeyup
    // Both onkeydown and onclick will repeat when holding the keydown
    // Because of that the press events are locked until the onKeyup is released
    if (e.key === 'Enter') {
      state.ignoreClickAfterPress = false
    }

    if (state.target === null) {
      return
    }

    if (
      isValidKeyboardEvent(e.nativeEvent, e.currentTarget) &&
      !e.repeat &&
      e.currentTarget.contains(e.target as Element)
    ) {
      triggerPressUp(createEvent(state.target, e), 'keyboard')
    }
  }

  return {
    onKeyUp,
    onKeyDown,
  }
}

const nonTextInputTypes = new Set([
  'checkbox',
  'radio',
  'range',
  'color',
  'file',
  'image',
  'button',
  'submit',
  'reset',
])

const shouldPreventDefaultKeyboard = (target: Element): boolean =>
  !(target instanceof HTMLInputElement || target instanceof HTMLButtonElement)

const isHTMLAnchorLink = (target: Element): boolean =>
  target.tagName === 'A' && target.hasAttribute('href')

const isValidInputKey = (target: HTMLInputElement, key: string): boolean =>
  // Only space should toggle checkboxes and radios, not enter.
  target.type === 'checkbox' || target.type === 'radio'
    ? key === ' '
    : nonTextInputTypes.has(target.type)

const isEnterOrSpaceEvent = (e: any): boolean =>
  e.key === 'Enter' || e.key === ' ' || e.code === 'Space'

const isValidKeyboardEvent = (
  event: unknown,
  currentTarget: unknown
): boolean => {
  const { key } = event as KeyboardEvent
  const element = currentTarget as HTMLElement
  const role = element.getAttribute('role')

  // Accessibility for keyboards. Space and Enter only.
  if (!isEnterOrSpaceEvent(event)) {
    return false
  }

  // Links rules:
  // - element has role='link'
  // - is an anchor <a> element
  // - fires only on Enter
  const isLink = isHTMLAnchorLink(element) || role === 'link'
  if (isLink && key === 'Enter') {
    return true
  }

  // link as button
  if (element.tagName === 'A') {
    if (role === 'button' && key === ' ') {
      return true
    }

    return false
  }

  // Buttons rules:
  // - element has role='button'
  // - is an anchor <button> element
  // - fires on Enter and Space
  const isButton = role === 'button' || element.tagName === 'BUTTON'
  if (isButton) {
    return true
  }

  // Inputs
  const isInput = element instanceof HTMLInputElement
  if (isInput && isValidInputKey(element, key) && key === ' ') {
    return true
  }

  // Non a11y element such as a div or span
  if (!isLink && !isButton && !isInput) {
    return true
  }

  return false
}
