import React from 'react'
import type { PopoverPortalProps } from '@radix-ui/react-popover'
import { Portal as RadixPortal } from '@radix-ui/react-popover'

import { useComboboxContext } from '../hooks/use-combobox-context'

export interface PortalProps extends PopoverPortalProps {
  /**
   * Specify a container element to portal the content into.
   */
  container?: HTMLElement | null
}

export const Portal = ({
  forceMount: forceMountProp,
  ...restProps
}: PortalProps): React.ReactNode => {
  const { openState } = useComboboxContext()

  // We always need to render content with items to be able to render Chips in Value,
  // but without forceMount Radix will not render Portal when Popover is closed
  const forceMount = !openState || forceMountProp

  return <RadixPortal forceMount={forceMount} {...restProps} />
}
