import React from 'react'
import type { ElementRef, AnchorHTMLAttributes } from 'react'

import { Item } from './item'
import type { ItemProps } from './item'

type ItemPropsWithAnchor = ItemProps & AnchorHTMLAttributes<typeof Item>
export interface LinkItemProps extends ItemPropsWithAnchor {}

export const LinkItem = React.forwardRef<
  ElementRef<typeof Item>,
  LinkItemProps
>(({ children, href, ...restProps }, forwardRef) => (
  <Item asChild ref={forwardRef} {...restProps}>
    <a href={href}>{children}</a>
  </Item>
))
