import type { ElementRef } from 'react'
import React from 'react'
import type { IconButtonProps } from '@mirohq/design-system-icon-button'
import { IconButton } from '@mirohq/design-system-icon-button'

export type HeaderButtonProps = IconButtonProps & {
  onFocus?: () => void
  onPress?: () => void
  isDisabled?: boolean // name comes from react-aria
  'aria-label': string
}

export const HeaderButton = React.forwardRef<
  ElementRef<'button'>,
  HeaderButtonProps
>(({ isDisabled, children, ...restProps }, forwardRef) => (
  <IconButton
    {...restProps}
    size='medium'
    disabled={isDisabled}
    ref={forwardRef}
  >
    {children}
  </IconButton>
)) as React.ForwardRefExoticComponent<HeaderButtonProps>
