import React, { useEffect, useRef } from 'react'
import type { ElementRef, ForwardRefExoticComponent } from 'react'
import type { FormElementProps } from '@mirohq/design-system-base-form'
import { useFormFieldContext } from '@mirohq/design-system-base-form'
import { mergeRefs } from '@mirohq/design-system-utils'
import type { SelectProps as RadixSelectProps } from '@radix-ui/react-select'
import { Root as RadixSelect } from '@radix-ui/react-select'
import { usePrevious } from '@mirohq/design-system-use-previous'
import {
  BaseTooltipProvider,
  useBaseTooltipContext,
} from '@mirohq/design-system-base-tooltip'

import { StyledSelectContent } from './select.styled'
import type { StyledSelectContentProps } from './select.styled'
import { Group } from './partials/group'
import { Item } from './partials/item'
import { Portal } from './partials/portal'
import { Trigger } from './partials/trigger'
import { Value } from './partials/value'
import { Content } from './partials/content'
import { GroupLabel } from './partials/group-label'
import { Separator } from './partials/separator'
import { SelectProvider, useSelectContext } from './hooks/use-select-context'
import type { Direction } from './types'

export interface SelectProps
  extends FormElementProps,
    Omit<RadixSelectProps, 'onOpenChange'>,
    Omit<StyledSelectContentProps, 'dir'> {
  /**
   * The value of the select when initially rendered. Use when you do not need
   * to control the state of the select.
   */
  defaultValue?: string

  /**
   * The controlled value of the select. Should be used in conjunction with
   * onValueChange.
   */
  value?: string

  /**
   * Event handler called when the value changes.
   */
  onValueChange?: (value: string) => void

  /**
   * The open state of the select when it is initially rendered. Use when you do
   * not need to control its open state.
   * @default false
   */
  defaultOpen?: boolean

  /**
   * The controlled open state of the select. Must be used in conjunction with
   * onOpen and onClose.
   */
  open?: boolean

  /**
   * Event handler called when the select opens.
   */
  onOpen?: () => void

  /**
   * Event handler called when the select closes.
   */
  onClose?: () => void

  /**
   * The reading direction of the select when applicable. If omitted, inherits
   * globally from DirectionProvider or assumes LTR (left-to-right) reading
   * mode.
   * @default 'ltr'
   */
  direction?: Direction

  /**
   * The name of the select. Submitted with its owning form as part of a
   * name/value pair.
   */
  name?: string

  /**
   * Enables loading state for the select.
   */
  loading?: boolean
}

const Root = React.forwardRef<ElementRef<'select'>, SelectProps>(
  (
    {
      onOpen,
      onClose,
      children,
      onValueChange,
      value,
      defaultValue,
      loading = false,
      css,
      // @ts-expect-error className required when extending the component with styled()
      className,
      ...restProps
    },
    forwardRef
  ) => {
    const {
      required,
      readOnly,
      'aria-disabled': ariaDisabled,
      setLoading,
      disabled,
      direction,
      setOpenState,
      openState,
      open,
    } = useSelectContext()

    const {
      setRequired,
      setDisabled,
      setAriaDisabled,
      setReadOnly,
      formElementRef,
    } = useFormFieldContext<HTMLSelectElement>()

    useEffect(() => {
      setRequired?.(required)
      setDisabled?.(disabled)
      setAriaDisabled?.(ariaDisabled)
      setLoading(loading)
      setReadOnly?.(readOnly)
    }, [
      readOnly,
      disabled,
      loading,
      ariaDisabled,
      required,
      setRequired,
      setDisabled,
      setAriaDisabled,
      setReadOnly,
      setLoading,
    ])

    const { ignoreNextTooltip } = useBaseTooltipContext()
    const prevOpen = usePrevious(open)

    useEffect(() => {
      if (prevOpen === true && open === false) {
        ignoreNextTooltip()
      }
    }, [ignoreNextTooltip, open, prevOpen])

    const selectRef = useRef<HTMLSelectElement>()

    // intentionally not using useCallback to force formElementRef to be updated
    const domRef = (node: HTMLDivElement): void => {
      const selectElement = node?.nextElementSibling as HTMLSelectElement | null

      if (selectElement?.tagName === 'SELECT') {
        selectRef.current = selectElement
      }

      // @ts-expect-error TS doesn't know about the dynamic set of the generated select element
      mergeRefs([forwardRef, formElementRef])(selectElement)
    }

    return (
      <RadixSelect
        {...restProps}
        value={value}
        defaultValue={defaultValue}
        onValueChange={newValue => {
          onValueChange?.(newValue)
        }}
        dir={direction}
        disabled={disabled}
        required={required}
        open={open ?? openState}
        onOpenChange={newOpen => {
          if (open == null) {
            setOpenState(newOpen)

            if (!newOpen) {
              ignoreNextTooltip()
            }
          }

          newOpen ? onOpen?.() : onClose?.()
        }}
      >
        <StyledSelectContent
          ref={domRef}
          data-form-element='select'
          css={css}
          // @ts-expect-error className required when extending the component with styled()
          className={className}
        >
          {children}
        </StyledSelectContent>
      </RadixSelect>
    )
  }
)

export const Select = React.forwardRef<ElementRef<'select'>, SelectProps>(
  (
    {
      'aria-disabled': ariaDisabled,
      defaultOpen = false,
      direction = 'ltr',
      disabled,
      readOnly,
      required,
      valid,
      open,
      ...restProps
    },
    forwardRef
  ) => (
    <SelectProvider
      open={open}
      aria-disabled={ariaDisabled}
      defaultOpen={defaultOpen}
      direction={direction}
      required={required}
      disabled={disabled}
      readOnly={readOnly}
      valid={valid}
    >
      <BaseTooltipProvider>
        <Root {...restProps} ref={forwardRef} />
      </BaseTooltipProvider>
    </SelectProvider>
  )
) as ForwardRefExoticComponent<SelectProps> & Partials

export interface Partials {
  Content: typeof Content
  Separator: typeof Separator
  GroupLabel: typeof GroupLabel
  Group: typeof Group
  Item: typeof Item
  Portal: typeof Portal
  Trigger: typeof Trigger
  Value: typeof Value
}

Select.Group = Group
Select.Separator = Separator
Select.Content = Content
Select.Item = Item
Select.Portal = Portal
Select.Trigger = Trigger
Select.Value = Value
Select.GroupLabel = GroupLabel
