import React from 'react'
import type { ElementRef } from 'react'
import type { StrictComponentProps } from '@mirohq/design-system-stitches'
import { Primitive } from '@mirohq/design-system-primitive'
import { styled } from '@mirohq/design-system-stitches'

import { ItemProvider } from '../hooks/use-item'

export const StyledItemDescription = styled(Primitive.div, {
  display: '-webkit-box',
  width: '100%',
  WebkitBoxOrient: 'vertical',
  WebkitLineClamp: 2,
  overflow: 'hidden',
  gridArea: 'item-description',
  fontSize: '$150',
  lineHeight: 1.5,
  color: '$text-neutrals-subtle',
  '&:is(:hover,[data-hovered]):not([aria-disabled="true"],[data-disabled])': {
    background: '$background-neutrals-hover',
    color: '$text-neutrals-hover',
    '&:not([aria-disabled="true"])': {
      boxShadow: 'none',
    },
  },
})

type StyledItemDescriptionProps = StrictComponentProps<
  typeof StyledItemDescription
>

export const ItemDescription = React.forwardRef<
  ElementRef<typeof StyledItemDescription>,
  StyledItemDescriptionProps
>(({ ...props }, forwardRef) => (
  <ItemProvider>
    <StyledItemDescription {...props} ref={forwardRef} />
  </ItemProvider>
))
