import React, { createContext, useContext, useState, useRef } from 'react'
import { booleanify } from '@mirohq/design-system-utils'
import type { FormElementProps } from '@mirohq/design-system-base-form'

interface InputProps extends FormElementProps {
  onChange?: React.ChangeEventHandler<HTMLInputElement>
  variant?: 'outline' | 'subtle'
}

interface InputContextProps extends InputProps {
  focused: boolean
  setFocused: React.Dispatch<React.SetStateAction<boolean>>
  hovered: boolean
  setHovered: React.Dispatch<React.SetStateAction<boolean>>
  active: boolean
  setActive: React.Dispatch<React.SetStateAction<boolean>>
  hasIconSlot: boolean
  setHasIconSlot: React.Dispatch<React.SetStateAction<boolean>>
  actionButtonRef: React.RefObject<HTMLAnchorElement | HTMLButtonElement>
  inputRef: React.RefObject<HTMLInputElement>
  editable: boolean
}

export interface InputProviderProps extends InputProps {
  children?: React.ReactNode
}

const InputContext = createContext<InputContextProps>({} as any)

export const InputProvider = ({
  children,
  disabled,
  'aria-disabled': ariaDisabled,
  readOnly,
  required,
  valid,
  ...restProps
}: InputProviderProps): JSX.Element => {
  const actionButtonRef = useRef<HTMLAnchorElement | HTMLButtonElement>(null)
  const inputRef = useRef<HTMLInputElement>(null)
  const [focused, setFocused] = useState(false)
  const [hovered, setHovered] = useState(false)
  const [active, setActive] = useState(false)
  const [hasIconSlot, setHasIconSlot] = useState(false)
  const editable =
    !booleanify(disabled) && !booleanify(ariaDisabled) && !booleanify(readOnly)

  return (
    <InputContext.Provider
      value={{
        ...restProps,
        setFocused,
        setHovered,
        setActive,
        setHasIconSlot,
        hasIconSlot,
        active,
        hovered,
        focused,
        disabled,
        'aria-disabled': ariaDisabled,
        readOnly,
        editable,
        inputRef,
        actionButtonRef,
        required,
        valid,
      }}
    >
      {children}
    </InputContext.Provider>
  )
}

export const useInputContext = (): InputContextProps => useContext(InputContext)
