import React from 'react'
import type { ElementRef } from 'react'

import { StyledViewport } from './viewport.styled'
import type { StyledViewportProps } from './viewport.styled'

export type ViewportProps = StyledViewportProps

export const Viewport = React.forwardRef<
  ElementRef<typeof StyledViewport>,
  ViewportProps
>((props, forwardRef) => <StyledViewport {...props} ref={forwardRef} />)
