import React from 'react'
import type { ElementRef } from 'react'

import { StyledItem } from './item.styled'
import type { StyledItemProps } from './item.styled'
import { useAriaDisabled } from '../hooks/use-aria-disabled'
import { ItemProvider } from '../hooks/use-item'

export interface ItemProps extends StyledItemProps {
  /**
   * When true, prevents the user from interacting with the item.
   * @default false
   */
  disabled?: boolean

  /**
   * Event handler called when the user selects an item (via mouse of keyboard).
   * Calling event.preventDefault in this handler will prevent the dropdown menu
   * from closing when selecting that item.
   */
  onSelect?: (event: Event) => void

  /**
   * Optional text used for typeahead purposes. By default, the typeahead
   * behavior will use the .textContent of the item. Use this when the content
   * is complex, or you have non-textual content inside.
   */
  textValue?: string

  /**
   * Change the item style.
   * @default 'subtle'
   */
  variant?: StyledItemProps['variant']
}

export const Item = React.forwardRef<ElementRef<typeof StyledItem>, ItemProps>(
  ({ disabled = false, variant = 'subtle', ...restProps }, forwardRef) => {
    const elementProps = useAriaDisabled(restProps)

    return (
      <ItemProvider>
        <StyledItem
          {...elementProps}
          variant={variant}
          disabled={disabled}
          ref={forwardRef}
        />
      </ItemProvider>
    )
  }
)
