import { useFormFieldContext } from '@mirohq/design-system-base-form'
import type {
  BaseInputProps,
  BaseInputStyledProps,
} from '@mirohq/design-system-base-input'
import { BaseInput } from '@mirohq/design-system-base-input'
import { mergeRefs, stringAttrValue } from '@mirohq/design-system-utils'
import React, { useEffect } from 'react'

import { useComboboxContext } from '../hooks/use-combobox-context'
import { InputActionButton } from './input-action-button'
import { StyledBaseInput } from './input.styled'

export type InputProps = Omit<BaseInputStyledProps, 'size'> & {
  /**
   * The size of the input.
   * @default 'large'
   */
  size?: BaseInputProps['size']

  /**
   * The content that will be rendered inside the Combobox.Input when no value or
   * defaultValue is set.
   */
  placeholder?: string

  /**
   * The label text for Input's action button when Combobox is empty and closed. Will be rendered in a Tooltip.
   */
  openActionLabel: string

  /**
   * The label text for Input's action button when Combobox is empty and open. Will be rendered in a Tooltip.
   */
  closeActionLabel: string

  /**
   * Show a button to clear the Combobox value.
   * @default true
   */
  clearable?: boolean
} & (
    | { clearable: false; clearActionLabel?: never }
    | {
        /**
         * The label text for Input's action button when Combobox has values selected. Will be rendered in a Tooltip.
         */
        clearActionLabel: string
      }
  )

export const Input = React.forwardRef<HTMLInputElement, InputProps>(
  (
    {
      onFocus,
      id,
      children,
      size = 'large',
      'aria-describedby': ariaDescribedBy,
      'aria-invalid': ariaInvalid,
      placeholder,
      openActionLabel,
      closeActionLabel,
      clearable = true,
      clearActionLabel,
      onChange,
      css,
      // @ts-expect-error className required when extending the component with styled()
      className,
      ...restProps
    },
    ref
  ): React.ReactNode => {
    const {
      'aria-disabled': ariaDisabled,
      valid: comboboxValid,
      disabled,
      value = [],
      readOnly,
      triggerRef,
      inputRef,
      searchValue,
      setSearchValue,
      setSize,
      setPlaceholder,
    } = useComboboxContext()

    const {
      formElementId,
      ariaDescribedBy: formFieldContextDescribedBy,
      ariaInvalid: formFieldAriaInvalid,
      valid: formFieldValid,
    } = useFormFieldContext()

    useEffect(() => {
      setSize(size)
    }, [size, setSize])

    useEffect(() => {
      setPlaceholder(placeholder)
    }, [setPlaceholder, placeholder])

    const valid = formFieldValid ?? comboboxValid

    const scrollIntoView = (
      event: React.FocusEvent<HTMLInputElement>
    ): void => {
      const trigger = triggerRef?.current
      const baseInput = inputRef?.current?.parentElement

      if (baseInput != null && trigger != null) {
        event.preventDefault()
        baseInput.scrollTo({
          top: trigger.scrollHeight,
        })
      }

      if (onFocus !== undefined) {
        onFocus(event)
      }
    }

    const onInputChange = (e: React.ChangeEvent<HTMLInputElement>): void => {
      setSearchValue(e.target.value)
      onChange?.(e)
    }

    return (
      <StyledBaseInput
        aria-disabled={ariaDisabled}
        valid={valid}
        disabled={disabled}
        readOnly={readOnly}
        size={size}
        onChange={onInputChange}
        css={css}
        // @ts-expect-error className required when extending the component with styled()
        className={className}
      >
        <BaseInput.Input
          {...restProps}
          ref={mergeRefs([inputRef, ref])}
          value={searchValue}
          id={id ?? formElementId}
          aria-describedby={stringAttrValue(
            ariaDescribedBy,
            formFieldContextDescribedBy
          )}
          aria-invalid={ariaInvalid ?? formFieldAriaInvalid}
          placeholder={
            children === null || children === undefined || value.length === 0
              ? placeholder
              : undefined
          }
          onFocus={scrollIntoView}
        />
        {children}
        <InputActionButton
          openActionLabel={openActionLabel}
          closeActionLabel={closeActionLabel}
          clearActionLabel={clearActionLabel}
          clearable={clearable}
          size={size}
        />
      </StyledBaseInput>
    )
  }
)
