import React from 'react'
import type { ElementRef } from 'react'
import { Badge } from '@mirohq/design-system-badge'
import { stringAttrValue } from '@mirohq/design-system-utils'
import type { BaseButtonProps } from '@mirohq/design-system-base-button'
import { useId } from '@mirohq/design-system-use-id'

import type { StyledIconButtonProps } from './icon-buttons.styled'
import { StyledIconButton } from './icon-buttons.styled'

// Omit used only to help the prop-docs generator to expose the omitted props
export type IconButtonProps = Omit<BaseButtonProps, 'aria-label'> & {
  /**
   * Icon button label used to provide a description.
   */
  'aria-label': string

  /**
   * Change the Icon button style
   * @default 'ghost'
   */
  variant?: StyledIconButtonProps['variant']

  /**
   * Change the Icon button size.
   * @default 'large'
   */
  size?: StyledIconButtonProps['size']

  /**
   * Wraps the Icon button with a badge.
   * @default false
   */
  showBadge?: boolean

  /**
   * The content to be displayed inside of the badge.
   */
  badgeContent?: string

  /**
   * Badge label used to make Badge recognizable by the screen readers.
   */
  badgeAriaLabel?: string
}

export const IconButton = React.forwardRef<
  ElementRef<typeof StyledIconButton>,
  IconButtonProps
>(
  (
    {
      children,
      showBadge = false,
      badgeContent,
      badgeAriaLabel,
      variant = 'ghost',
      size = 'large',
      'aria-describedby': ariaDescribedBy,
      asChild = false,
      ...restProps
    },
    forwardRef
  ) => {
    const badgeId = useId()
    let badgeOffset = {}
    if (size === 'medium') {
      badgeOffset =
        badgeContent !== undefined && badgeContent !== ''
          ? { offsetX: -7, offsetY: -5 }
          : { offsetX: 3, offsetY: -3 }
    }

    let formattedChildren = children
    const isAsChildIsValid =
      asChild &&
      React.Children.count(children) === 1 &&
      React.isValidElement(children)

    if (isAsChildIsValid) {
      // when using asChild we need to render element to merge props with first
      const firstLevelChild = React.Children.only(children)
      const { children: secondLevelChildren, ...childProps } =
        firstLevelChild.props

      formattedChildren = React.cloneElement(firstLevelChild, {
        ...childProps,
        children: (
          <>
            {showBadge ? (
              <Badge
                id={badgeId}
                aria-label={badgeAriaLabel}
                {...badgeOffset}
                show={showBadge}
                content={badgeContent}
                inverted={variant === 'primary'}
              >
                {secondLevelChildren}
              </Badge>
            ) : (
              secondLevelChildren
            )}
          </>
        ),
      })
    } else {
      formattedChildren = (
        <>
          <Badge
            id={badgeId}
            aria-label={badgeAriaLabel}
            {...badgeOffset}
            show={showBadge}
            content={badgeContent}
            inverted={variant === 'primary'}
          >
            {children}
          </Badge>
        </>
      )
    }

    return (
      <StyledIconButton
        {...restProps}
        variant={variant}
        size={size}
        asChild={asChild}
        ref={forwardRef}
        aria-describedby={stringAttrValue(
          ariaDescribedBy,
          showBadge && badgeAriaLabel !== undefined && badgeId
        )}
      >
        {formattedChildren}
      </StyledIconButton>
    )
  }
) as React.ForwardRefExoticComponent<IconButtonProps>
