import React, { useCallback } from 'react'
import { InputSearch } from '@mirohq/design-system-input'
import { styled } from '@mirohq/design-system-stitches'

import { useComboboxContext } from '../hooks/use-combobox-context'

const StyledInputSearch = styled(InputSearch, {
  '&&': {
    border: '1px solid $border-neutrals',
    backgroundColor: '$background-control',
  },
})

export const SearchInput = React.forwardRef<
  HTMLInputElement,
  React.ComponentProps<typeof InputSearch>
>((props, ref) => {
  const { setSearchValue, contentRef } = useComboboxContext()

  const handleSearchChange = useCallback(
    (event: React.ChangeEvent<HTMLInputElement>): void => {
      setSearchValue(event.target.value)
      props.onChange?.(event)
    },
    [props, setSearchValue]
  )

  const handleKeyDown = (
    event: React.KeyboardEvent<HTMLInputElement>
  ): void => {
    if (event.key === 'ArrowDown') {
      event.preventDefault()
      // Select first item that is not disabled
      const firstItem = contentRef.current?.querySelector<HTMLElement>(
        '[role="option"]:not([aria-disabled="true"])'
      )
      firstItem?.focus()
    }

    props.onKeyDown?.(event)
  }

  return (
    <StyledInputSearch
      {...props}
      ref={ref}
      onKeyDown={handleKeyDown}
      onChange={handleSearchChange}
    />
  )
})
