import { styled } from '@mirohq/design-system-stitches'
import type { StrictComponentProps } from '@mirohq/design-system-stitches'
import * as RadixDropdownMenu from '@radix-ui/react-dropdown-menu'

import { itemDefaults } from '../styles/item'

export const StyledItem = styled(RadixDropdownMenu.Item, {
  ...itemDefaults,
  variants: {
    // This is a hack for the :has() selector
    // Remove it after Firefox implements it
    hasRightSlot: {
      true: {
        paddingRight: '$600',
      },
    },
    variant: {
      subtle: {},
      danger: {
        '&:is(:hover,[data-hovered]):not([aria-disabled="true"],[data-disabled])':
          {
            color: '$text-danger',
            backgroundColor: '$background-danger-subtle',
          },
        '&:active:not([aria-disabled="true"],[data-disabled])': {
          color: '$text-danger-active',
          backgroundColor: '$background-danger-subtle-active',
        },
      },
    },
  },
})

export type StyledItemProps = StrictComponentProps<typeof StyledItem>
