import React from 'react'
import type { ElementRef } from 'react'
import { IconChevronRight } from '@mirohq/design-system-icons'

import { StyledIconContainer, StyledSubTrigger } from './sub-trigger.styled'
import type { StyledSubTriggerProps } from './sub-trigger.styled'
import { RightSlot } from './right-slot'
import { useAriaDisabled } from '../hooks/use-aria-disabled'

export interface SubTriggerProps extends StyledSubTriggerProps {
  /**
   * Prevents the user from interacting with the switch.
   * @default false
   */
  disabled?: boolean

  /**
   * Optional text used for type ahead purposes. By default the type ahead
   * behavior will use the .textContent of the item. Use this when the content
   * is complex, or you have.
   */
  textValue?: string
}

export const SubTrigger = React.forwardRef<
  ElementRef<typeof StyledSubTrigger>,
  StyledSubTriggerProps
>(({ children, disabled = false, ...restProps }, forwardRef) => {
  // remove onSelect to avoid TS mismatch error
  let { onSelect, ...elementProps } = restProps
  elementProps = useAriaDisabled(elementProps)

  return (
    <StyledSubTrigger {...elementProps} disabled={disabled} ref={forwardRef}>
      {children}
      <RightSlot>
        <StyledIconContainer
          data-testid={
            process.env.NODE_ENV === 'test' ? 'submenu-arrow-icon' : undefined
          }
        >
          <IconChevronRight size='small' />
        </StyledIconContainer>
      </RightSlot>
    </StyledSubTrigger>
  )
})
