import React from 'react'
import type { ElementRef } from 'react'
import { Hotkey as BaseHotkey } from '@mirohq/design-system-base-hotkey'

import type { StyledHotkeyProps } from './hotkey.styled'
import { StyledHotkey } from './hotkey.styled'

export interface HotkeyProps extends StyledHotkeyProps {
  /**
   * The content.
   */
  children: string
}

export const Hotkey = React.forwardRef<
  ElementRef<typeof StyledHotkey>,
  HotkeyProps
>(({ children, ...restProps }, forwardRef) => (
  <StyledHotkey {...restProps} ref={forwardRef}>
    <BaseHotkey variant='inverted'>{children}</BaseHotkey>
  </StyledHotkey>
))
