import React from 'react'
import { Corner as RadixCorner } from '@radix-ui/react-scroll-area'
import { styled } from '@mirohq/design-system-stitches'
import type { StrictComponentProps } from '@mirohq/design-system-stitches'
import type { ElementRef } from 'react'

const StyledCorner = styled(RadixCorner)
type StyledCornerProps = StrictComponentProps<typeof StyledCorner>

export interface CornerProps extends StyledCornerProps {}

export const Corner = React.forwardRef<
  ElementRef<typeof StyledCorner>,
  CornerProps
>((props, forwardRef) => <StyledCorner {...props} ref={forwardRef} />)
