import React from 'react'

import type { StyledTriggerProps } from './trigger.styled'

interface TabsContextProps {
  variant: StyledTriggerProps['variant']
  size: StyledTriggerProps['size']
  onValueChange?: (value: string) => void
}

interface TabsContextProviderProps extends TabsContextProps {
  children: React.ReactNode
}

export const TabsContext = React.createContext<TabsContextProps>({
  variant: 'tabs',
  size: 'medium',
})

export const TabsContextProvider = ({
  variant,
  size,
  onValueChange,
  children,
}: TabsContextProviderProps): React.ReactNode => (
  <TabsContext.Provider
    value={{
      variant,
      size,
      onValueChange,
    }}
  >
    {children}
  </TabsContext.Provider>
)
