import React from 'react'
import type { ElementRef } from 'react'

import { StyledGroupLabel } from './group-label.styled'
import type { StyledGroupLabelProps } from './group-label.styled'

export interface GroupLabelProps extends StyledGroupLabelProps {}

export const GroupLabel = React.forwardRef<
  ElementRef<typeof StyledGroupLabel>,
  GroupLabelProps
>((props, forwardRef) => <StyledGroupLabel {...props} ref={forwardRef} />)
