import React, { useState } from 'react'
import type { ElementRef } from 'react'

import { StyledSub } from './sub.styled'
import type { StyledSubProps } from './sub.styled'

export interface SubProps extends StyledSubProps {
  /**
   * The open state of the submenu when it is initially rendered. Use when you
   * do not need to control its open state.
   */
  defaultOpen?: boolean

  /**
   * The controlled open state of the submenu. Must be used in conjunction with
   * onOpenChange.
   */
  open?: boolean

  /**
   * Event handler called when the submenu opens.
   */
  onOpen?: () => void

  /**
   * Event handler called when the submenu closes.
   */
  onClose?: () => void
}

export const Sub = React.forwardRef<ElementRef<typeof StyledSub>, SubProps>(
  ({ defaultOpen, onOpen, onClose, open, ...restProps }, forwardRef) => {
    const [openState, setOpenState] = useState(defaultOpen)
    return (
      <StyledSub
        {...restProps}
        open={open ?? openState}
        onOpenChange={newOpen => {
          if (open == null) {
            setOpenState(newOpen)
          }

          newOpen ? onOpen?.() : onClose?.()
        }}
        ref={forwardRef}
      />
    )
  }
)
