import type { ComponentPropsWithRef } from 'react'
import { Primitive } from '@mirohq/design-system-primitive'
import { styled, theme } from '@mirohq/design-system-stitches'
import { mapKeysToVariants } from '@mirohq/design-system-utils'

export const StyledFlex = styled(Primitive.div, {
  display: 'flex',
  variants: {
    align: {
      start: {
        alignItems: 'flex-start',
      },
      center: {
        alignItems: 'center',
      },
      end: {
        alignItems: 'flex-end',
      },
      stretch: {
        alignItems: 'stretch',
      },
      baseline: {
        alignItems: 'baseline',
      },
    },
    direction: {
      row: {
        flexDirection: 'row',
      },
      column: {
        flexDirection: 'column',
      },
      rowReverse: {
        flexDirection: 'row-reverse',
      },
      columnReverse: {
        flexDirection: 'column-reverse',
      },
    },
    gap: mapKeysToVariants(theme.space, key => ({
      gap: `$${key}`,
      [`@supports not(gap: ${key})`]: {
        margin: `calc($${key} / 2 * -1)`,
        '> *': {
          margin: `calc($${key} / 2)`,
        },
      },
    })),
    justify: {
      start: {
        justifyContent: 'flex-start',
      },
      center: {
        justifyContent: 'center',
      },
      end: {
        justifyContent: 'flex-end',
      },
      between: {
        justifyContent: 'space-between',
      },
      around: {
        justifyContent: 'space-around',
      },
      evenly: {
        justifyContent: 'space-evenly',
      },
    },
    wrap: {
      noWrap: {
        flexWrap: 'nowrap',
      },
      wrap: {
        flexWrap: 'wrap',
      },
      wrapReverse: {
        flexWrap: 'wrap-reverse',
      },
    },
  },
})

export type StyledFlexProps = ComponentPropsWithRef<typeof StyledFlex>
