import React from 'react'
import type { ElementRef } from 'react'

import { StyledSpinner } from './spinner.styled'
import type { StyledSpinnerProps } from './spinner.styled'

export interface SpinnerProps extends StyledSpinnerProps {
  /**
   * The size of the spinner.
   * @default 'medium'
   */
  size?: StyledSpinnerProps['size']
}

export const Spinner = React.forwardRef<
  ElementRef<typeof StyledSpinner>,
  SpinnerProps
>(({ size = 'medium', ...restProps }, forwardRef) => (
  <StyledSpinner
    {...restProps}
    viewBox='25 25 50 50'
    size={size}
    ref={forwardRef}
  >
    <circle id='circle' cx='50' cy='50' r='20' />
  </StyledSpinner>
))
