import React, {
  createContext,
  useContext,
  useState,
  useRef,
  useEffect,
} from 'react'
import type { AriaAttributes } from 'react'
import type { Booleanish } from '@mirohq/design-system-types'
import { useId } from '@mirohq/design-system-use-id'
import { stringAttrValue } from '@mirohq/design-system-utils'

import type { FormFieldStatus } from '../constants'
import { FORM_FIELD_STATUS } from '../constants'

type FormElements = HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement

interface FormFieldProps {
  /**
   * Field status
   * @default "pristine"
   */
  status?: FormFieldStatus

  id?: string
}

interface FormFieldContextProps<FormElementRef extends FormElements>
  extends FormFieldProps {
  setStatus: React.Dispatch<React.SetStateAction<FormFieldStatus>>
  isFloatingLabel: boolean
  setIsFloatingLabel: React.Dispatch<React.SetStateAction<boolean>>
  isHiddenLabel: boolean
  setIsHiddenLabel: React.Dispatch<React.SetStateAction<boolean>>
  label: React.ReactNode
  setLabel: React.Dispatch<React.SetStateAction<React.ReactNode>>
  focused: boolean
  setFocused: React.Dispatch<React.SetStateAction<boolean>>
  helperId: string
  setHelperId: React.Dispatch<React.SetStateAction<string>>
  messageId: string
  setMessageId: React.Dispatch<React.SetStateAction<string>>
  descriptionId: string
  setDescriptionId: React.Dispatch<React.SetStateAction<string>>
  formElementId: string
  formElementRef: React.MutableRefObject<FormElementRef>
  ariaDescribedBy: string | undefined
  shouldUseFloatingLabel: boolean
  required: boolean | undefined
  setRequired: React.Dispatch<React.SetStateAction<boolean | undefined>>
  readOnly: boolean | undefined
  setReadOnly: React.Dispatch<React.SetStateAction<boolean | undefined>>
  disabled: boolean | undefined
  setDisabled: React.Dispatch<React.SetStateAction<boolean | undefined>>
  ariaDisabled: Booleanish | undefined
  setAriaDisabled: React.Dispatch<React.SetStateAction<Booleanish | undefined>>
  ariaInvalid: AriaAttributes['aria-invalid']
  valid: boolean | undefined
}

export interface FormFieldProviderProps extends FormFieldProps {
  children?: React.ReactNode
}

const FormFieldContext = createContext<FormFieldContextProps<any>>({} as any)

export const FormFieldProvider = ({
  children,
  status: customStatus,
  id: customId,
  ...restProps
}: FormFieldProviderProps): JSX.Element => {
  // form field state
  const [status, setStatus] = useState<FormFieldStatus>(
    customStatus ?? 'pristine'
  )
  const [focused, setFocused] = useState(false)
  const [isFloatingLabel, setIsFloatingLabel] = useState(true)
  const [isHiddenLabel, setIsHiddenLabel] = useState(false)
  const [label, setLabel] = useState<React.ReactNode>(null)
  const shouldUseFloatingLabel = label != null && isFloatingLabel

  // partials
  const autoId = useId()
  const id = customId ?? autoId

  const [messageId, setMessageId] = useState(`${id}-message`)
  const [helperId, setHelperId] = useState(`${id}-helper`)
  const [descriptionId, setDescriptionId] = useState(`${id}-description`)

  // form element
  const formElementRef = useRef<HTMLInputElement>(null)
  const [required, setRequired] = useState<boolean>()
  const [readOnly, setReadOnly] = useState<boolean>()
  const [disabled, setDisabled] = useState<boolean>()
  const [ariaDisabled, setAriaDisabled] = useState<Booleanish>()
  const [ariaDescribedBy, setAriaDescribedBy] = useState<string | undefined>('')

  const valid =
    status === FORM_FIELD_STATUS.valid
      ? true
      : status === FORM_FIELD_STATUS.invalid
      ? false
      : undefined

  const ariaInvalid: AriaAttributes['aria-invalid'] =
    status === FORM_FIELD_STATUS.invalid ? true : undefined

  useEffect(() => {
    const message = document.getElementById(messageId)
    const helper = document.getElementById(helperId)
    const description = document.getElementById(descriptionId)

    const ariaDescribedBy = stringAttrValue(
      // message or description
      message != null ? messageId : description != null && descriptionId,
      helper != null && helperId
    )

    /**
     * message + helper text
     * description + helper text
     */
    setAriaDescribedBy(ariaDescribedBy === '' ? undefined : ariaDescribedBy)
  }, [messageId, helperId, descriptionId])

  return (
    <FormFieldContext.Provider
      value={{
        ...restProps,
        id,
        formElementRef,
        formElementId: `${id}-form-element`,
        messageId,
        setMessageId,
        helperId,
        setHelperId,
        descriptionId,
        setDescriptionId,
        status,
        setStatus,
        focused,
        setFocused,
        isFloatingLabel,
        setIsFloatingLabel,
        isHiddenLabel,
        setIsHiddenLabel,
        label,
        setLabel,
        ariaDescribedBy,
        shouldUseFloatingLabel,
        required,
        setRequired,
        readOnly,
        setReadOnly,
        disabled,
        setDisabled,
        ariaDisabled,
        setAriaDisabled,
        ariaInvalid,
        valid,
      }}
    >
      {children}
    </FormFieldContext.Provider>
  )
}

export const useFormFieldContext = <
  T extends FormElements
>(): FormFieldContextProps<T> => useContext(FormFieldContext)
