import React from 'react'
import type { ElementRef } from 'react'
import { booleanify } from '@mirohq/design-system-utils'

import { useSliderContext } from '../hooks/use-slider-context'
import { StyledRoot, StyledTrack } from './track.styled'
import type { StyledTrackProps } from './track.styled'

export interface TrackProps extends StyledTrackProps {}

export const Track = React.forwardRef<
  ElementRef<typeof StyledRoot>,
  TrackProps
>(
  (
    {
      children,
      css,
      // @ts-expect-error className required when extending the component with styled()
      className,
      ...restProps
    },
    forwardRef
  ) => {
    const {
      value,
      min,
      max,
      step,
      onValueCommit,
      onValueChange,
      disabled,
      ariaDisabled,
      name,
    } = useSliderContext()

    return (
      <StyledRoot
        css={css}
        // @ts-expect-error className required when extending the component with styled()
        className={className}
        step={step}
        disabled={disabled}
        aria-disabled={ariaDisabled}
        value={[value]}
        ref={forwardRef}
        min={min}
        max={max}
        name={name}
        onValueCommit={value => onValueCommit?.(value[0])}
        onValueChange={value => onValueChange(value[0])}
        onPointerDown={e => {
          if (booleanify(ariaDisabled)) {
            e.preventDefault()
          }
        }}
      >
        <StyledTrack
          {...restProps}
          disabled={disabled}
          ariaDisabled={ariaDisabled}
          ref={forwardRef}
        >
          {children}
        </StyledTrack>
      </StyledRoot>
    )
  }
)
