import React, { createContext, useContext } from 'react'
import type { FormElementProps } from '@mirohq/design-system-base-form'

interface GroupProviderSharedProps extends FormElementProps {
  groupId?: string
}

interface GroupContextProps extends GroupProviderSharedProps {}

interface GroupProviderProps extends GroupProviderSharedProps {
  children: React.ReactNode
}

const GroupContext = createContext<GroupContextProps>({} as any)

export const GroupProvider = ({
  children,
  ...restProps
}: GroupProviderProps): JSX.Element => (
  <GroupContext.Provider
    value={{
      ...restProps,
    }}
  >
    {children}
  </GroupContext.Provider>
)

export const useGroupContext = (): GroupContextProps => useContext(GroupContext)
