import { useLocalStorage } from '@mirohq/design-system-use-local-storage'
import { booleanify } from '@mirohq/design-system-utils'
import type { Booleanish, Nullable } from '@mirohq/design-system-types'
import { useCallback, useEffect, useRef } from 'react'

export interface Logger {
  printLog: (msg: string, ...args: any[]) => void
  enableLogger: () => void
  disableLogger: () => void
}

export const useLogger = (
  key: string,
  enabled: Booleanish = false,
  logPrefix: string = key
): Logger => {
  const [keyEnabled, setKeyEnabled] = useLocalStorage<Nullable<Booleanish>>(
    key,
    enabled
  )

  const keyEnabledRef = useRef<Nullable<Booleanish>>(keyEnabled)

  useEffect(() => {
    keyEnabledRef.current = keyEnabled
  }, [keyEnabled])

  const printLog = useCallback(
    (msg: string, ...args: any[]): void => {
      if (booleanify(keyEnabledRef.current)) {
        console.log(`[ ${logPrefix}] :: ${msg}`, ...args)
      }
    },
    [logPrefix]
  )

  const enableLogger = (): void => setKeyEnabled(true)
  const disableLogger = (): void => setKeyEnabled(false)

  return {
    printLog,
    enableLogger,
    disableLogger,
  }
}
