import {
  createContext,
  useCallback,
  useContext,
  useEffect,
  useState,
} from 'react'
import type { ReactNode } from 'react'

interface SharedProps {
  name?: string
  disabled: boolean
  ariaDisabled: boolean
  min: number
  max: number
  step?: number
  onValueCommit?: (value: number) => void
}

interface SliderContextProps extends SharedProps {
  value: number
  onValueChange: (value: number) => void
}

export interface SliderProviderProps extends SharedProps {
  children?: ReactNode
  value?: number
  onValueChange?: (value: number) => void
}

const SliderContext = createContext<SliderContextProps>({} as any)

export const SliderProvider = ({
  children,
  min,
  value: valueProp,
  onValueChange: onValueChangeProp,
  ...restProps
}: SliderProviderProps): JSX.Element => {
  const [value, setSliderValue] = useState<number>(valueProp ?? min)

  const onValueChange = useCallback(
    (value: number): void => {
      setSliderValue(value)
      if (onValueChangeProp != null) {
        onValueChangeProp(value)
      }
    },
    [onValueChangeProp]
  )

  useEffect(() => setSliderValue(valueProp ?? min), [valueProp, min])

  return (
    <SliderContext.Provider
      value={{
        ...restProps,
        min,
        value,
        onValueChange,
      }}
    >
      {children}
    </SliderContext.Provider>
  )
}

export const useSliderContext = (): SliderContextProps =>
  useContext(SliderContext)
