import React from 'react'
import type { ElementRef } from 'react'
import type { CSSProperties } from '@stitches/react'
import { theme } from '@mirohq/design-system-stitches'

import { StyledSubContent } from './sub-content.styled'
import { CONTENT_OFFSET, CONTENT_PADDING_TOKENS } from '../styles/content'
import { ContentProvider, useContent } from '../hooks/use-content'
import {
  RADIX_CONTENT_AVAILABLE_HEIGHT,
  ScrollableContent,
} from './scrollable-content'
import type { StyledSubContentProps } from './sub-content.styled'
import { StyledItemsContainer } from './content.styled'
import type {
  PointerDownOutsideEvent,
  FocusOutsideEvent,
  StickyBehavior,
  Overflow,
  CollisionPadding,
} from '../types'
import { useDropdownContext } from '../hooks/use-dropdown-context'

export interface SubContentProps extends StyledSubContentProps {
  /**
   * Whether keyboard navigation should loop around
   * @default false
   */
  loop?: boolean

  /**
   * Event handler called when focus moves to the trigger after closing. It can
   * be prevented by calling event.preventDefault. This prop is ignored within
   * submenus.
   */
  onCloseAutoFocus?: (event: Event) => void

  /**
   * Event handler called when the escape key is down. It can be prevented by
   * calling event.preventDefault.
   */
  onEscapeKeyDown?: (event: KeyboardEvent) => void

  /**
   * Event handler called when a pointer event occurs outside the bounds of the
   * component. It can be prevented by calling event.preventDefault.
   */
  onPointerDownOutside?: (event: PointerDownOutsideEvent) => void

  /**
   * Event handler called when focus moves outside the bounds of the component.
   * It can be prevented by calling event.preventDefault.
   */
  onFocusOutside?: (event: FocusOutsideEvent) => void

  /**
   * Event handler called when an interaction (pointer or focus event) happens
   * outside the bounds of the component. It can be prevented by calling
   * event.preventDefault.
   */
  onInteractOutside?: (
    event: PointerDownOutsideEvent | FocusOutsideEvent
  ) => void

  /**
   * Used to force mounting when more control is needed. Useful when controlling
   * animation with React animation libraries.
   */
  forceMount?: true

  /**
   * The distance in pixels from the trigger.
   * @default CONTENT_PADDING_Y + CONTENT_OFFSET
   */
  sideOffset?: number

  /**
   * An offset in pixels from the "start" or "end" alignment options.
   * @default -CONTENT_PADDING_Y
   */
  alignOffset?: number

  /**
   * When true, overrides the side and align preferences to prevent collisions
   * with window edges.
   * @default true
   */
  avoidCollisions?: boolean

  /**
   * The distance in pixels from the boundary edges where collision detection
   * should occur. Accepts a number (same for all sides), or a partial padding
   * object, for example: { top: 20, left: 20 }.
   * @default 0
   */
  collisionPadding?: CollisionPadding

  /**
   * The element used as the collision boundary. By default this is the
   * viewport, though you can provide additional element(s) to be included in
   * this check.
   */
  collisionBoundary?: Element | null

  /**
   * The sticky behavior on the align axis. "partial" will keep the content in
   * the boundary as long as the trigger is at least partially in the boundary
   * whilst "always" will keep the content in the boundary regardless.
   * @default 'partial'
   */
  sticky?: StickyBehavior

  /**
   * Whether to hide the content when the trigger becomes fully occluded.
   * @default true
   */
  hideWhenDetached?: boolean

  /**
   * The max height for the subContent.
   */
  maxHeight?: CSSProperties['maxHeight']

  /**
   * Setting overflow as "visible" means that the content can extend beyond
   * its collision boundary without any clipping or scrolling being
   * applied.
   * When set to "auto," a scrollbar is added if the content exceeds its
   * boundaries. If no maxHeight is defined, it will be automatically adjusted
   * to fit the remaining space between the trigger and the boundary edge.
   * @default 'visible'
   */
  overflow?: Overflow
}

export const SubContent = React.forwardRef<
  ElementRef<typeof StyledSubContent>,
  SubContentProps
>(
  (
    {
      sideOffset,
      alignOffset,
      collisionPadding = 0,
      loop = false,
      hideWhenDetached = true,
      sticky = 'partial',
      overflow = 'visible',
      maxHeight,
      collisionBoundary,
      children,
      ...restProps
    },
    forwardRef
  ) => {
    /* A new ContentProvider instance is created per Content and SubContent,
    using the context from the parent is necessary to preserve this value. */
    const { containerSpacing } = useContent()
    const { direction } = useDropdownContext()

    // We need to calculate offsets based on content padding,
    // otherwise submenu would overlap the menu and won't be aligned with the menu properly
    const paddingToken = CONTENT_PADDING_TOKENS[containerSpacing]
    const contentSideOffset =
      CONTENT_OFFSET + parseInt(theme.space[paddingToken.x])
    const contentAlignOffset = -parseInt(theme.space[paddingToken.y])

    /*
      When SubContent renders inside the Content and they both have collisionBoundary,
      Radix content available height is not calculated correctly and uses Content's available height.
      If we get collisionBoundary's height, it works just fine
    */
    const availableHeight =
      collisionBoundary != null
        ? getComputedStyle(collisionBoundary).getPropertyValue('height')
        : RADIX_CONTENT_AVAILABLE_HEIGHT

    return (
      <ContentProvider containerSpacing={containerSpacing}>
        <StyledSubContent
          {...restProps}
          ref={forwardRef}
          sideOffset={sideOffset ?? contentSideOffset}
          alignOffset={alignOffset ?? contentAlignOffset}
          collisionPadding={collisionPadding}
          loop={loop}
          hideWhenDetached={hideWhenDetached}
          sticky={sticky}
          collisionBoundary={collisionBoundary}
        >
          <ScrollableContent
            maxHeight={
              maxHeight !== undefined
                ? `min(${availableHeight}, ${
                    typeof maxHeight === 'number' ? `${maxHeight}px` : maxHeight
                  })`
                : availableHeight
            }
            overflow={overflow}
            direction={direction}
          >
            <StyledItemsContainer containerSpacing={containerSpacing}>
              {children}
            </StyledItemsContainer>
          </ScrollableContent>
        </StyledSubContent>
      </ContentProvider>
    )
  }
)
