import React from 'react'
import type { ElementRef } from 'react'
import { useId } from '@mirohq/design-system-use-id'

import { useComboboxContext } from '../hooks/use-combobox-context'
import type { StyledGroupProps } from './group.styled'
import { StyledGroup } from './group.styled'
import { useInvisibleContent } from '../hooks/use-invisible-content'
import { GroupProvider } from '../hooks/use-group-context'

export type GroupProps = StyledGroupProps

export const Group = React.forwardRef<
  ElementRef<typeof StyledGroup>,
  GroupProps
>(({ children, ...rest }, forwardRef) => {
  const { autoFilter, searchValue, filteredItems } = useComboboxContext()

  const id = useId()
  const getInvisibleContent = useInvisibleContent()

  let hasVisibleContent = true

  if (autoFilter && searchValue.length > 0) {
    hasVisibleContent = filteredItems.some(item => item.groupId === id)
  }

  return (
    <GroupProvider groupId={id}>
      {hasVisibleContent ? (
        <StyledGroup {...rest} ref={forwardRef}>
          {children}
        </StyledGroup>
      ) : (
        getInvisibleContent(children)
      )}
    </GroupProvider>
  )
})
