import React from 'react'
import { useDateFormatter } from 'react-aria'
import type { CalendarState, RangeCalendarState } from 'react-stately'
import { IconChevronLeft, IconChevronRight } from '@mirohq/design-system-icons'

import {
  StyledHeaderPlaceholder,
  StyledHeader,
  StyledHeaderTitle,
} from './header.styled'
import type { HeaderButtonProps } from './header-button'
import { HeaderButton } from './header-button'

interface IHeader {
  state: RangeCalendarState | CalendarState
  prevButtonProps: HeaderButtonProps
  nextButtonProps: HeaderButtonProps
  visibleMonths?: number
}

export const Header = ({
  state,
  prevButtonProps,
  nextButtonProps,
  visibleMonths,
}: IHeader): React.ReactNode => {
  const monthDateFormatter = useDateFormatter({
    month: 'long',
    year: 'numeric',
    timeZone: state.timeZone,
  })

  return (
    <StyledHeader>
      <HeaderButton {...prevButtonProps}>
        <IconChevronLeft />
      </HeaderButton>
      <StyledHeaderTitle level={2} styledAs='h3'>
        {monthDateFormatter.format(
          state.visibleRange.start.toDate(state.timeZone)
        )}
      </StyledHeaderTitle>
      {visibleMonths === 2 && (
        <>
          <StyledHeaderPlaceholder />
          <StyledHeaderTitle level={2} styledAs='h3'>
            {monthDateFormatter.format(
              state.visibleRange.start.add({ months: 1 }).toDate(state.timeZone)
            )}
          </StyledHeaderTitle>
        </>
      )}
      <HeaderButton {...nextButtonProps}>
        <IconChevronRight />
      </HeaderButton>
    </StyledHeader>
  )
}
