import { useState, useCallback, useEffect, useRef } from 'react'

export const useClipboard = <T>(
  options: { timeout?: number } = { timeout: 2000 }
): {
  value: T | undefined
  copy: (value: T) => Promise<void>
  copied: boolean
  clearTimeout: null | (() => void)
} => {
  const [copied, setCopied] = useState(false)
  const [valueState, setValueState] = useState<T>()

  const copy = useCallback(
    async (value: T) => {
      try {
        if (typeof value === 'string' || typeof value === 'number') {
          await navigator.clipboard.writeText(`${value}`)
        } else if (value instanceof Blob) {
          const blob = value
          const data = [new ClipboardItem({ [blob.type]: blob })]
          await navigator.clipboard.write(data)
        } else {
          throw Error(
            'The clipboard can only handle data of type string, number, or blob.'
          )
        }

        setCopied(true)
        setValueState(value)
      } catch (e) {
        console.error(`Failed to copy.\n${e}`)
      }
    },
    [setValueState, setCopied]
  )

  const clearTimeout = useRef<null | (() => void)>(null)

  useEffect(() => {
    let timeoutId: number | null = null

    const clear = (): void => {
      if (timeoutId !== null) {
        setCopied(false)
        window.clearTimeout(timeoutId)
      }
    }

    if (
      copied &&
      typeof options.timeout === 'number' &&
      isFinite(options.timeout)
    ) {
      timeoutId = window.setTimeout(() => {
        setCopied(false)
      }, options.timeout)

      clearTimeout.current = clear
    } else {
      clearTimeout.current = () => setCopied(false)
    }

    return clear
  }, [options.timeout, copied])

  return {
    value: valueState,
    copy,
    copied,
    clearTimeout: clearTimeout.current,
  }
}
