import type { ElementRef, ForwardRefExoticComponent, ReactNode } from 'react'
import React from 'react'

import type { StyledTagProps } from './tag.styled'
import { StyledInner, StyledTag } from './tag.styled'
import { IconSlot } from './partials/icon-slot'

export type TagProps = Omit<StyledTagProps, 'shade' | 'border'> & {
  /**
   *
   * Change the tag style.
   * @default 'primary'
   */
  variant?: StyledTagProps['variant']

  /**
   * Change the tag shade.
   * @default 'mild'
   */
  DEPRECATED_shade?: StyledTagProps['shade']

  /**
   * Change the tag border.
   * @default false
   */
  DEPRECATED_border?: StyledTagProps['border']

  /**
   * Change the tag text transform.
   * @default true
   */
  textTransform?: StyledTagProps['textTransform']

  /**
   * The text content.
   */
  children: ReactNode
}

export const Tag = React.forwardRef<ElementRef<typeof StyledTag>, TagProps>(
  (
    {
      variant = 'primary',
      children,
      DEPRECATED_shade = 'mild',
      DEPRECATED_border = false,
      ...restProps
    },
    forwardRef
  ) => (
    <StyledTag
      {...restProps}
      variant={variant}
      border={DEPRECATED_border}
      shade={DEPRECATED_shade}
      ref={forwardRef}
    >
      <StyledInner>{children}</StyledInner>
    </StyledTag>
  )
) as ForwardRefExoticComponent<TagProps> & Partials

export interface Partials {
  IconSlot: typeof IconSlot
}

Tag.IconSlot = IconSlot
