import React, { useEffect, useRef } from 'react'
import type { ElementRef } from 'react'
import { booleanishAttrValue } from '@mirohq/design-system-utils'
import {
  useFormFieldContext,
  FormFieldProvider,
  FORM_FIELD_STATUS,
} from '@mirohq/design-system-base-form'
import type { FormFieldProviderProps } from '@mirohq/design-system-base-form'

import type { StyledFieldProps } from './field.styled'
import { StyledField } from './field.styled'

export interface FieldProps extends StyledFieldProps, FormFieldProviderProps {}

const Root = React.forwardRef<ElementRef<typeof StyledField>, FieldProps>(
  ({ onFocus, onBlur, status: statusProps, ...restProps }, forwardRef) => {
    const {
      id,
      status: formFieldStatus,
      setStatus,
      required,
      focused,
      setFocused,
      disabled,
      ariaDisabled,
      readOnly,
      formElementRef,
    } = useFormFieldContext()

    const status = statusProps ?? formFieldStatus
    const initialValueRef = useRef<string>()

    if (
      formElementRef.current?.value !== undefined &&
      initialValueRef.current === undefined
    ) {
      initialValueRef.current = formElementRef.current.value
    }

    useEffect(() => {
      if (statusProps !== undefined) {
        setStatus(statusProps)
      }
    }, [statusProps, setStatus])

    return (
      <StyledField
        data-status={status}
        data-required={booleanishAttrValue(required)}
        data-focused={booleanishAttrValue(focused)}
        data-disabled={booleanishAttrValue(
          disabled === true || ariaDisabled === true
        )}
        data-readonly={booleanishAttrValue(readOnly)}
        onFocus={e => {
          setFocused(true)
          onFocus?.(e)
        }}
        onBlur={e => {
          if (statusProps === undefined) {
            setStatus(
              initialValueRef.current !== formElementRef.current?.value
                ? FORM_FIELD_STATUS.dirty
                : FORM_FIELD_STATUS.touched
            )
          } else {
            setStatus(statusProps)
          }

          setFocused(false)
          onBlur?.(e)
        }}
        {...restProps}
        id={id}
        ref={forwardRef}
      />
    )
  }
)

export const Field = React.forwardRef<
  ElementRef<typeof StyledField>,
  FieldProps
>(({ id, status, ...restProps }, forwardRef) => (
  <FormFieldProvider status={status} id={id}>
    {/* explicit use status as props because the provider will add a default */}
    <Root status={status} {...restProps} ref={forwardRef} />
  </FormFieldProvider>
))
