import React, { createContext, useContext } from 'react'

export interface ProviderProps {
  /**
   * The duration from when the mouse enters a tooltip trigger until
   * the tooltip opens.
   */
  delayDuration?: number

  /**
   * How much time a user has to enter another trigger without incurring
   * a delay again.
   * When set to `0`, when hovering between triggers there wont be delay duration.
   * @default 500
   */
  skipDelayDuration?: number

  /**
   * The content
   */
  children?: React.ReactNode
}

export const ProviderContext = createContext<ProviderProps>({} as any)

export const Provider = ({
  children,
  ...restProps
}: ProviderProps): React.ReactNode => (
  <ProviderContext.Provider value={restProps}>
    {children}
  </ProviderContext.Provider>
)

Provider.displayName = 'Tooltip.Provider'

export const useTooltipContext = (): ProviderProps =>
  useContext(ProviderContext)
