import type { ComponentPropsWithRef } from 'react'
import { styled } from '@mirohq/design-system-stitches'
import { Primitive } from '@mirohq/design-system-primitive'
import { textFieldStyles } from '@mirohq/design-system-base-text-field'

export const StyledFloatingLabel = styled(Primitive.label, {
  transitionProperty: 'transform, margin, top, left, font-size',
  transitionDuration: '200ms',
  transitionTimingFunction: 'cubic-bezier(0, 0, 0.2, 1)',
  position: 'absolute',
  top: 0,
  left: 0,
  zIndex: 1,
  pointerEvents: 'none',
  whiteSpace: 'nowrap',
  fontStyle: 'normal',
  lineHeight: 1.5,
  height: '0.75em',
  ...textFieldStyles.base.placeholder,

  variants: {
    floating: {
      true: {
        top: '0',
        transform: 'translateY(-50%)',
        left: '$150',
        fontSize: '$150',
        padding: '0 2px',
        height: 'auto',
        lineHeight: 1,
        backgroundColor: '$background-neutrals-container',
      },
      false: {
        fontSize: '$200',
        marginTop: '-1px', // adjust for the input's border
      },
    },
    visuallyHidden: {
      true: {
        // todo MDS-1003: replace with shared styles
        border: '0',
        clip: 'rect(0 0 0 0)',
        height: '1px',
        margin: '-1px',
        overflow: 'hidden',
        padding: '0',
        position: 'absolute',
        width: '1px',
      },
    },
    size: {
      medium: {},
      large: {},
      'x-large': {},
    },
  },
  compoundVariants: [
    {
      size: 'medium',
      floating: false,
      css: {
        top: '$50',
        left: '$100',
      },
    },
    {
      size: 'large',
      floating: false,
      css: {
        top: '$100',
        left: '$150',
      },
    },
    {
      size: 'x-large',
      floating: false,
      css: {
        top: '$150',
        // $space$250 does not exist by design but we will force it here
        left: 'calc($200 + $space$50)',
      },
    },
  ],
  defaultVariants: {
    size: 'large',
  },
})

export type StyledFloatingLabelProps = ComponentPropsWithRef<
  typeof StyledFloatingLabel
>
