import React from 'react'
import type { ElementRef } from 'react'
import type { HotkeySharedProps as BaseHotkeyProps } from '@mirohq/design-system-base-hotkey'
import { Hotkey as BaseHotkey } from '@mirohq/design-system-base-hotkey'

import { RightSlot } from './right-slot'

export type HotkeyProps = BaseHotkeyProps & {
  /**
   * The content.
   */
  children: string
}

export const Hotkey = React.forwardRef<
  ElementRef<typeof BaseHotkey>,
  HotkeyProps
>(({ children, ...restProps }, forwardRef) => (
  <RightSlot>
    <BaseHotkey ref={forwardRef} {...restProps} variant='neutral'>
      {children}
    </BaseHotkey>
  </RightSlot>
))
