import type { ElementRef } from 'react'
import React from 'react'
import { IconCalendarBlank } from '@mirohq/design-system-icons'
import { useFormFieldContext } from '@mirohq/design-system-base-form'
import { Trigger as RadixTrigger } from '@radix-ui/react-popover'
import { booleanify, stringAttrValue } from '@mirohq/design-system-utils'

import type { StyledTriggerProps } from './trigger.styled'
import {
  StyledIconContainer,
  StyledCustomTrigger,
  StyledPlaceholder,
  StyledTrigger,
  StyledValue,
} from './trigger.styled'
import { ClearAction } from './clear-action'
import { useCalendarContext } from '../use-calendar-context'

type TriggerSharedProps = Omit<StyledTriggerProps, 'withClearButton'> & {
  /**
   * The content that will be rendered inside the Calendar trigger when there is no value
   */
  placeholder?: string

  /**
   * Show a button to clear the Calendar value
   * @default false
   */
  clearable?: boolean

  /**
   * The content
   */
  children: React.ReactNode
}

export type TriggerProps = TriggerSharedProps &
  (
    | { clearable: false; clearLabel?: never; onClear?: never }
    | {
        /**
         * Event handler called when the calendar is cleared
         */
        onClear?: () => void

        /**
         * The label text for Trigger's action button when Combobox has values selected. Will be rendered in a Tooltip.
         */
        clearLabel: string
      }
  )

export const Trigger = React.forwardRef<ElementRef<'button'>, TriggerProps>(
  (
    {
      id,
      'aria-describedby': ariaDescribedBy,
      placeholder,
      asChild = false,
      clearable = true,
      clearLabel,
      onClear,
      children,
      ...restProps
    },
    forwardRef
  ) => {
    const { value, setValue, disabled, ariaDisabled } = useCalendarContext()

    const { formElementId, ariaDescribedBy: formFieldContextDescribedBy } =
      useFormFieldContext()

    if (asChild) {
      return (
        <RadixTrigger ref={forwardRef} asChild>
          <StyledCustomTrigger asChild {...restProps}>
            {children}
          </StyledCustomTrigger>
        </RadixTrigger>
      )
    }

    const onClearHandler = (): void => {
      setValue(undefined)
      if (onClear !== undefined) {
        onClear()
      }
    }

    const clearButtonVisible =
      clearable && value != null && clearLabel !== undefined

    return (
      <>
        <RadixTrigger ref={forwardRef} asChild>
          <StyledTrigger
            id={id ?? formElementId}
            aria-describedby={stringAttrValue(
              ariaDescribedBy,
              formFieldContextDescribedBy
            )}
            withClearButton={clearButtonVisible}
            disabled={disabled}
            aria-disabled={ariaDisabled}
            {...restProps}
          >
            {value != null ? (
              <StyledValue>{children}</StyledValue>
            ) : (
              <StyledPlaceholder>{placeholder}</StyledPlaceholder>
            )}

            {!clearButtonVisible && (
              <StyledIconContainer>
                <IconCalendarBlank size='small' />
              </StyledIconContainer>
            )}
          </StyledTrigger>
        </RadixTrigger>
        {clearButtonVisible && (
          <ClearAction
            label={clearLabel}
            onPress={onClearHandler}
            disabled={disabled ?? booleanify(ariaDisabled)}
          />
        )}
      </>
    )
  }
)
