import type { ElementRef, ReactElement, ReactNode } from 'react'
import React, { useRef } from 'react'
import { useCalendarState, useRangeCalendarState } from 'react-stately'
import { useCalendar, useRangeCalendar, useLocale } from 'react-aria'
import { createCalendar } from '@internationalized/date'
import { Flex } from '@mirohq/design-system-flex'
import type { DateRange, DateValue, AriaButtonProps } from 'react-aria'
import type { DOMAttributes } from '@react-types/shared'
import type { CalendarState, RangeCalendarState } from 'react-stately'
import { theme } from '@mirohq/design-system-stitches'

import { useCalendarContext } from '../use-calendar-context'
import type { StyledContentProps } from './content.styled'
import {
  StyledContent,
  StyledClearContent,
  StyledCalendarBody,
  StyledBodyContentLeft,
  StyledBodyContentRight,
  StyledGridContent,
} from './content.styled'
import { Header } from './header'
import { Grid } from './grid'
import type { Align, PointerDownOutsideEvent, Side, Sticky } from '../types'
import type { HeaderButtonProps } from './header-button'
import { PredefinedRanges } from './predefined-ranges'

export const CONTENT_OFFSET = parseInt(theme.space[50])

export type ContentProps = StyledContentProps & {
  /**
   * Calendar's content.
   */
  children?: ReactNode

  /**
   * Event handler called when focus moves to the trigger after closing. It can
   * be prevented by calling event.preventDefault.
   */
  onCloseAutoFocus?: (event: Event) => void

  /**
   * Event handler called when focus moves to the trigger after closing. It can
   * be prevented by calling event.preventDefault.
   */
  onEscapeKeyDown?: (event: KeyboardEvent) => void

  /**
   * Event handler called when a pointer event occurs outside the bounds of the
   * component. It can be prevented by calling event.preventDefault.
   */
  onPointerDownOutside?: (event: PointerDownOutsideEvent) => void

  /**
   * The preferred side of the anchor to render against when open. Will be
   * reversed when collisions occur and avoidCollisions is enabled.
   * @default 'bottom'
   */
  side?: Side

  /**
   * The distance in pixels from the anchor.
   * @default CONTENT_OFFSET
   */
  sideOffset?: number

  /**
   * The preferred alignment against the anchor. May change when collisions
   * occur.
   * @default 'start'
   */
  align?: Align

  /**
   * An offset in pixels from the "start" or "end" alignment options.
   * @default 0
   */
  alignOffset?: number

  /**
   * When true, overrides the side and align preferences to prevent collisions
   * with boundary edges.
   * @default true
   */
  avoidCollisions?: boolean

  /**
   * The element used as the collision boundary. By default this is the
   * viewport, though you can provide additional element(s) to be included in
   * this check.
   */
  collisionBoundary?: Element | null

  /**
   * The distance in pixels from the boundary edges where collision detection
   * should occur. Accepts a number (same for all sides), or a partial padding
   * object, for example: { top: 20, left: 20 }.
   * @default 0
   */
  collisionPadding?: number | Partial<Record<Side, number>>

  /**
   * The sticky behavior on the align axis. "partial" will keep the content in the
   * boundary as long as the trigger is at least partially in the boundary whilst
   * "always" will keep the content in the boundary regardless.
   * @default 'partial'
   */
  sticky?: Sticky

  /**
   * Whether to hide the content when the trigger becomes fully occluded.
   * @default true
   */
  hideWhenDetached?: boolean
}

interface SharedContentProps {
  calendarProps: DOMAttributes
  prevButtonProps: AriaButtonProps
  nextButtonProps: AriaButtonProps
  state: RangeCalendarState | CalendarState
}

const SharedContent = React.forwardRef<HTMLDivElement, SharedContentProps>(
  ({ calendarProps, prevButtonProps, nextButtonProps, state }, forwardRef) => {
    const { visibleDuration } = useCalendarContext<DateValue>()

    return (
      <StyledCalendarBody {...calendarProps} ref={forwardRef}>
        <PredefinedRanges />
        <Flex direction='column'>
          <Header
            state={state}
            prevButtonProps={prevButtonProps as HeaderButtonProps}
            nextButtonProps={nextButtonProps as HeaderButtonProps}
            visibleMonths={visibleDuration.months}
          />
          <StyledGridContent>
            <StyledBodyContentLeft>
              <Grid state={state} />
            </StyledBodyContentLeft>
            {visibleDuration.months === 2 && (
              <StyledBodyContentRight>
                <Grid state={state} offset={{ months: 1 }} />
              </StyledBodyContentRight>
            )}
          </StyledGridContent>
        </Flex>
      </StyledCalendarBody>
    )
  }
)

const DatePickerContent = (): ReactElement => {
  const { locale } = useLocale()

  const {
    calendarValue,
    visibleDuration,
    minDate,
    maxDate,
    onCalendarDateClick,
  } = useCalendarContext<DateValue>()

  const state = useCalendarState({
    value: calendarValue,
    visibleDuration,
    minValue: minDate,
    maxValue: maxDate,
    autoFocus: true,
    onChange: onCalendarDateClick,
    locale,
    createCalendar,
  })

  const { calendarProps, prevButtonProps, nextButtonProps } = useCalendar(
    {},
    state
  )

  return (
    <SharedContent
      calendarProps={calendarProps}
      prevButtonProps={prevButtonProps}
      nextButtonProps={nextButtonProps}
      state={state}
    />
  )
}

const RangePickerContent = (): ReactElement => {
  const { locale } = useLocale()

  const {
    calendarValue,
    visibleDuration,
    minDate,
    maxDate,
    onCalendarDateClick,
  } = useCalendarContext<DateRange>()

  const ref = useRef<HTMLDivElement>(null)

  const state = useRangeCalendarState({
    value: calendarValue,
    visibleDuration,
    minValue: minDate,
    maxValue: maxDate,
    autoFocus: true,
    onChange: onCalendarDateClick,
    locale,
    createCalendar,
  })

  const { calendarProps, prevButtonProps, nextButtonProps } = useRangeCalendar(
    {},
    state,
    ref
  )

  return (
    <SharedContent
      ref={ref}
      calendarProps={calendarProps}
      prevButtonProps={prevButtonProps}
      nextButtonProps={nextButtonProps}
      state={state}
    />
  )
}

export const Content = React.forwardRef<
  ElementRef<typeof StyledContent>,
  ContentProps
>(
  (
    {
      side = 'bottom',
      sideOffset = CONTENT_OFFSET,
      align = 'start',
      alignOffset = 0,
      collisionPadding = 0,
      avoidCollisions = true,
      sticky = 'partial',
      hideWhenDetached = true,
      children,
      ...restProps
    },
    forwardRef
  ) => {
    const { picker, open } = useCalendarContext()

    if (!open) {
      return null
    }

    return (
      <StyledContent
        {...restProps}
        ref={forwardRef}
        side={side}
        sideOffset={sideOffset}
        align={align}
        alignOffset={alignOffset}
        collisionPadding={collisionPadding}
        avoidCollisions={avoidCollisions}
        sticky={sticky}
        hideWhenDetached={hideWhenDetached}
      >
        <Flex direction='column'>
          {picker === 'single' ? <DatePickerContent /> : <RangePickerContent />}
          {children != null && (
            <StyledClearContent>{children}</StyledClearContent>
          )}
        </Flex>
      </StyledContent>
    )
  }
)
