import React, { useCallback } from 'react'
import type { FC } from 'react'
import { Chip } from '@mirohq/design-system-chip'
import { booleanify } from '@mirohq/design-system-utils'

import { useComboboxContext } from '../hooks/use-combobox-context'

export interface ValueProps {
  /**
   * The label to make the remove button recognizable by the screen readers.
   */
  unselectAriaLabel: string

  /**
   * Allow single or multiple selection.
   * @default true
   */
  multiple?: boolean
}

export const Value: FC<ValueProps> = ({
  unselectAriaLabel,
  multiple = true,
}) => {
  const {
    value = [],
    setValue,
    disabled,
    readOnly,
    'aria-disabled': ariaDisabled,
    itemsMap,
  } = useComboboxContext()
  const canRemoveItem =
    !booleanify(ariaDisabled) && !booleanify(disabled) && !booleanify(readOnly)

  const onItemRemove = useCallback(
    (item: string): void => {
      if (multiple) {
        setValue(prevValue =>
          (prevValue as string[]).filter(value => value !== item)
        )
      } else {
        setValue('')
      }
    },
    [setValue, multiple]
  )

  const getItemText = useCallback(
    (itemValue: string) => {
      const itemData = itemsMap.get(itemValue)
      if (itemData === undefined || itemData.displayedText === '') {
        return null
      }

      return (
        <Chip
          key={itemValue}
          onRemove={e => {
            onItemRemove(itemValue)
            e.stopPropagation()
          }}
          removable={canRemoveItem}
          removeAriaLabel={`${unselectAriaLabel} ${itemData.displayedText}`}
          data-testid={
            process.env.NODE_ENV === 'test'
              ? `combobox-value-${itemValue}`
              : undefined
          }
        >
          {itemData.displayedText}
        </Chip>
      )
    },
    [canRemoveItem, itemsMap, onItemRemove, unselectAriaLabel]
  )

  return (
    <>
      {multiple && Array.isArray(value)
        ? value.map(getItemText)
        : getItemText(value as string)}
    </>
  )
}
