import React from 'react'
import type { ElementRef } from 'react'
import { useAriaDisabled } from '@mirohq/design-system-use-aria-disabled'

import type { StyledThumbProps } from './thumb.styled'
import { StyledThumb, StyledKnob } from './thumb.styled'
import { useSliderContext } from '../hooks/use-slider-context'

export interface ThumbProps extends StyledThumbProps {}

export const Thumb = React.forwardRef<
  ElementRef<typeof StyledThumb>,
  ThumbProps
>((props, forwardRef) => {
  const { disabled, ariaDisabled } = useSliderContext()

  const elementProps = useAriaDisabled({
    ...props,
    ariaDisabled,
  })

  return (
    <StyledThumb {...elementProps} disabled={disabled} ref={forwardRef}>
      <StyledKnob />
    </StyledThumb>
  )
})
